<template>
  <v-card>
    <v-card-title>
      <span class="headline"></span>
    </v-card-title>
    <v-card-text>
      <v-form ref="formSubmit" v-model="valid" lazy-validation>
        <v-container>
          <v-row>
            <v-col>
              <small>
                <span class="black--text">Processing Information: </span></small
              >
              <br />
              <!-- ###### For Action #########-->
              <v-select
                v-if="childData.user_role != 2"
                :items="actionChoice"
                label="Action"
                item-text="name"
                return-object
                v-model="selectAction"
                @change="getItemText"
                dense
                outlined
                :rules="requiredField"
                required
              >
              </v-select>

              <!-- ###### Select For Internal Action #########-->
              <v-select
                v-if="
                  childData.user_role != 2 &&
                  childData.user_role != 5 &&
                  selectAction.value == 0
                "
                :items="actionChoice2"
                label="Internal Action"
                item-text="name"
                return-object
                v-model="selectInternalAction"
                dense
                outlined
                :rules="requiredField"
                required
              >
              </v-select>

              <!-- ###### Select For Personnel #########-->
              <v-select
                v-if="childData.user_role != 5"
                item-text="text"
                label="Personnel"
                return-object
                :items="personnelItem"
                v-model="cmbPersonnel"
                dense
                outlined
                :rules="requiredField"
                required
                :readonly="isEditable"
                :filled="isEditable"
              ></v-select>

              <!-- !!!!! THIS DIV is For NSWMC Account Only !!!!-->
              <div v-if="childData.user_role == 5">
                <!-- ###### Select For Personnel #########-->
                <v-select
                  v-if="selectAction.value != 0"
                  item-text="text"
                  label="Personnel"
                  return-object
                  :items="personnelItem"
                  v-model="cmbPersonnel"
                  dense
                  outlined
                  :rules="requiredField"
                  required
                  :readonly="isEditable"
                  :filled="isEditable"
                ></v-select>

                <!-- ###### Select For Group #########-->
                <v-select
                  v-if="selectAction.value == 0"
                  item-text="text"
                  clearable
                  label="Group"
                  return-object
                  :items="allGroup"
                  v-model="assigned_group"
                  :rules="requiredField"
                  dense
                  outlined
                  required
                ></v-select>
              </div>

              <v-textarea
                outlined
                auto-grow
                dense
                label="Remarks"
                v-model="remarks"
                :rules="requiredField"
                required
              >
              </v-textarea>
            </v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <small>
                <span class="black--text">Attachments: </span>
                <span class="blue--text">
                  (max. size of 70 MB per attachment)</span
                ><span class="red--text"> -required</span>
                <br />
                <span class="blue--text">
                  * please upload attachment(s) before saving / processing
                  *</span
                ></small
              >
              <v-row>
                <v-col md="12">
                  <v-btn
                    color="info"
                    class="mr-2"
                    small
                    outlined
                    @click="$refs.refInputEl.click()"
                  >
                    <v-icon left> mdi-plus </v-icon>
                    <span class="d-none d-sm-block">Add Files</span>
                  </v-btn>

                  <input
                    ref="refInputEl"
                    type="file"
                    :hidden="true"
                    multiple
                    @change="onFileChange"
                  />
                  <!-- <v-btn outlined color="info" small>
                            <v-icon left> mdi-cloud-upload </v-icon>
                            Upload All
                          </v-btn> -->
                </v-col>
              </v-row>
              <v-row>
                <v-col md="12" class="pt-0">
                  <div>
                    <v-list dense>
                      <!-- <v-list-item-group color="primary"> -->
                      <v-list-item v-for="(item, i) in uploads" :key="i">
                        <!-- <v-list-item-icon>
                                  <v-icon v-text="item.icon"></v-icon>
                                </v-list-item-icon> -->
                        <v-list-item-content>
                          <v-list-item-title
                            v-text="item.name"
                          ></v-list-item-title>
                        </v-list-item-content>
                        <v-list-item-action dense class="ma-0 d-flex flex-row">
                          <v-btn @click="uploadSpecificFile(item, i)" icon>
                            <v-icon color="blue lighten-1">mdi-upload</v-icon>
                          </v-btn>
                          <v-btn @click="removeFile(i)" icon>
                            <v-icon color="red lighten-1"
                              >mdi-close-circle</v-icon
                            >
                          </v-btn>
                        </v-list-item-action>
                      </v-list-item>
                      <!-- </v-list-item-group> -->
                    </v-list>
                  </div>
                </v-col>
              </v-row>
              <v-row>
                <v-col md="12" class="pt-0">
                  <small>
                    <span class="black--text">Current Attachments: </span>
                  </small>
                  <div>
                    <v-list dense>
                      <!-- <v-list-item-group color="primary"> -->
                      <v-list-item
                        v-for="(item, i) in convertFile(allTypAttach)"
                        :key="i"
                      >
                        <!-- <v-list-item-icon>
                                  <v-icon v-text="item.icon"></v-icon>
                                </v-list-item-icon> -->
                        <v-list-item-content>
                          <v-list-item-title
                            v-text="item.file_path_name[0].orig_name"
                          ></v-list-item-title>
                        </v-list-item-content>
                        <v-list-item-action dense class="ma-0 d-flex flex-row">
                          <v-btn @click="deleteFile(item)" icon>
                            <v-icon color="red lighten-1"
                              >mdi-delete-circle</v-icon
                            >
                          </v-btn>
                        </v-list-item-action>
                      </v-list-item>
                      <!-- </v-list-item-group> -->
                    </v-list>
                  </div>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-container>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="blue darken-1" text @click="handleCancel">Close</v-btn>
      <v-btn color="blue darken-1" text @click="handleSubmit">Submit</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
let basePath = window.location.origin;
export default {
  props: ["childData"],

  data: () => ({
    name: "ProcessModal",
    textFile: ["pdf", "docx", "pptx"],
    imageFile: ["jpg", "png"],
    dialogProcess: false,
    selectAction: "",
    selectInternalAction: "",
    personnelItem: [],
    cmbPersonnel: "",
    assigned_group: "",
    remarks: "",
    rowStatus: 0,
    uploads: [],
    valid: true,
    isEditable: true,
    requiredField: [(v) => !!v || "Field is required"],

    actionChoice: [
      { value: 1, name: "Submit" },
      { value: 0, name: "Forward" },
      { value: 2, name: "Return" },
    ],
    actionChoice2: [
      { value: 0, name: "For Evaluation" },
      { value: 1, name: "For Review" },
      { value: 2, name: "For Comment" },
    ],
  }),
  created() {
    console.log(this.childData);

    // # For LGU Account use
    if (this.childData["user_role"] == 2) {
      if (this.childData["central_to_lgu"] == 1) {
        this.fetchSpecificPersonel({
          user_id: this.childData["senderId"],
        }).then(() => {
          this.personnelItem = this.allPersonnel;
          this.cmbPersonnel = this.allPersonnel[0];
        });
      } else {
        this.getPersonnel(1);
      }
    }

    // # For Central Office Account use
    if (this.childData["user_role"] == 4) {
      this.actionChoice.push({ value: 3, name: "Return To LGU" });
    }

    if (this.childData["user_role"] == 5) {
      this.actionChoice = this.actionChoice.map((item) =>
        item.name === "Forward" ? { value: item.value, name: "Assign" } : item
      );
    }

    this.getTypAttach(
      this.childData["headerId"],
      this.childData["route_order"]
    );
  },
  methods: {
    ...mapActions([
      "fetchTypAttach",
      "fetchSpecificPersonel",
      "fetchPersonnel",
      "fetchGroup",
      "uploadTypPerLine",
      "deleteFileVuex",
    ]),

    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return parts.join(".");
    },

    clearinput() {
      this.cmbPersonnel = "";
      this.remarks = "";
      this.uploads = [];
      this.$refs.formSubmit.resetValidation();
    },

    handleSubmit() {
      if (this.childData.user_role != 2) {
        var return_data = {
          remarks: this.remarks,
          personnel: this.cmbPersonnel,
          assigned_group: this.assigned_group,
          action1: this.selectAction,
          action2: this.selectInternalAction,
        };
      } else {
        var return_data = {
          remarks: this.remarks,
          personnel: this.cmbPersonnel,
        };
      }
      if (this.$refs.formSubmit.validate()) {
        this.$emit("submit-data", return_data);
      }
    },

    handleCancel() {
      this.clearinput();
      this.$emit("close-modal");
    },

    getPersonnel(action) {
      this.fetchPersonnel({
        reg_id: this.childData["regionId"],
        user_role: parseInt(this.childData["user_role"]),
        action: action,
        user_id: this.childData["user_id"],
      }).then(() => {
        this.personnelItem = this.allPersonnel;
        this.cmbPersonnel = this.allPersonnel[0];
      });
    },

    getItemText() {
      this.cmbPersonnel = "";
      this.assigned_group = "";
      let action = this.selectAction.value;
      // Action ; 1 = Submit , 0 = Forward , 2 = Return , 3 = Return to LGU
      if (action != 2 && action != 3) {
        //#If Action is not Return And Return to LGU
        if (this.childData["user_role"] == 5 && action == 0) {
          // #If NSWMC Account
          this.fetchGroup({
            reg_id: this.childData["region_num"],
          });
        } else {
          // #If Normal Account
          this.isEditable = false;
          this.getPersonnel(action);
        }
      } else {
        // #For Normal Return
        let user_id = this.childData["senderId"];

        // #For Return to LGU
        if (action == 3) {
          user_id = this.childData["creatorId"];
        }

        // #For Region Office Normal Return
        if (this.childData["user_role"] == 3) {
          user_id = this.childData["creatorId"];
        }

        if (this.childData["user_role"] == 4 && action == 2) {
          let personelreg = 0;
          if (action == 2) {
            personelreg = this.childData["regionId"];
          } else {
            personelreg = 18;
          }
          // IF Central Office Return
          this.fetchPersonnel({
            reg_id: personelreg,
            user_role: parseInt(this.childData["user_role"]),
            action: action,
            user_id: this.childData["user_id"],
          }).then(() => {
            this.personnelItem = this.allPersonnel;
            this.cmbPersonnel = this.allPersonnel[0];
          });
        } else {
          // IF Normal Return
          this.fetchSpecificPersonel({
            user_id: user_id,
          }).then(() => {
            this.personnelItem = this.allPersonnel;
            this.cmbPersonnel = this.allPersonnel[0];
          });
        }
      }
    },
    //File Upload
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      this.uploads = Object.entries(files).map((e, i) => e[1]);
    },

    removeFile(i) {
      this.uploads.splice(i, 1); // 2nd parameter means remove one item only
    },

    getTypAttach(id, route_order) {
      this.fetchTypAttach({
        header_id: id,
        route: route_order,
        user: this.childData["user_id"],
      }).then(() => {
        //this.setAttachment();
      });
    },

    uploadSpecificFile(item, i) {
      let error = 0;
      let allowed_ext = [
        "image/png",
        "image/jpeg",
        "image/svg",
        "application/pdf",
      ];
      if (!allowed_ext.includes(item["type"])) {
        error++;
      }
      var formData = new FormData();
      formData.append("files[]", item);
      formData.append("header_id", this.childData["headerId"]);
      formData.append("route", parseInt(this.childData["route_order"]));
      formData.append("user", this.childData["user_id"]);
      formData.append(
        "reference_number",
        // sessionStorage.getItem("reference_number")
        localStorage.getItem("reference_number")
      );
      if (error == 0) {
        this.uploadTypPerLine(formData).then(() => {
          this.removeFile(i);
          this.getTypAttach(
            this.childData["headerId"],
            this.childData["route_order"]
          );
        });
      } else {
        swal({
          title: "File type are not allowed",
          icon: "error",
          showCloseButton: true,
          dangerMode: true,
        });
      }
    },

    deleteFile(item) {
      this.deleteFileVuex(item).then(() => {
        this.getTypAttach(
          this.childData["headerId"],
          this.childData["route_order"]
        );
      });
    },

    convertFile(data) {
      let newAllAttach = JSON.stringify(data);
      let new_data = JSON.parse(newAllAttach);
      new_data = new_data.map((e, i) => ({
        ...e,
        file_path_name: JSON.parse(e.file_path_name),
      }));
      return new_data;
    },
  },
  computed: {
    ...mapGetters([
      "allForm2",
      "globalBasepath",
      "allTypAttach",
      "allPersonnel",
      "allGroup",
    ]),
  },
};
</script>