  }),
  methods: {
    ...mapActions(["saveProfile3", "fetchProfile", "fetchPopulation"]),
    ...mapMutations([]),

    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      9, 150.2;
      return parts.join(".");
    },
    nonNullItems(index) {
      let x = this.tables[index].tableHeader
        .filter((e) => {
          return e.total;
        })
        .map((e) => {
          return e.total;
        });
      return x;
    },
    sumField(key, index) {
      // sum data in give key (property)
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid) {
        let value = this.tables[index].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        return this.numberWithCommas(value.toFixed(2));
      }
    },
    sumFieldPer(key, index) {
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid && key != "totalKg") {
        let value = this.tables[index].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        let total = this.tables[index].tableData.reduce(
          (a, b) =>
            parseFloat(a) + (parseFloat(b["totalKg"].replace(/,/g, "")) || 0),
          0
        );
        let per = (value / total) * 100;
        if (isNaN(per)) {
          per = 0;
        }
        return this.numberWithCommas(per.toFixed(2)) + "%";
      }
    },

    getObjectUrl(file) {
      if (!file) {
        return;
      }
      return URL.createObjectURL(file);
      // return file;
    },

    // TABLE FUNCTION

    addItem(index, tableName) {
      // this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editTable = index;
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );
      this.dialogEdit = true;
    },
    editItem(index, tableName, item) {
      // this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editTable = index;
      //Finding index of relative object from outline table to edit form object
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );
      //Finding index of row in table data
      this.editedIndex = this.tables[index].tableData.indexOf(item);

      //Assign row data to Edit Forms
      let new_item = JSON.parse(JSON.stringify(item));
      let new_item2 = this.tables[index].tableData[this.editedIndex];

      this.editFormObject[this.editFormIndex].editableForm =
        this.editFormObject[this.editFormIndex].editableForm.map((e, i) => ({
          ...e,
          value: new_item2[e.model],
        }));

      this.dialogEdit = true;
    },
    deleteItem(index, item) {
      // this.editTable = tableName;
      this.tables[index].tableData.splice(
        this.tables[index].tableData.indexOf(item),
        1
      );
      // this.dialogDelete = true;
    },
    setDefaultEdit() {
      let editFormLengthObject =
        this.editFormObject[this.editFormIndex].editableForm.length;
      for (let i = 0; i < editFormLengthObject; i++) {
        this.editFormObject[this.editFormIndex].editableForm[i].value = "";
      }
      if (this.editFormIndex == 15) {
        this.editFormObject[this.editFormIndex].editableForm[4].value = [];
      }
    },
    closeModal() {
      this.setDefaultEdit();
      this.dialogEdit = false;

      this.$nextTick(() => {
        this.editedIndex = -1;
        this.editFormIndex = -1;
      });
    },
    save() {
      let m = [];
      m = this.editFormObject[this.editFormIndex].editableForm.map((e) => ({
        [e.model]: e.value,
      }));
      let merged = m.reduce((obj, item) => ({ ...obj, ...item }), {});
      if (this.editedIndex > -1) {
        Object.assign(
          this.tables[this.editTable].tableData[this.editedIndex],
          merged
        );
      } else {
        this.tables[this.editTable].tableData.push(merged);
      }
      console.log(merged);

      this.setDefaultEdit();
      this.closeModal();
    },

    // TABLE FUNCTION

    saveProfile() {
      // console.log(this.tables[10].tableData);
      var formData = new FormData();
      // formData.append("user", this.getStateSession["iis_employee_number"]);
      // formData.append("lgu", localStorage.getItem("city_id"));
      formData.append("tbl_3101", JSON.stringify(this.tables[10].tableData));
      console.log(this.tables[10].tableData);

      // this.tables[10].tableData.forEach((row) => {
      //   row.files.forEach((file) => {
      //     formData.append("files", file);
      //   });
      // });

      // for (var row in this.tables[10].tableData) {
      //   for (var index in row.file) {
      //     console.log(this.course[prop]);
      //   }
      // }

      for (var key in this.tables[10].tableData) {
        // skip loop if the property is from prototype
        if (!this.tables[10].tableData.hasOwnProperty(key)) continue;

        var obj = this.tables[10].tableData[key];
        for (var prop in obj) {
          // skip loop if the property is from prototype
          if (!obj.hasOwnProperty(prop)) continue;

          // your code
          // alert(prop + " = " + obj[prop]);
          console.log(obj[prop]);
        }
      }

      // this.saveProfile3(formData);
      console.log(formData);
      // this.saveProfile3({
      //   user: this.getStateSession["iis_employee_number"],
      //   lgu: localStorage.getItem("city_id"),
      //   tbl_311: JSON.stringify(this.tables[0].tableData),
      //   tbl_321: JSON.stringify(this.tables[1].tableData),
      //   tbl_322: JSON.stringify(this.tables[2].tableData),
      //   tbl_323: JSON.stringify(this.tables[3].tableData),
      //   tbl_341: JSON.stringify(this.tables[4].tableData),
      //   tbl_342: JSON.stringify(this.tables[5].tableData),
      //   tbl_371: JSON.stringify(this.tables[6].tableData),
      //   tbl_381: JSON.stringify(this.tables[7].tableData),
      //   tbl_3811: JSON.stringify(this.tables[8].tableData),
      //   tbl_391: JSON.stringify(this.tables[9].tableData),
      //   tbl_3101: JSON.stringify(this.tables[10].tableData),
      // });
    },

    getProfile() {
      this.fetchProfile({
        user: this.getStateSession["iis_employee_number"],
        lgu: localStorage.getItem("city_id"),
      }).then(() => {
        let profile = [];
        profile = JSON.stringify(this.allProfile);

        if (profile.length > 3) {
          let new_data = JSON.parse(profile);

          if (new_data.tbl_311) {
            let table1data = JSON.parse(new_data.tbl_311);
            this.tables[0].tableData = table1data;
          }

          if (new_data.tbl_321) {
            let table1data = JSON.parse(new_data.tbl_321);
            this.tables[1].tableData = table1data;
          }

          if (new_data.tbl_322) {
            let table1data = JSON.parse(new_data.tbl_322);
            this.tables[2].tableData = table1data;
          }

          if (new_data.tbl_323) {
            let table1data = JSON.parse(new_data.tbl_323);
            this.tables[3].tableData = table1data;
          }

          if (new_data.tbl_341) {
            let table1data = JSON.parse(new_data.tbl_341);
            this.tables[4].tableData = table1data;
          }
          if (new_data.tbl_342) {
            let table1data = JSON.parse(new_data.tbl_342);
            this.tables[5].tableData = table1data;
          }
          if (new_data.tbl_371) {
            let table1data = JSON.parse(new_data.tbl_371);
            this.tables[6].tableData = table1data;
          }
          if (new_data.tbl_381) {
            let table1data = JSON.parse(new_data.tbl_381);
            this.tables[7].tableData = table1data;
          }
          if (new_data.tbl_3811) {
            let table1data = JSON.parse(new_data.tbl_3811);
            this.tables[8].tableData = table1data;
          }
          if (new_data.tbl_391) {
            let table1data = JSON.parse(new_data.tbl_391);
            this.tables[9].tableData = table1data;
          }
          if (new_data.tbl_3101) {
            let table1data = JSON.parse(new_data.tbl_3101);
            this.tables[10].tableData = table1data;
          }
        }
      });
    },
  },
  computed: {
    ...mapGetters(["allProfile", "getStateSession", "allPopulation"]),
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },
  created() {
    this.getProfile();
  },
  watch: {
    tab() {
      if (this.tab == 1) {
        this.getProfile();
      }
    },
  },
};
</script>