<template>
  <v-container class="pa-1">
    <!-- <v-toolbar color="cyan" dark>
      <v-toolbar-title>Form Two Evaluation</v-toolbar-title>
    </v-toolbar> -->
    <v-card class="mx-auto card-padding pa-4">
      <!--Process Modal  -->
      <template>
        <v-dialog
          v-model="dialogProcess"
          persistent
          max-width="1000px"
          :retain-focus="false"
        >
          <template v-slot:activator="{}">
            <v-row>
              <v-col>
                <v-row>
                  <v-col>
                    <v-text-field
                      dense
                      label="Search Bar"
                      v-model="Search"
                      @keyup="getSearch"
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-combobox
                      dense
                      v-model="limitRow"
                      label="Show"
                      :items="allLimit"
                      @change="limitFunction"
                    >
                    </v-combobox>
                  </v-col>
                </v-row>
                <!-- <v-btn color="primary" dark v-bind="attrs" v-on="on">
                  Add New
                </v-btn> -->
              </v-col>
              <v-col>
                <!-- <v-btn @click="getSearch">Search</v-btn> -->
                <v-btn
                  small
                  class="float-right mini-margin-left"
                  @click="nextFunction"
                  >Next</v-btn
                >
                <v-btn small class="float-right" @click="prevFunction"
                  >Prev</v-btn
                >
                <input v-model="tgetCurrentrow" type="hidden" />
              </v-col>
            </v-row>
          </template>
          <template v-if="dialogProcess">
            <ProcessModal
              :childData="childData"
              @submit-data="handleSubmitData"
              @close-modal="handleCancel"
            />
          </template>
        </v-dialog>
      </template>

      <!-- View Evaluation Modal -->
      <template>
        <v-dialog
          v-model="dialogView"
          persistent
          max-width="900px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <!-- <span class="headline">{{ formTitle }}</span> -->
            </v-card-title>
            <v-card-text>
              <div class="text-h5 mb-4">Evaluations</div>
              <v-expansion-panels>
                <v-expansion-panel
                  v-for="(data, chapterIndex) in allothersEval"
                  :key="chapterIndex"
                >
                  <v-expansion-panel-header>
                    {{ data.name }}
                    <div class="text-subtitle-2" v-if="data.evaluator_app == 0">
                      <v-icon color="success" class="ml-4"
                        >mdi-checkbox-marked-circle</v-icon
                      >
                      Approved
                    </div>
                    <div class="text-subtitle-2" v-if="data.evaluator_app == 1">
                      <v-icon color="error" class="ml-4"
                        >mdi-close-circle</v-icon
                      >
                      Disapproved
                    </div>
                    <div
                      class="text-subtitle-2"
                      v-if="data.evaluator_app == null"
                    >
                      <v-icon color="blue-grey" class="ml-4"
                        >mdi-close-circle</v-icon
                      >
                      No Evaluation
                    </div>
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <v-divider></v-divider>
                    <div class="text-subtitle-2 mb-4">Comments:</div>
                    <p>
                      {{
                        data.evaluator_remarks == null
                          ? "None"
                          : data.evaluator_remarks
                      }}
                    </p>
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
              <v-btn
                class="mt-2 mx-2"
                small
                color="primary"
                @click="dialogViewPlan = true"
                >10-Year Plan</v-btn
              >
              <v-menu offset-y dense>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    v-bind="attrs"
                    v-on="on"
                    class="mt-2 mx-2"
                    small
                    color="primary"
                  >
                    Central Evaluation
                  </v-btn>
                </template>
                <v-list dense class="py-0">
                  <v-list-item
                    dense
                    @click="
                      eval_type = 0;
                      dialogViewForm2 = true;
                    "
                  >
                    Form 2
                  </v-list-item>
                  <v-list-item
                    dense
                    @click="
                      eval_type = 0;
                      dialogViewForm3 = true;
                    "
                  >
                    Form 3</v-list-item
                  >
                </v-list>
              </v-menu>
              <v-menu offset-y dense>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    v-bind="attrs"
                    v-on="on"
                    class="mt-2 mx-2"
                    small
                    color="primary"
                  >
                    Region Evaluation
                  </v-btn>
                </template>
                <v-list dense class="py-0">
                  <v-list-item
                    dense
                    @click="
                      eval_type = 1;
                      dialogViewForm2 = true;
                    "
                  >
                    Form 2
                  </v-list-item>
                  <v-list-item
                    dense
                    @click="
                      eval_type = 1;
                      dialogViewForm3 = true;
                    "
                  >
                    Form 3</v-list-item
                  >
                </v-list>
              </v-menu>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogView = false"
                >Close</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- Submit Confirmation Modal -->
      <template>
        <v-dialog v-model="dialogSubmit" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <h2>Are you sure want to submit ?</h2>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogSubmit = false"
                >No</v-btn
              >
              <v-btn color="info darken-1" text @click="submitHeader"
                >Yes</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- View Plan Modal -->
      <template>
        <v-dialog
          v-model="dialogViewPlan"
          max-width="1300px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewPlan">
                <TypViewModal :header_id="editedIndex" :typ_year="typ_year" />
              </template>
            </v-card-text>
          </v-card>
        </v-dialog>
      </template>

      <!-- View Form 2 Modal -->
      <template>
        <v-dialog
          v-model="dialogViewForm2"
          max-width="1300px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewForm2 == true && eval_type == 0">
                <Form2Modal :header_id="editedIndex" :form_id="form2_id" />
              </template>
              <template v-if="dialogViewForm2 == true && eval_type == 1">
                <Form2Modal :header_id="editedIndex" />
              </template>
            </v-card-text>
          </v-card>
        </v-dialog>
      </template>

      <!-- View Form 3 Modal -->
      <template>
        <v-dialog
          v-model="dialogViewForm3"
          max-width="1300px"
          :retain-focus="false"
          ><v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewForm3 == true && eval_type == 0">
                <Form3Modal :header_id="editedIndex" :form_id="form3_id" />
              </template>
              <template v-if="dialogViewForm3 == true && eval_type == 1">
                <Form3Modal :header_id="editedIndex" />
              </template>
            </v-card-text>
            <v-card-actions class="justify-end"> </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- View History Modal -->
      <template>
        <v-dialog
          v-model="dialogViewHistory"
          max-width="1700px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewHistory == true">
                <HistoryViewModal :header_id="editedIndex" />
              </template>
            </v-card-text>
          </v-card>
        </v-dialog>
      </template>

      <!-- TABLE -->
      <template>
        <v-simple-table
          class="mini-margin-top my_class"
          loading
          loading-text="Loading... Please wait"
        >
          <template v-slot:default>
            <thead>
              <tr>
                <th>Actions</th>
                <th class="display-none">#</th>
                <th>City</th>
                <th>Reference Number</th>
                <th>Submit By</th>
                <th>Remarks</th>
                <th>Assigned Evaluator</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="res in allPlanList" :key="res.typ_header_id">
                <td>
                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        class="mx-1"
                        color="info"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="getHeaderView(res)"
                      >
                        <v-icon small>mdi-archive-eye-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>View</span>
                  </v-tooltip>

                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        class="mx-1"
                        color="success"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="getHeaderEdit(res)"
                      >
                        <v-icon small>mdi-account-arrow-right</v-icon>
                      </v-btn>
                    </template>
                    <span>Process</span>
                  </v-tooltip>

                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        class="mx-1"
                        color="success"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="
                          editedIndex = res.id;
                          dialogViewHistory = true;
                        "
                      >
                        <v-icon small>mdi-history</v-icon>
                      </v-btn>
                    </template>
                    <span>History</span>
                  </v-tooltip>
                </td>
                <td class="display-none">{{ res.id }}</td>
                <td width="150px">{{ res.city_name }}</td>
                <td width="150px">{{ res.reference_number }}</td>
                <td width="210px">{{ res.sender_name }}</td>
                <td>{{ res.remarks }}</td>
                <td>
                  <v-chip
                    :color="res.group_name == null ? 'blue-grey' : 'success'"
                    class="ma-2 ml-0 white--text"
                  >
                    {{
                      res.group_name == null ? "Not Assigned" : res.group_name
                    }}
                  </v-chip>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>

        <v-overlay :value="overlay">
          <v-progress-circular indeterminate size="32"></v-progress-circular>
          Loading...
        </v-overlay>
      </template>
    </v-card>
  </v-container>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import TypViewModal from "../Include/typ_view_modal";
import Form2Modal from "../Include/form2_view";
import Form3Modal from "../Include/form3_view";
import HistoryViewModal from "../Include/history_view";
import ProcessModal from "../Include/process_modal";
export default {
  name: "List",
  components: {
    TypViewModal,
    Form2Modal,
    Form3Modal,
    HistoryViewModal,
    ProcessModal,
  },
  data: () => ({
    allLimit: [5, 10, 20, 50, 100],
    limitRow: [5],

    dialogView: false,
    dialogSubmit: false,
    dialogViewPlan: false,
    dialogViewForm2: false,
    dialogViewForm3: false,
    dialogViewHistory: false,
    dialogProcess: false,
    overlay: true,

    Search: "",

    user_region_id: "",
    region_num: "",
    cmbPersonnel: "",
    assigned_group: "",
    remarks: "",

    form2_id: 0,
    form3_id: 0,
    eval_type: 0,

    valid: true,

    editedIndex: -1,
    typ_year: 0,
    switch1: "",
    actionChoice: [
      {
        value: 1,
        name: "Submit",
      },
      {
        value: 0,
        name: "Assign",
      },
      {
        value: 2,
        name: "Return",
      },
    ],
    action_taken: "",
    requiredField: [(v) => !!v || "Field is required"],
    getCondition: "del",

    uploads: [],
    route_order: "",

    //new
    childData: {},
  }),
  methods: {
    ...mapActions([
      "increment",
      "decrement",
      "getOthersEval",
      "uploadTypPerLine",
      "fetchTypAttach",
      "deleteFileVuex",
      //new
      "fetchPlan",
      "SubmitHeaderPlan",
    ]),
    ...mapMutations(["setPlanList"]),
    // Table Function
    fetchListComponent() {
      this.fetchPlan({
        txtsearch: this.Search,
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: 4,
        new_app: 1,
        user_type: this.getStateSession["user_type"],
        user_id: this.getStateSession["id"],
        regionId: this.getStateSession["region_id"],
        group: this.getStateSession["group"],
      }).then(() => {
        this.overlay = false;
      });
    },
    nextFunction() {
      this.increment({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
      });
      this.fetchListComponent();
    },
    prevFunction() {
      this.decrement({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
      });
      this.fetchListComponent();
    },
    limitFunction() {
      this.fetchListComponent();
    },
    getSearch() {
      this.fetchListComponent();
    },

    // Modal Function
    getHeaderEdit(res) {
      this.editedIndex = res.id;
      this.region_num = res.rgnnum;
      this.route_order = parseInt(res.route_order);
      this.rowStatus = parseInt(res.status);
      this.dialogProcess = true;
      localStorage.setItem("reference_number", res.reference_number);

      //new
      this.childData = {
        user_role: this.getStateSession["access"],
        user_id: this.getStateSession["id"],

        headerId: res.id,
        regionId: this.getStateSession["region_id"],
        region_num: res.rgnnum,
        route_order: res.route_order,

        senderId: res.sender,
      };
    },
    handleSubmitData(newValue) {
      console.log(newValue);
      this.cmbPersonnel = newValue["personnel"];
      this.assigned_group = newValue["assigned_group"];
      this.remarks = newValue["remarks"];
      this.actionTaken = newValue["action1"];
      this.internalActionTaken = newValue["action2"];
      this.dialogSubmit = true;
    },
    handleCancel() {
      this.editedIndex = -1;
      this.dialogProcess = false;
    },
    getHeaderView(res) {
      this.editedIndex = res.id;
      this.user_region_id = res.region_id;
      this.txtStartYear = parseInt(res.year);
      this.txtReferenceNumber = res.reference_number;
      this.region_num = res.rgnnum;

      this.rowStatus = parseInt(res.status);
      this.route_order = parseInt(res.route_order);
      this.txtPcg = res.pcg;
      this.form2_id = res.form_id;
      this.form3_id = res.form_id3;

      this.typ_year = res.year;
      if (res.assigned_group == null) {
        swal({
          title: "Assign Group first",
          icon: "warning",
        });
        this.dialogView = false;
      } else {
        this.getOthersEval({
          typ_id: res.id,
          group: res.assigned_group,
          condition: "all",
          user_id: this.getStateSession["id"],
        });
        this.dialogView = true;
      }
    },

    // Process Function
    submitHeader() {
      console.log(this.assigned_group["value"]);
      let stat = 0;
      if (this.actionTaken.value == 0) {
        stat = this.rowStatus;
      } else if (this.actionTaken.value == 1) {
        stat = this.rowStatus + 1;
      } else {
        stat = this.rowStatus - 1;
      }
      this.SubmitHeaderPlan({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,

        user_type: this.getStateSession["user_type"],

        id: this.editedIndex,
        user_id: this.getStateSession["id"],
        user_name: this.getStateSession["name"],
        user_role: this.getStateSession["access"],
        regionId: this.getStateSession["region_id"],

        receiver_id: this.cmbPersonnel["value"],
        receiver_name: this.cmbPersonnel["text"],
        receiver_region: this.cmbPersonnel["region"],
        receiver_role: this.cmbPersonnel["role"],

        remarks: this.remarks,
        status: stat,
        route_order: this.route_order + 1,

        action_taken: this.actionTaken.name,
        internal_action: "",
        assigned_group: this.assigned_group["value"],
        date: this.getNow(),

        search_status: 4,
        new_app: 1,
        action: "",
        group: this.getStateSession["group"],
        city: "",
      });

      this.dialogSubmit = false;
      this.dialogProcess = false;
    },

    // Other Function

    getNow() {
      const today = new Date();
      const date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + " " + time;
      return dateTime;
    },
  },
  computed: {
    ...mapGetters([
      "tgetCurrentrow",
      "regionId",
      "allothersEval",
      "getStateSession",

      //new
      "allPlanList",
    ]),
  },
  created() {
    this.setPlanList([]); // mutation from planModule to remove the data from getter allPlanList
    setTimeout(this.fetchListComponent, 1000);
  },
};
</script>