<template>
  <v-stepper alt-labels v-model="e1">
    <!-- START OF STEPPER HEADER-->
    <v-stepper-header>
      <template v-for="(chapter, chapterIndex) in chapters">
        <v-stepper-step
          :complete="e1 > chapterIndex"
          :step="chapterIndex + 1"
          :key="chapter.chapterTitle"
          @click="skip(chapterIndex + 1)"
        >
          <div v-if="e1 - 1 == chapterIndex">
            <div v-if="e1 - 1 != 12">Chapter {{ chapterIndex + 1 }}</div>
            <div v-else-if="e1 - 1 == 12">Done</div>
          </div>
        </v-stepper-step>

        <v-divider
          v-if="chapterIndex < chapters.length - 1"
          :key="chapterIndex"
        />
      </template>
      <v-btn
        style="align-self: center"
        class="mr-5"
        fab
        depressed
        x-small
        @click="snackbar = !snackbar"
      >
        <v-icon small class="blue--text">mdi-help-circle-outline</v-icon>
      </v-btn>
    </v-stepper-header>
    <!-- END OF STEPPER HEADER-->

    <!-- START OF STEPPER CONTENT-->
    <v-stepper-items>
      <v-stepper-content
        v-for="(chapterContent, chapterIndex) in chapters"
        :key="chapterIndex"
        :step="chapterIndex + 1"
      >
        <!-- PRINT LOOP FOR CHAPTER TITLE-->
        <div class="text-h4" v-text="chapterContent.chapterTitle"></div>
        <!-- PRINT LOOP FOR CHAPTER TITLE-->
        <br />

        <!-- PRINT LOOP CHAPTER SUB TITLE-->
        <div class="text-h5" v-text="chapterContent.chapterSubTitle"></div>
        <!-- PRINT LOOP CHAPTER SUB TITLE-->
        <br />

        <!--START LOOP OF OUTLINE PER CHAPTER-->
        <v-container
          v-for="(outline, outlineIndex) in chapterContent.outline"
          :key="outlineIndex"
        >
          <!-- TEXTAREA OF OUTLINE PER CHAPTER-->
          <v-textarea
            outlined
            auto-grow
            :name="outline.outlineName"
            :label="outline.outlineTitle"
            :id="outline.outlineName"
            :value="outline.outlineModel"
            :ref="outline.outlineName"
            @change="(v) => (outline.outlineModel = v)"
          ></v-textarea>

          <!-- <vue-editor
            v-model="outline.outlineModel"
            :editor-toolbar="customToolbar"
          >
          </vue-editor>
          <div>{{ outline.outlineModel }}</div> -->

          <!--LOOP FOR UPLOAD within textarea FILES-->
          <v-container
            v-for="(upload, uploadIndex) in outline.uploads"
            :key="'up' + uploadIndex"
          >
            <v-row>
              <v-col md="8">
                <v-file-input
                  color="blue darken-2"
                  type="file"
                  ref="file"
                  :id="upload.uploadsName"
                  :label="upload.uploadsTitle"
                  multiple
                  placeholder="Select your files"
                  prepend-icon="mdi-paperclip"
                  outlined
                  dense
                  :show-size="1000"
                  small-chips
                  @change="
                    onFileChangePerOutline(
                      chapterIndex,
                      outlineIndex,
                      upload.uploadsName,
                      $event
                    )
                  "
                >
                  <template v-slot:selection="{ text }">
                    <v-chip color="blue darken-2" dark label small>
                      {{ text }}
                    </v-chip>
                  </template>
                </v-file-input>
              </v-col>
              <v-col md="4">
                <v-menu offset-y>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn color="primary" small dark v-bind="attrs" v-on="on">
                      Current File
                    </v-btn>
                  </template>
                  <v-list>
                    <v-list-item
                      v-for="(item, index) in upload.curr_data"
                      :key="index"
                      :href="item.new_name"
                      target="_blank"
                      :download="item.new_name"
                    >
                      <v-list-item-title>
                        {{ item.orig_name }}</v-list-item-title
                      >
                    </v-list-item>
                  </v-list>
                </v-menu>
                <v-btn
                  :id="upload.uploadsName + 'btn'"
                  :loading="upload.loading"
                  :disabled="upload.loading"
                  color="indigo darken-1"
                  class="ml-2 white--text"
                  @click="
                    loader = { chapterIndex, outlineIndex };
                    uploadFilePerOl(chapterIndex, outlineIndex);
                  "
                  small
                >
                  Upload
                  <v-icon right dark> mdi-cloud-upload </v-icon>
                </v-btn>
                <v-btn
                  style="align-self: center"
                  class="ml-2"
                  fab
                  depressed
                  x-small
                  @click="snackbarUpload = !snackbarUpload"
                >
                  <v-icon class="blue--text">mdi-help-circle-outline</v-icon>
                </v-btn>
              </v-col>

              <!-- <div class="p" v-text="upload.curr_data"></div> -->
            </v-row>
          </v-container>

          <!--START LOOP OF TABLE PER OUTLINE-->
          <v-container
            v-for="(table, tableIndex) in outline.outlineTable"
            :key="tableIndex"
          >
            <br />
            <!--START SET UP OF TABLE WITH LOOP FOR HEADERS AND TABLE DATA-->
            <template
              v-if="table.tableName == '621' || table.tableName == '821'"
            >
              <v-data-table
                :headers="table.tableHeader"
                :items="table.tableData"
                :hide-default-footer="true"
                class="elevation-1 my_class2"
                style="white-space: pre-line"
                dense
              >
                <!--START OF CREATING ACTIONS PER ROW-->
                <template v-slot:[`item.actions`]="{ item }">
                  <v-icon
                    small
                    class="mr-2"
                    @click="
                      editItem(
                        chapterIndex,
                        outlineIndex,
                        tableIndex,
                        table.tableName,
                        item
                      )
                    "
                  >
                    mdi-pencil
                  </v-icon>
                  <v-icon
                    small
                    @click="
                      deleteItem(chapterIndex, outlineIndex, tableIndex, item)
                    "
                    v-if="table.tableAddNew > 0"
                  >
                    mdi-delete
                  </v-icon>
                </template>
                <!--END OF CREATING ACTIONS PER ROW-->
                <!-- <template v-slot:[`item.wasteDivPer`]="{ item }">
                  <v-chip :class="'green lighten-4'">
                    {{ item.wasteDivPer }}
                  </v-chip>
                </template>
                <template v-slot:[`item.wasteDisPer`]="{ item }">
                  <v-chip :class="'green lighten-4'">
                    {{ item.wasteDisPer }}
                  </v-chip>
                </template>
                <template v-slot:[`item.wacsbioPer`]="{ item }">
                  <v-chip :class="'green lighten-4'">
                    {{ item.wacsbioPer }}
                  </v-chip>
                </template>
                <template v-slot:[`item.wacsrecPer`]="{ item }">
                  <v-chip :class="'green lighten-4'">
                    {{ item.wacsrecPer }}
                  </v-chip>
                </template>
                <template v-slot:[`item.wacsrwpdPer`]="{ item }">
                  <v-chip :class="'green lighten-4'">
                    {{ item.wacsrwpdPer }}
                  </v-chip>
                </template>
                <template v-slot:[`item.wacsdisPer`]="{ item }">
                  <v-chip :class="'green lighten-4'">
                    {{ item.wacsdisPer }}
                  </v-chip>
                </template>
                <template v-slot:[`item.wacsspecPer`]="{ item }">
                  <v-chip :class="'green lighten-4'">
                    {{ item.wacsspecPer }}
                  </v-chip>
                </template> -->
              </v-data-table>
            </template>

            <template v-else>
              <v-data-table
                v-if="!table.notShow"
                dense
                :headers="table.tableHeader"
                :items="table.tableData"
                :items-per-page="-1"
                :hide-default-footer="true"
                class="elevation-1 my_class2"
                style="white-space: pre-line"
                loading="false"
                loading-text="No Data"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title class="text-subtitle-2">{{
                      table.tableTitle
                    }}</v-toolbar-title>
                    <v-divider class="mx-4" inset vertical></v-divider>
                    <v-spacer></v-spacer>
                    <template v-if="table.tableAddNew > 0">
                      <v-btn
                        color="primary"
                        dark
                        class="mb-2"
                        @click="
                          addItem(
                            chapterIndex,
                            outlineIndex,
                            tableIndex,
                            table.tableName
                          )
                        "
                      >
                        New Item
                      </v-btn>
                    </template>
                  </v-toolbar>
                </template>
                <!-- v-if="table.tableName == '231'" -->

                <template slot="body.append" v-if="table.withTotal == 1">
                  <tr>
                    <td class="text-subtitle-2">Total</td>
                    <td
                      class="text-subtitle-2"
                      v-for="x in nonNullItems(
                        chapterIndex,
                        outlineIndex,
                        tableIndex
                      )"
                      :key="x"
                      :id="`${tableIndex}-${x}`"
                    >
                      {{ sumField(chapterIndex, outlineIndex, tableIndex, x) }}
                    </td>
                  </tr>
                </template>
                <template slot="body.append" v-if="table.withTotalPer == 1">
                  <tr>
                    <td class="text-subtitle-2">Total %</td>
                    <td
                      class="text-subtitle-2"
                      v-for="x in nonNullItems(
                        chapterIndex,
                        outlineIndex,
                        tableIndex
                      )"
                      :key="x"
                      :id="`${tableIndex}-${x}`"
                    >
                      {{
                        sumFieldPer(chapterIndex, outlineIndex, tableIndex, x)
                      }}
                    </td>
                  </tr>
                </template>
                <!--START OF CREATING ACTIONS PER ROW-->
                <template
                  v-slot:[`item.actions`]="{ item }"
                  v-if="table.nonAction == null"
                >
                  <v-icon
                    small
                    class="mr-2"
                    @click="
                      editItem(
                        chapterIndex,
                        outlineIndex,
                        tableIndex,
                        table.tableName,
                        item
                      )
                    "
                  >
                    mdi-pencil
                  </v-icon>
                  <v-icon
                    small
                    @click="
                      deleteItem(chapterIndex, outlineIndex, tableIndex, item)
                    "
                    v-if="table.tableAddNew > 0"
                  >
                    mdi-delete
                  </v-icon>
                </template>
                <!--END OF CREATING ACTIONS PER ROW-->
              </v-data-table>
            </template>
            <!--END SET UP OF TABLE WITH LOOP FOR HEADERS AND TABLE DATA-->
            <br />
          </v-container>
          <!--END LOOP OF TABLE PER OUTLINE-->
          <hr />
        </v-container>
        <!--END LOOP OF OUTLINE PER CHAPTER-->

        <!--UPLOAD FILES SAMPLE FOR STEP 13-->
        <v-container
          v-for="(upload, uploadIndex) in chapterContent.uploads"
          :key="uploadIndex"
        >
          <v-row>
            <v-col md="9">
              <v-file-input
                color="blue darken-2"
                type="file"
                ref="file"
                :id="upload.uploadsName"
                :label="upload.uploadsTitle"
                multiple
                placeholder="Select your files"
                prepend-icon="mdi-paperclip"
                outlined
                dense
                :show-size="1000"
                small-chips
                @change="onFileChange(uploadIndex, upload.uploadsName, $event)"
              >
                <template v-slot:selection="{ text }">
                  <v-chip color="blue darken-2" dark label small>
                    {{ text }}
                  </v-chip>
                </template>
              </v-file-input>
            </v-col>
            <v-col md="3">
              <v-menu offset-y>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn color="primary" dark v-bind="attrs" v-on="on">
                    Current File
                  </v-btn>
                </template>
                <v-list>
                  <v-list-item
                    v-for="(item, index) in upload.curr_data"
                    :key="index"
                    :href="item.new_name"
                    target="_blank"
                    :download="item.new_name"
                  >
                    <v-list-item-title> {{ item.orig_name }}</v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>
              <v-btn
                :id="upload.uploadsName + 'btn'"
                :loading="upload.loading"
                :disabled="upload.loading"
                color="indigo darken-1"
                class="ml-2 white--text"
                @click="uploadImage(chapterIndex, uploadIndex)"
              >
                Upload
                <v-icon right dark> mdi-cloud-upload </v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </v-container>
        <!--UPLOAD FILES  SAMPLE FOR STEP 13-->

        <!--CARD FOOTER-->
        <v-card-actions>
          <v-btn
            @click="previous"
            v-if="chapterIndex > 0 && chapterIndex < chapters.length"
            >Back
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
            color="success"
            @click="next"
            v-if="chapterIndex < chapters.length - 1"
            >Save and Proceed</v-btn
          >
          <!-- <v-btn
            color="primary"
            @click="uploadImage('Draft')"
            v-if="chapterIndex == chapters.length - 2"
            >Save</v-btn
          > -->
          <!-- <v-btn
            color="success"
            @click="uploadImage('Submit')"
            v-if="chapterIndex == chapters.length - 2"
            >Submit</v-btn
          > -->
          <v-btn
            color="success"
            href="#/eplan"
            v-if="chapterIndex == chapters.length - 1"
            >Done!</v-btn
          >
        </v-card-actions>
        <!--CARD FOOTER-->
      </v-stepper-content>
    </v-stepper-items>
    <!-- END OF STEPPER CONTENT-->

    <!--START DIALOG FOR EDITING DATA ROW-->
    <v-dialog v-model="dialogEdit" :retain-focus="false">
      <v-card>
        <v-card-title>
          <span class="text-h5">{{ formTitle }}</span>
        </v-card-title>

        <v-card-text>
          <v-container>
            <template v-if="dialogEdit">
              <EditModal
                :chapters="chapters"
                :dialogEdit="dialogEdit"
                :indexes="indexes"
                :editFormObject="editFormObject"
                :editFormIndex="editFormIndex"
              />
            </template>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save"> Enter </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!--END DIALOG FOR EDITING DATA ROW-->

    <!--START DIALOG FOR CONFIRMING DELETE DATA ROW-->
    <v-dialog v-model="dialogDelete" :retain-focus="false" max-width="500px">
      <v-card>
        <v-card-title class="text-h5"
          >Are you sure you want to delete this item?</v-card-title
        >
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="deleteItemConfirm"
            >OK</v-btn
          >
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!--END DIALOG FOR CONFIRMING DELETE DATA ROW-->

    <v-snackbar
      v-model="snackbar"
      color="success"
      outlined
      timeout="5000"
      :left="true"
      :bottom="true"
      transition="slide-x-transition"
    >
      You can use tab header buttons to skip to specific chapter.
      <br />
      <span class="red--text"
        ><v-icon class="red--text" small>mdi-alert-circle-outline</v-icon> Using
        this button <strong>won't save your work</strong>.
      </span>
      <br />
      <span class="blue--text"
        ><v-icon class="blue--text" small>mdi-alert-outline</v-icon> To save
        your work, click <strong>"Save and Proceed"</strong> button
      </span>

      <!-- <template v-slot:action="{ attrs }">
        <v-btn color="indigo" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template> -->
    </v-snackbar>

    <v-snackbar
      v-model="snackbarUpload"
      color="primary"
      outlined
      timeout="4000"
      :middle="true"
      :top="true"
      transition="slide-x-transition"
    >
      <span class="text-h6 red--text"
        ><v-icon class="red--text">mdi-alert-circle-outline</v-icon>
        <strong> Note:</strong> Maximum filesize is 5MB.
      </span>
      <br />
      <span class="text-h6 blue--text"
        ><v-icon class="blue--text">mdi-alert-outline</v-icon> Accepted
        filetypes are<strong> PDF,JPEG,PNG </strong>only.
      </span>

      <!-- <template v-slot:action="{ attrs }">
        <v-btn color="indigo" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template> -->
    </v-snackbar>
  </v-stepper>
</template>

<script>
//import VTextField from "vuetify/lib/components/VTextField";
//VTextField
import EditModal from "./Modal/edit_modal";
import { mapGetters, mapActions } from "vuex";

export default {
  components: {
    EditModal,
  },
  props: ["chapters", "editFormObject"],
  data() {
    return {
      //e1: 1,
      file: "",
      name: "Planning",
      starting_year: "",
      projected_year: [],
      year: "",
      subEdit: [],
      files: [],

      dialogEdit: false,
      dialogDelete: false,
      editedIndex: -1,
      editFormIndex: -1,

      indexes: {
        chapter: "",
        outline: "",
        table: "",
      },

      loader: {},
      loading: false,
      header_id: localStorage.getItem("header_id"),

      snackbar: false,
      snackbarUpload: false,
      customToolbar: [
        ["bold", "italic", "underline"],
        [{ list: "ordered" }, { list: "bullet" }],
        ["image", "code-block"],
      ],
    };
  },
  created() {
    var year = localStorage.getItem("startingYear");
    this.pushProjectedYear(year);

    this.starting_year = year;

    // this.fetchBarangay(this.$route.query.city).then(() => {
    //   this.pushBarangayPerTable(this.allBarangay, "231");
    //   this.pushBarangayPerTable(this.allBarangay, "232");
    //   this.pushBarangayPerTable(this.allBarangay, "831");
    //   this.pushBarangayPerTable(this.allBarangay, "541");
    // });

    this.fetchPerChapter({
      city: localStorage.getItem("city_id"),
      header_id: localStorage.getItem("header_id"),
      chapter: this.$route.query.chapter,
    }).then(() => {
      if (this.$route.query.chapter == 2) {
        this.fetchLandList({
          offset: 0,
          limit: 100,
        }).then(() => {
          this.pushLandClass(this.allLand);
        });

        this.fetchNatureList({
          offset: 0,
          limit: 100,
        }).then(() => {
          this.pushNature(this.allNature);
        });
      }
      this.setChapterDetails();
    });

    this.fetchAttachPerChapter({
      city: localStorage.getItem("city_id"),
      header_id: localStorage.getItem("header_id"),
      chapter: this.$route.query.chapter,
    }).then(() => {
      this.setAttachment();
    });
  },
  methods: {
    ...mapActions([
      "createChapter1",
      "createChapter2",
      "createChapter3",
      "createChapter4",
      "createChapter5",
      "createChapter6",
      "createChapter7",
      "createChapter8",
      "createChapter9",
      "createChapter10",
      "createChapter11",
      "createChapter12",
      "uploadFile",
      "uploadFilePerOutline",
      "fetchBarangay",
      "previousVueX",
      "fetchPerChapter",
      "fetchAttachPerChapter",
      "fetchLandList",
      "fetchNatureList",
      "fetchPopulation", // From chapters.js
      "fetchWasteGen", // From chapters.js
      "fetchWasteDiv", // From evaluationModule.js
      "upperSkip",
    ]),
    getNextChap(num) {
      this.fetchPerChapter({
        city: localStorage.getItem("city_id"),
        header_id: localStorage.getItem("header_id"),
        chapter: num,
      }).then(() => {
        if (num == 2) {
          this.fetchLandList({
            offset: 0,
            limit: 100,
          }).then(() => {
            this.pushLandClass(this.allLand);
          });

          this.fetchNatureList({
            offset: 0,
            limit: 100,
          }).then(() => {
            this.pushNature(this.allNature);
          });
        }
        this.setChapterDetails();
      });
      this.fetchAttachPerChapter({
        city: localStorage.getItem("city_id"),
        header_id: localStorage.getItem("header_id"),
        chapter: num,
      }).then(() => {
        this.setAttachment();
      });
    },
    setChapterDetails() {
      let newAllChapter = [];
      newAllChapter = JSON.stringify(this.allChapter);
      if (newAllChapter.length >= 3) {
        var chapterBase = this.e1 - 1;

        if (this.e1 == 1) {
          let new_data = JSON.parse(newAllChapter)[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.introduction;
          this.chapters[chapterBase].outline[1].outlineModel = new_data.purpose;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.approach;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.acknowledgement;
        }
        if (this.e1 == 2) {
          let new_data = JSON.parse(newAllChapter)[0];
          //let new_data = new_data1[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.location;
          this.chapters[chapterBase].outline[1].outlineModel = new_data.history;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.population;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.economic_profile_land_use;
          this.chapters[chapterBase].outline[4].outlineModel =
            new_data.physical_characteristic;
          this.chapters[chapterBase].outline[5].outlineModel =
            new_data.topography;
          this.chapters[chapterBase].outline[6].outlineModel = new_data.geology;
          this.chapters[chapterBase].outline[7].outlineModel = new_data.climate;

          // Checking and populatings For table 231
          if (new_data.current_population_barangay) {
            let curPop = JSON.parse(new_data.current_population_barangay);
            this.chapters[chapterBase].outline[2].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[2].outlineTable[0].tableData =
              curPop;
          } else {
            this.fetchBarangay2("231");
          }

          // Checking and populating For table 232
          if (new_data.ten_year_projected_p) {
            let tenYear = JSON.parse(new_data.ten_year_projected_p);
            this.chapters[chapterBase].outline[2].outlineTable[1].tableData =
              [];
            this.chapters[chapterBase].outline[2].outlineTable[1].tableData =
              tenYear;
          } else {
            this.fetchBarangay2("232");
          }

          // Checking and populating  For table 241
          if (new_data.ten_year_projected_p) {
            let listInd = JSON.parse(new_data.list_industries_within_city);
            this.chapters[chapterBase].outline[3].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[0].tableData =
              listInd;
          }

          // Checking and populating  For table 242
          if (new_data.land_used_map_urban) {
            let mapUrban = JSON.parse(new_data.land_used_map_urban);
            this.chapters[chapterBase].outline[3].outlineTable[1].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[1].tableData =
              mapUrban;
          }

          // Checking and populating  For table 243
          if (new_data.land_used_map_urban) {
            let mapGen = JSON.parse(new_data.land_used_map_general);
            this.chapters[chapterBase].outline[3].outlineTable[2].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[2].tableData =
              mapGen;
          }
        }
        if (this.e1 == 3) {
          let new_data = JSON.parse(newAllChapter)[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.inistitutional_arrangement;
