    setAttachment() {
      let newAllAttach = JSON.stringify(this.allAttach);
      let new_data = JSON.parse(newAllAttach);
      let reference_number = localStorage.getItem("reference_number");
      var chapterBase = this.e1 - 1;
      if (new_data) {
        let count = new_data.length;
        for (let i = 0; i < count; i++) {
          let [b] = [new_data[i]].map((e, i) => ({
            ...e,
            file_path_name: JSON.parse(e.file_path_name).map((e, i) => ({
              ...e,
              //new_name: `${this.globalBasepath}/swms_api/public/typ_file/${reference_number}/outline/${e.new_name}`,
              new_name: this.globalFileChangeName(
                this.globalBasepath,
                reference_number,
                e.new_name
              ),
            })),
          }));
          if (chapterBase == 12) {
            // this.chapters[chapterBase].uploads[new_data[i].outline].curr_data =
            //   b.file_path_name;
          } else {
            this.chapters[chapterBase].outline[
              new_data[i].outline
            ].uploads[0].curr_data = b.file_path_name;
          }
        }
      }
    },

    pushProjectedYear(event) {
      this.projected_year = [];
      if (this.chapters[1].outline[2].outlineTable[1].tableHeader.length > 3) {
        this.chapters[1].outline[2].outlineTable[1].tableHeader.splice(1, 10);
      }
      //this.editFormObject[1].editableForm.splice(1);
