    pushPopulation() {
      let new_data = JSON.parse(this.allPopulation);
      console.log(new_data);
      if (new_data != undefined) {
        this.chapters[3].outline[2].outlineTable[1].tableData =
          this.chapters[3].outline[2].outlineTable[1].tableData.map((e, i) => ({
            ...e,
            // pcg: localStorage.getItem("pcg"),
            popul: this.numberWithCommas(new_data[0]["column" + i]),
          }));

        this.chapters[5].outline[1].outlineTable[0].tableData =
          this.chapters[5].outline[1].outlineTable[0].tableData.map((e, i) => ({
            ...e,
            projPopulation: this.numberWithCommas(new_data[0]["column" + i]),
          }));
      }
    },

    pushPCG() {
      let new_data = JSON.parse(this.allPopulation);
      if (new_data != undefined) {
        this.chapters[3].outline[2].outlineTable[1].tableData =
          this.chapters[3].outline[2].outlineTable[1].tableData.map((e, i) => ({
