    getWasteGen(chapterBase, outline, table) {
      this.fetchWasteGen(this.header_id).then(() => {
        this.pushWasteGen(chapterBase, outline, table);
      });
    },

    pushWasteGen(chapterBase, outline, table) {
      let x = JSON.parse(this.allWasteGen);
      if (x[0] != undefined) {
        let y = JSON.parse(x[0]["generated_waste_projected"]);
        let m = y.map((e, i) => ({
          [e.year.toString()]: e.totalKg,
        }));
        let new_data = m.reduce((obj, item) => ({ ...obj, ...item }), {});

        if (chapterBase == 5 && outline == 1 && table == 0) {
          this.chapters[chapterBase].outline[outline].outlineTable[
            table
          ].tableData = this.chapters[chapterBase].outline[
            outline
          ].outlineTable[table].tableData.map((e, i) => ({
            ...e,
            wasteGen: new_data[e.year],
            // .filter((e2) => parseInt(e.year) === parseInt(e2.year))
            // .map((e3, i) => {
            //   return e3.totalKg;
            // }),
          }));
        }
      }

      //let merged = m.reduce((obj, item) => ({ ...obj, ...item }), {});
    },

    getWasteDiv(chapterBase, outline, table) {
      this.fetchWasteDiv(this.header_id).then(() => {
        this.pushWasteDiv(chapterBase, outline, table);
      });
    },

    pushWasteDiv(chapterBase, outline, table) {
      let x = JSON.parse(this.allwasteDiv);
      if (x[0] != undefined) {
        let y = JSON.parse(x[0]["target_per_year"]);
        this.chapters[chapterBase].outline[outline].outlineTable[
          table
        ].tableData = y;
      }
    },

