        });
        this.scrollTop();
        this.e1;
      }

      if (this.e1 == 10) {
        this.createChapter10({
          header_id: localStorage.getItem("header_id"),
          city_id: localStorage.getItem("city_id"),
          social_aspects: document.getElementById("socialAspectsOutline").value,
          environmental: document.getElementById("environmentalOutline").value,
        });
        this.scrollTop();
        this.e1;
      }

      if (this.e1 == 11) {
        this.createChapter11({
          header_id: localStorage.getItem("header_id"),
          city_id: localStorage.getItem("city_id"),
          investment_cost: document.getElementById("investmentCostOutline")
            .value,
          annual_cost: document.getElementById("annualCostOutline").value,
          funding_option: document.getElementById("fundingOptionOutline").value,
          evaluation: document.getElementById("evaluationOutline").value,
          summary: document.getElementById("summaryOutline").value,
          //Tables
          investment_cost_data: JSON.stringify(
            this.chapters[10].outline[0].outlineTable[0].tableData
          ),
          personnel_services: JSON.stringify(
            this.chapters[10].outline[1].outlineTable[0].tableData
          ),
          operating_services: JSON.stringify(
            this.chapters[10].outline[1].outlineTable[1].tableData
          ),
          funding_option_data: JSON.stringify(
            this.chapters[10].outline[2].outlineTable[0].tableData
          ),
          cost_evaluation: JSON.stringify(
            this.chapters[10].outline[3].outlineTable[0].tableData
          ),
          summary_data: JSON.stringify(
            this.chapters[10].outline[4].outlineTable[0].tableData
          ),
        });
        this.scrollTop();
        this.e1;
      }

      if (this.e1 == 12) {
        this.createChapter12({
          header_id: localStorage.getItem("header_id"),
          city_id: localStorage.getItem("city_id"),
          phase_res: document.getElementById("PhaseResOutline").value,
          milestone: document.getElementById("milestoneOutline").value,
          implementation_sched: document.getElementById(
            "implementationSchedOutline"
          ).value,
          implementation_sched_data: JSON.stringify(
            this.chapters[11].outline[2].outlineTable[0].tableData
          ),
        });
        this.scrollTop();
        this.e1;
      }
      let num = parseInt(this.e1) + 1;
      this.getNextChap(num);
    },

    onFileChange(index, id, e) {
      var tenYearPlan = document.getElementById(id).files;
      this.chapters[12].uploads[index].uploads = tenYearPlan;
    },

    uploadImage(chapterIndex, uploadIndex) {
      let count =
        this.chapters[chapterIndex].uploads[uploadIndex].uploads.length;
      var formData = new FormData();

      if (count > 0) {
        for (var i = 0; i < count; i++) {
          formData.append(
            "files[]",
            this.chapters[chapterIndex].uploads[uploadIndex].uploads[i]
          );
        }

        let curr_count =
          this.chapters[chapterIndex].uploads[uploadIndex].curr_data.length;
        if (curr_count > 0) {
          for (var i = 0; i < curr_count; i++) {
            formData.append(
              "curr_files[]",
              this.chapters[chapterIndex].uploads[uploadIndex].curr_data[
                i
              ].new_name.replace(
                //"http://swm.emb.gov.ph/swms_api/uploads/",
                `${this.globalBasepath}/swms_api/uploads/`,
                ""
              )
            );
          }
        }

        formData.append("header_id", localStorage.getItem("header_id"));
        formData.append("city_id", localStorage.getItem("city_id"));
        formData.append("chapter", chapterIndex + 1);
        formData.append("outline", uploadIndex);

        this.uploadFilePerOutline(formData).then(() => {
          this.fetchAttachPerChapter({
            city: localStorage.getItem("city_id"),
            header_id: localStorage.getItem("header_id"),
            chapter: chapterIndex + 1,
          }).then(() => {
            this.setAttachment();
          });
        });
      } else {
        swal({
          title: "Attachment field is empty!",
          icon: "error",
          showCloseButton: true,
          dangerMode: true,
        });
      }
    },

    onFileChangePerOutline(chapterIndex, outlineIndex, id, e) {
      var tenYearPlan = document.getElementById(id).files;
      this.chapters[chapterIndex].outline[outlineIndex].uploads[0].uploads =
        tenYearPlan;
    },
    uploadFilePerOl(chapterIndex, outlineIndex) {
      let error = 0;
      let allowed_ext = [
        "image/png",
        "image/jpeg",
        "image/svg",
        "application/pdf",
      ];
      let count =
        this.chapters[chapterIndex].outline[outlineIndex].uploads[0].uploads
          .length;
      let reference_number = localStorage.getItem("reference_number");
      var formData = new FormData();

      if (count > 0) {
        for (var i = 0; i < count; i++) {
          console.log(
            this.chapters[chapterIndex].outline[outlineIndex].uploads[0]
              .uploads[i]["type"]
          );
          if (
            !allowed_ext.includes(
              this.chapters[chapterIndex].outline[outlineIndex].uploads[0]
                .uploads[i]["type"]
            )
          ) {
            error++;
          }
          formData.append(
            "files[]",
            this.chapters[chapterIndex].outline[outlineIndex].uploads[0]
              .uploads[i]
          );
        }

        let curr_count =
          this.chapters[chapterIndex].outline[outlineIndex].uploads[0].curr_data
            .length;
        if (curr_count > 0) {
          for (var i = 0; i < curr_count; i++) {
            formData.append(
              "curr_files[]",
              this.chapters[chapterIndex].outline[
                outlineIndex
              ].uploads[0].curr_data[i].new_name.replace(
                //"http://swm.emb.gov.ph/swms_api/uploads/",
                this.globalFileChangeNameForUpload(
                  this.globalBasepath,
                  reference_number
                ),
                ""
              )
            );
          }
        }
        formData.append("header_id", localStorage.getItem("header_id"));
        formData.append("city_id", localStorage.getItem("city_id"));
        formData.append("chapter", chapterIndex + 1);
        formData.append(
          "reference_number",
          localStorage.getItem("reference_number")
        );
        formData.append("outline", outlineIndex);

        if (error == 0) {
          this.uploadFilePerOutline(formData).then(() => {
            this.fetchAttachPerChapter({
              city: localStorage.getItem("city_id"),
              header_id: localStorage.getItem("header_id"),
              chapter: chapterIndex + 1,
            }).then(() => {
              this.setAttachment();
            });
          });
        } else {
          swal({
            title: "File type are not allowed",
            icon: "error",
            showCloseButton: true,
            dangerMode: true,
          });
        }
      } else {
        swal({
          title: "Attachment field is empty!",
          icon: "error",
          showCloseButton: true,
          dangerMode: true,
        });
      }
    },

    submit() {},
    previous() {
      this.previousVueX(this.e1);
      let num = parseInt(this.e1);
      this.getNextChap(num);
      this.scrollTop();
    },
    skip(chap) {
      this.upperSkip(chap);
      let num = parseInt(this.e1);
      this.getNextChap(num);
      this.scrollTop();
    },
    scrollTop() {
      setTimeout(() => {
        window.scrollTo({
          top: 0,
          behavior: "smooth",
        });
      }, 500);
    },
    // PAGE TRANSITIONING

    //TABLES FUNCTION
    putIndex(chapterIndex, outlineIndex, tableIndex) {
      this.indexes.chapter = chapterIndex;
      this.indexes.outline = outlineIndex;
      this.indexes.table = tableIndex;
    },
    addItem(chapterIndex, outlineIndex, tableIndex, tableName) {
      this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );
      this.dialogEdit = true;
    },
    editItem(chapterIndex, outlineIndex, tableIndex, tableName, item) {
      this.putIndex(chapterIndex, outlineIndex, tableIndex);

      //Finding index of relative object from outline table to edit form object
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );

      //Finding index of row in table data
      this.editedIndex =
        this.chapters[chapterIndex].outline[outlineIndex].outlineTable[
          tableIndex
        ].tableData.indexOf(item);

      //Assign row data to Edit Forms
      let new_item = JSON.parse(JSON.stringify(item));
      this.editFormObject[this.editFormIndex].editableForm =
        this.editFormObject[this.editFormIndex].editableForm.map((e, i) => ({
          ...e,
          value: new_item[e.model],
        }));

      this.dialogEdit = true;

      //this.subEdit = [];
      // for (const key of Object.keys(new_item)) {
      //   this.subEdit.push(new_item[key]);
      // }

      // let editFormLengthObject =
      //   this.editFormObject[this.editFormIndex].editableForm.length;

      // for (let i = 0; i < editFormLengthObject; i++) {
      //   this.editFormObject[this.editFormIndex].editableForm[i].value =
      //     this.subEdit[i];
      // }
    },

    deleteItem(chapterIndex, outlineIndex, tableIndex, item) {
      this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editedIndex =
        this.chapters[chapterIndex].outline[outlineIndex].outlineTable[
          tableIndex
        ].tableData.indexOf(item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.chapters[this.indexes.chapter].outline[
        this.indexes.outline
      ].outlineTable[this.indexes.table].tableData.splice(this.editedIndex, 1);
      //=============FOR TABLE 412 ====================//
      if (
        this.indexes.chapter == 3 &&
        this.indexes.outline == 0 &&
        this.indexes.table == 1
      ) {
        this.chapters[3].outline[1].outlineTable[0].tableData.splice(
          this.editedIndex,
          1
        );
      }
      this.closeDelete();
    },

    close() {
      this.setDefaultEdit();
      this.dialogEdit = false;
      this.$nextTick(() => {
        this.editedIndex = -1;
        this.editFormIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedIndex = -1;
        this.editedIndex = -1;
      });
    },

    setDefaultEdit() {
      let editFormLengthObject =
        this.editFormObject[this.editFormIndex].editableForm.length;
      for (let i = 0; i < editFormLengthObject; i++) {
        this.editFormObject[this.editFormIndex].editableForm[i].value = "";
      }
    },

    save() {
      // let editFormLengthObject =
      //   this.editFormObject[this.editFormIndex].editableForm.length;

      // let keys = [];
      // let values = [];
      // for (let i = 0; i < editFormLengthObject; i++) {
      //   keys.push(
      //     this.editFormObject[this.editFormIndex].editableForm[i].model
      //   );
      //   values.push(
      //     this.editFormObject[this.editFormIndex].editableForm[i].value
      //   );
      // }
      // const merged2 = keys.reduce(
      //   (obj, key, index) => ({ ...obj, [key]: values[index] }),
      //   {}
      // );

      let m = [];
      m = this.editFormObject[this.editFormIndex].editableForm.map((e) => ({
        [e.model]: e.value,
      }));
      let merged = m.reduce((obj, item) => ({ ...obj, ...item }), {});
      if (this.editedIndex > -1) {
        Object.assign(
          this.chapters[this.indexes.chapter].outline[this.indexes.outline]
            .outlineTable[this.indexes.table].tableData[this.editedIndex],
          merged
        );
      } else {
        this.chapters[this.indexes.chapter].outline[
          this.indexes.outline
        ].outlineTable[this.indexes.table].tableData.push(merged);
      }
      //=============FOR COPY OF TABLE 412 TO TABLE 421 ====================//
      console.log(merged);
      if (
        this.indexes.chapter == 3 &&
        this.indexes.outline == 0 &&
        this.indexes.table == 1
      ) {
        if (this.editedIndex > -1) {
          Object.assign(
            this.chapters[3].outline[1].outlineTable[0].tableData[
              this.editedIndex
            ],
            merged
          );
        } else {
          this.chapters[3].outline[1].outlineTable[0].tableData.push(merged);
        }
      }

      //=============FOR COPY OF TABLE 412 TO TABLE 421 ====================//
      this.setDefaultEdit();
      this.close();
    },
    //TABLES FUNCTION

    sumField(chapterBase, outline, outlineTable, key) {
      // sum data in give key (property)
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid) {
        let value = this.chapters[chapterBase].outline[outline].outlineTable[
          outlineTable
        ].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        return this.numberWithCommas(value.toFixed(2));
      }
    },

    sumFieldPer(chapterBase, outline, outlineTable, key) {
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid && key != "totalKg") {
        let value = this.chapters[chapterBase].outline[outline].outlineTable[
          outlineTable
        ].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        let total = this.chapters[chapterBase].outline[outline].outlineTable[
          outlineTable
        ].tableData.reduce(
          (a, b) =>
            parseFloat(a) + (parseFloat(b["totalKg"].replace(/,/g, "")) || 0),
          0
        );
        let per = (value / total) * 100;
        if (isNaN(per)) {
          per = 0;
        }
        return this.numberWithCommas(per.toFixed(2)) + "%";
      }
    },

    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return parts.join(".");
    },
    nonNullItems(chapterIndex, outlineIndex, tableIndex) {
      let x = this.chapters[chapterIndex].outline[outlineIndex].outlineTable[
        tableIndex
      ].tableHeader
        .filter((e) => {
          return e.total;
        })
        .map((e) => {
          return e.total;
        });
      return x;
    },

    //TABLES FUNCTION
  },
  computed: {
    ...mapGetters({
      e1: "e1",
      allBarangay: "allBarangay",
      allChapter: "allChapter",
      allLand: "allLand",
      allNature: "allNature",
      allAttach: "allAttach",
      globalBasepath: "globalBasepath",
      allPopulation: "allPopulation",
      allWasteGen: "allWasteGen",
      allwasteDiv: "allwasteDiv",
      globalHeader: "globalHeader",
      globalCity: "globalCity",
    }),
    // years() {
    //   let year = new Date().getFullYear();
    //   return Array.from(
    //     { length: 10 },
    //     (value, index) => parseInt() + index
    //   );
    // },
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },
  watch: {
    dialogEdit(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    loader() {
      //l is the data of clicked button
      //const l = this.loader;
      //call the data of clicked button,and change to true
      // this[l] = !this[l];

      this.chapters[this.loader.chapterIndex].outline[
        this.loader.outlineIndex
      ].uploads[0].loading =
        !this.chapters[this.loader.chapterIndex].outline[
          this.loader.outlineIndex
        ].uploads[0].loading;

      setTimeout(
        () =>
          (this.chapters[this.loader.chapterIndex].outline[
            this.loader.outlineIndex
          ].uploads[0].loading = false),
        1000
      );
    },
  },
};
</script>
