<template>
  <v-container>
    <v-tabs
      v-model="tab"
      background-color="transparent"
      color="basil"
      absolute
      grow
    >
      <v-tab
        v-for="(chapterContent, chapterIndex) in chapters"
        :key="chapterIndex"
        :tab="chapterIndex + 1"
      >
        Chapter {{ chapterIndex + 1 }}
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item
        v-for="(chapterContent, chapterIndex) in chapters"
        :key="chapterIndex"
        :tab="chapterIndex + 1"
      >
        <v-card color="basil" flat>
          <br />
          <div
            class="text-h4 text-center"
            v-text="chapterContent.chapterTitle"
          ></div>
          <br />
          <div
            class="text-h5 text-center"
            v-text="chapterContent.chapterSubTitle"
          ></div>
          <!-- PRINT LOOP CHAPTER SUB TITLE-->
          <!-- <br />
          <div class="text-h5" v-text="chapterContent.chapterSubTitle"></div> -->
          <br />
          <v-container
            v-for="(outline, outlineIndex) in chapterContent.outline"
            :key="outlineIndex"
          >
            <p class="text-h6">{{ outline.outlineTitle }}</p>
            <p>{{ outline.outlineModel }}</p>

            <!--START ATTACHMENT PER OUTLINE-->
            <v-container
              class="not-to-print"
              v-for="(upload, uploadIndex) in outline.uploads"
              :key="'up' + uploadIndex"
            >
              <v-row id="not-to-print" class="not-to-print">
                <v-col md="4">
                  <v-menu offset-y>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        small
                        color="primary"
                        dark
                        v-bind="attrs"
                        v-on="on"
                      >
                        PDF Attachments
                      </v-btn>
                    </template>
                    <v-list>
                      <!-- :href="item.new_name" -->
                      <!-- v-if="item.ext == `pdf`" -->
                      <template v-for="(item, index) in upload.curr_data">
                        <v-list-item
                          :key="`fle` + index"
                          v-if="textFile.includes(item.ext)"
                          :href="item.new_name"
                          target="_blank"
                          :download="item.new_name"
                        >
                          <v-list-item-title>
                            {{ item.orig_name }}</v-list-item-title
                          >
                        </v-list-item>
                      </template>
                    </v-list>
                  </v-menu>
                </v-col>
                <!-- <div class="p" v-text="upload.curr_data"></div> -->
              </v-row>

              <v-container>
                <v-row>
                  <!-- <div
                    :class="
                      `images` + chapterIndex + outlineIndex + uploadIndex
                    "
                    v-viewer="options"
                  >
                    <content
                      v-for="(item, index) in upload.curr_data"
                      :key="`content` + index"
                      style="display: inline-block"
                      class="contentimage"
                    >
                      <img
                        :key="index"
                        :src="item.new_name"
                        :data-source="item.new_name"
                        :label="item.orig_name"
                        class="image"
                        @click="show(chapterIndex, outlineIndex, uploadIndex)"
                      />
                      <p :key="`label` + index">{{ item.orig_name }}</p>
                    </content>
                  </div> -->

                  <template v-for="(item, index) in upload.curr_data">
                    <v-flex
                      sm6
                      xs12
                      md6
                      lg3
                      :key="`img` + index"
                      v-if="item.ext == `jpg` || item.ext == `png`"
                      class="d-flex child-flex"
                    >
                      <div
                        :key="`viewer` + index"
                        :class="
                          `images` +
                          chapterIndex +
                          outlineIndex +
                          uploadIndex +
                          index
                        "
                        v-viewer="options"
                        style="height: auto"
                      >
                        <img
                          :src="item.new_name"
                          :data-source="item.new_name"
                          class="image grey lighten-2"
                          @click="
                            show(chapterIndex, outlineIndex, uploadIndex, index)
                          "
                        />
                        <p>{{ item.orig_name }}</p>
                        <!-- <v-img
                          :src="item.new_name"
                          :data-source="item.new_name"
                          max-width="300"
                          class="grey lighten-2"
                          @click="show(chapterIndex, outlineIndex, index)"
                        >
                          <template v-slot:placeholder>
                            <v-row
                              class="fill-height ma-0"
                              align="center"
                              justify="center"
                            >
                              <v-progress-circular
                                indeterminate
                                color="grey lighten-5"
                              ></v-progress-circular>
                            </v-row>
                          </template>
                        </v-img> -->
                      </div>
                    </v-flex>
                  </template>
                </v-row>
              </v-container>
            </v-container>
            <!--START ATTACHMENT PER OUTLINE-->

            <!-- PRINT TEXTAREA OF OUTLINE PER CHAPTER-->

            <!--START LOOP OF TABLE PER OUTLINE-->
            <v-container
              v-for="(table, tableIndex) in outline.outlineTable"
              :key="tableIndex"
            >
              <br />
              <!--START SET UP OF TABLE WITH LOOP FOR HEADERS AND TABLE DATA-->
              <template
                v-if="table.tableName == '621' || table.tableName == '821'"
              >
                <v-data-table
                  :headers="table.tableHeader"
                  :items="table.tableData"
                  :hide-default-footer="true"
                  class="elevation-1 my_class2"
                  style="white-space: pre-line"
                  dense
                >
                  <!-- <template v-slot:item="i">
                  <tr>
                    <td :class="'red lighten-4'">{{ i.item.year }}</td>
                    <td :class="'red lighten-4'">
                      {{ i.item.projPopulation }}
                    </td>
                    <td :class="'red lighten-4'">{{ i.item.wasteGen }}</td>
                    <td :class="'orange lighten-4'">{{ i.item.wasteDiv }}</td>
                    <td :class="'orange darken-1'">
                      {{ i.item.wasteDivPer }}%
                    </td>
                    <td :class="'orange lighten-4'">{{ i.item.wasteDis }}</td>
                    <td :class="'orange darken-1'">
                      {{ i.item.wasteDisPer }}%
                    </td>
                    <td :class="'blue lighten-1'">{{ i.item.wacsbio }}</td>
                    <td :class="'blue darken-1'">{{ i.item.wacsbioPer }}%</td>
                    <td :class="'blue lighten-1'">{{ i.item.wacsrec }}</td>
                    <td :class="'blue darken-1'">{{ i.item.wacsrecPer }}%</td>
                    <td :class="'blue lighten-1'">{{ i.item.wacsrwpd }}</td>
                    <td :class="'blue darken-1'">{{ i.item.wacsrwpdPer }}%</td>
                    <td :class="'blue lighten-1'">{{ i.item.wacsdis }}</td>
                    <td :class="'blue darken-1'">{{ i.item.wacsdisPer }}%</td>
                    <td :class="'blue lighten-1'">{{ i.item.wacsspec }}</td>
                    <td :class="'blue darken-1'">{{ i.item.wacsspecPer }}%</td>
                  </tr>
                </template> -->
                  <!-- <template v-slot:[`item.wasteDivPer`]="{ item }">
                    <v-chip :class="'green lighten-4'">
                      {{ item.wasteDivPer }}
                    </v-chip>
                  </template>
                  <template v-slot:[`item.wasteDisPer`]="{ item }">
                    <v-chip :class="'green lighten-4'">
                      {{ item.wasteDisPer }}
                    </v-chip>
                  </template>
                  <template v-slot:[`item.wacsbioPer`]="{ item }">
                    <v-chip :class="'green lighten-4'">
                      {{ item.wacsbioPer }}
                    </v-chip>
                  </template>
                  <template v-slot:[`item.wacsrecPer`]="{ item }">
                    <v-chip :class="'green lighten-4'">
                      {{ item.wacsrecPer }}
                    </v-chip>
                  </template>
                  <template v-slot:[`item.wacsrwpdPer`]="{ item }">
                    <v-chip :class="'green lighten-4'">
                      {{ item.wacsrwpdPer }}
                    </v-chip>
                  </template>
                  <template v-slot:[`item.wacsdisPer`]="{ item }">
                    <v-chip :class="'green lighten-4'">
                      {{ item.wacsdisPer }}
                    </v-chip>
                  </template>
                  <template v-slot:[`item.wacsspecPer`]="{ item }">
                    <v-chip :class="'green lighten-4'">
                      {{ item.wacsspecPer }}
                    </v-chip>
                  </template> -->
                </v-data-table>
              </template>

              <template v-else>
                <v-data-table
                  v-if="!table.notShow"
                  dense
                  :headers="filterHeader(table.tableHeader)"
                  :items="table.tableData"
                  :items-per-page="-1"
                  :hide-default-footer="true"
                  class="elevation-1 my_class2"
                  style="white-space: pre-line"
                  loading="false"
                  loading-text="No Data"
                >
                  <!-- v-if="table.tableName == '231'" -->
                  <template v-slot:top>
                    <v-toolbar flat>
                      <v-toolbar-title class="text-subtitle-2">{{
                        table.tableTitle
                      }}</v-toolbar-title>
                      <v-divider class="mx-4" inset vertical></v-divider>
                      <v-spacer></v-spacer>
                    </v-toolbar>
                  </template>
                  <template slot="body.append" v-if="table.withTotal == 1">
                    <tr>
                      <td class="text-subtitle-2">Total</td>
                      <td
                        class="text-subtitle-2"
                        v-for="x in nonNullItems(
                          chapterIndex,
                          outlineIndex,
                          tableIndex
                        )"
                        :key="x"
                        :id="`${tableIndex}-${x}`"
                      >
                        {{
                          sumField(chapterIndex, outlineIndex, tableIndex, x)
                        }}
                      </td>
                    </tr>
                  </template>
                  <template slot="body.append" v-if="table.withTotalPer == 1">
                    <tr>
                      <td class="text-subtitle-2">Total %</td>
                      <td
                        class="text-subtitle-2"
                        v-for="x in nonNullItems(
                          chapterIndex,
                          outlineIndex,
                          tableIndex
                        )"
                        :key="x"
                        :id="`${tableIndex}-${x}`"
                      >
                        {{
                          sumFieldPer(chapterIndex, outlineIndex, tableIndex, x)
                        }}
                      </td>
                    </tr>
                  </template>

                  <!-- <template slot="body.append">
                <tr v-html="footerTotal()" class="pink--text"></tr>
              </template> -->
                </v-data-table>
              </template>
              <!--END SET UP OF TABLE WITH LOOP FOR HEADERS AND TABLE DATA-->
              <br />
            </v-container>
            <!--END LOOP OF TABLE PER OUTLINE-->
          </v-container>
        </v-card>
      </v-tab-item>
    </v-tabs-items>
  </v-container>
</template>
<style lang="scss" rel="stylesheet/scss" scoped>
.image {
  width: calc(20% - 10px);
  cursor: pointer;
  margin: 5px;
  display: inline-block;
  width: 200px;
  height: 200px;
}
</style>
<script>
import { mapGetters, mapActions } from "vuex";
import "viewerjs/dist/viewer.css";
import { directive as viewer } from "v-viewer";
let basePath = window.location.origin;
export default {
  props: ["header_id", "typ_year"],
  directives: {
    viewer: viewer({
      debug: true,
    }),
  },
  data: () => ({
    name: "TypViewModal",
    chapters: [],
    tab: null,
    reference_number: "",
    textFile: ["pdf", "docx", "pptx"],
    imageFile: ["jpg", "png"],
    options: {
      toolbar: false,
      url: "data-source",
    },
  }),
  created() {
    this.chapters = JSON.parse(JSON.stringify(this.allOutline));
    this.pushProjectedYear(this.typ_year);
    this.fetchchapters(this.header_id);
  },
  methods: {
    ...mapActions(["fetchAllPerChapter", "fetchAttachAllPerChapter"]),
    show(chapterIndex, outlineIndex, uploadIndex, index) {
      const viewer = this.$el.querySelector(
        ".images" + chapterIndex + outlineIndex + uploadIndex + index
      ).$viewer;
      viewer.show();
    },
    pushProjectedYear(event) {
      this.projected_year = [];
      if (this.chapters[1].outline[2].outlineTable[1].tableHeader.length > 3) {
        this.chapters[1].outline[2].outlineTable[1].tableHeader.splice(1, 10);
      }

      let populationFrom232 = "";
      for (let i = 0; i < 10; i++) {
        this.projected_year.push(event);

        //For table and edit form 232
        this.chapters[1].outline[2].outlineTable[1].tableHeader.splice(
          i + 2,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
            divider: true,
            total: "column" + i,
          }
        );

        //For table 432
        this.chapters[3].outline[2].outlineTable[1].tableData.push({
          year: event,
          wasteGeneration: "",
          totalKg: "",
          totalTon: "",
          totalCubic: "",
        });

        //For table 621
        //populationFrom232 = document.getElementById("1-column" + i);
        // var text = populationFrom232.innerText || populationFrom232.textContent;
        // element.innerHTML = text;
        this.chapters[5].outline[1].outlineTable[0].tableData.push({
          year: event,
          projPopulation: "",
          wacsbio: "",
          wacsbioPer: "",
          wacsdis: "",
          wacsdisPer: "",
          wacsrec: "",
          wacsrecPer: "",
          wacsrwpd: "",
          wacsrwpdPer: "",
          wacsspec: "",
          wacsspecPer: "",
          wasteDis: "",
          wasteDisPer: "",
          wasteDiv: "",
          wasteDivPer: "",
          wasteGen: "",
        });

        //For table 761
        this.chapters[6].outline[17].outlineTable[0].tableData.push({
          year: event,
          projectedDis: "",
          capacityRes: "",
        });

        //For table 771
        this.chapters[6].outline[21].outlineTable[0].tableData.push({
          year: event,
          estimatedQty: 0,
          collection: "",
          storageTreatment: "",
        });

        //For table 7711
        this.chapters[6].outline[22].outlineTable[0].tableData.push({
          year: event,
          estimatedQty: "",
          collection: "",
          storageTreatment: "",
        });

        //For table and edit form 1111
        this.chapters[10].outline[0].outlineTable[0].tableHeader.splice(
          i + 1,
          0,
          {
            text: event,
            value: "column" + i,
            total: "column" + i,
            sortable: false,
            divider: true,
          }
        );
        //For table and edit form 1121
        this.chapters[10].outline[1].outlineTable[0].tableHeader.splice(
          i + 1,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
            total: "column" + i,
            divider: true,
          }
        );

        //For table and edit form 1122
        this.chapters[10].outline[1].outlineTable[1].tableHeader.splice(
          i + 1,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
            total: "column" + i,
            divider: true,
          }
        );

        //For table and edit form 1131
        this.chapters[10].outline[2].outlineTable[0].tableHeader.splice(
          i + 1,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
            total: "column" + i,
            divider: true,
          }
        );
        //For table and edit form 1141
        this.chapters[10].outline[3].outlineTable[0].tableHeader.splice(
          i + 1,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
            total: "column" + i,
            divider: true,
          }
        );

        //For table and edit form 1151
        this.chapters[10].outline[4].outlineTable[0].tableHeader.splice(
          i + 1,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
            total: "column" + i,
            divider: true,
          }
        );

        //For table and edit form 1231
        this.chapters[11].outline[2].outlineTable[0].tableHeader.splice(
          i + 3,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
          }
        );

        //For table and edit form 821
        this.chapters[7].outline[1].outlineTable[0].tableData.push({
          year: event,
          projPopulation: "",
          wacsbio: "",
          wacsbioPer: "",
          wacsdis: "",
          wacsdisPer: "",
          wacsrec: "",
          wacsrecPer: "",
          wacsrwpd: "",
          wacsrwpdPer: "",
          wacsspec: "",
          wacsspecPer: "",
          wasteDis: "",
          wasteDisPer: "",
          wasteDiv: "",
          wasteDivPer: "",
          wasteGen: "",
        });

        event++;
      }
    },
    fetchchapters(header_id) {
      this.fetchAllPerChapter({
        header_id: header_id,
      }).then(() => {
        this.reference_number = this.allChapter[0]["reference_number"];
        for (var i = 1; i <= 13; i++) {
          this.setChapterDetails(i);
        }
        this.fetchAttachement(header_id);
      });
    },

    fetchAttachement(header_id) {
      this.fetchAttachAllPerChapter({
        header_id: header_id,
      }).then(() => {
        this.setAttachment();
      });
    },

    filterHeader(headers) {
      var newArrObj = headers.filter(function (obj) {
        return obj.text !== "Actions";
      });
      return newArrObj;
    },
    nonNullItems(chapterIndex, outlineIndex, tableIndex) {
      let x = this.chapters[chapterIndex].outline[outlineIndex].outlineTable[
        tableIndex
      ].tableHeader
        .filter((e) => {
          return e.total;
        })
        .map((e) => {
          return e.total;
        });
      return x;
    },
    sumField(chapterBase, outline, outlineTable, key) {
      // sum data in give key (property)
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      // if (!isValid) {
      //   let value = this.chapters[chapterBase].outline[outline].outlineTable[
      //     outlineTable
      //   ].tableData.reduce(
      //     (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
      //     0
      //   );

      //   return this.numberWithCommas(value.toFixed(2));
      // }

      if (!isValid) {
        let value = this.chapters[chapterBase].outline[outline].outlineTable[
          outlineTable
        ].tableData.reduce(
          (previousValue, currentValue) =>
            parseFloat(previousValue) +
            (parseFloat(currentValue[key].replace(/,/g, "")) || 0),
          0
        );
        if (chapterBase == 10 && outline == 0 && outlineTable == 0) {
        }

        return this.numberWithCommas(value.toFixed(2));
      }
    },

    sumFieldPer(chapterBase, outline, outlineTable, key) {
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid && key != "totalKg") {
        let value = this.chapters[chapterBase].outline[outline].outlineTable[
          outlineTable
        ].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        let total = this.chapters[chapterBase].outline[outline].outlineTable[
          outlineTable
        ].tableData.reduce(
          (a, b) =>
            parseFloat(a) + (parseFloat(b["totalKg"].replace(/,/g, "")) || 0),
          0
        );
        let per = (value / total) * 100;

        return this.numberWithCommas(per.toFixed(2)) + "%";
      }
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return parts.join(".");
    },
    setChapterDetails(i) {
      let newAllChapter = [];
      newAllChapter = JSON.stringify(this.allChapter);
      if (newAllChapter.length >= 3) {
        var chapterBase = i - 1;

        if (i == 1) {
          let new_data = JSON.parse(newAllChapter)[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.chap1Intro;
          this.chapters[chapterBase].outline[1].outlineModel = new_data.purpose;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.approach;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.acknowledgement;
        }
        if (i == 2) {
          let new_data = JSON.parse(newAllChapter)[0];
          //let new_data = new_data1[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.location;
          this.chapters[chapterBase].outline[1].outlineModel = new_data.history;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.population;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.economic_profile_land_use;
          this.chapters[chapterBase].outline[4].outlineModel =
            new_data.physical_characteristic;
          this.chapters[chapterBase].outline[5].outlineModel =
            new_data.topography;
          this.chapters[chapterBase].outline[6].outlineModel = new_data.geology;
          this.chapters[chapterBase].outline[7].outlineModel = new_data.climate;

          // Checking and populatings For table 231
          if (new_data.current_population_barangay) {
            let curPop = JSON.parse(new_data.current_population_barangay);
            this.chapters[chapterBase].outline[2].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[2].outlineTable[0].tableData =
              curPop;
          }

          // Checking and populating For table 232
          if (new_data.ten_year_projected_p) {
            let tenYear = JSON.parse(new_data.ten_year_projected_p);
            this.chapters[chapterBase].outline[2].outlineTable[1].tableData =
              [];
            this.chapters[chapterBase].outline[2].outlineTable[1].tableData =
              tenYear;
          }

          // Checking and populating  For table 241
          if (new_data.ten_year_projected_p) {
            let listInd = JSON.parse(new_data.list_industries_within_city);
            this.chapters[chapterBase].outline[3].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[0].tableData =
              listInd;
          }

          // Checking and populating  For table 242
          if (new_data.land_used_map_urban) {
            let mapUrban = JSON.parse(new_data.land_used_map_urban);
            this.chapters[chapterBase].outline[3].outlineTable[1].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[1].tableData =
              mapUrban;
          }

          // Checking and populating  For table 243
          if (new_data.land_used_map_urban) {
            let mapGen = JSON.parse(new_data.land_used_map_general);
            this.chapters[chapterBase].outline[3].outlineTable[2].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[2].tableData =
              mapGen;
          }
        }
        if (i == 3) {
          let new_data = JSON.parse(newAllChapter)[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.inistitutional_arrangement;
          this.chapters[chapterBase].outline[1].outlineModel =
            new_data.inventory_equipment_staff;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.source_reduction;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.collection;
          this.chapters[chapterBase].outline[4].outlineModel =
            new_data.transfer;
          this.chapters[chapterBase].outline[5].outlineModel =
            new_data.processing_facilities;
          this.chapters[chapterBase].outline[6].outlineModel =
            new_data.final_disposal;
          this.chapters[chapterBase].outline[7].outlineModel =
            new_data.special_wastes;
          this.chapters[chapterBase].outline[8].outlineModel =
            new_data.health_care_wastes;
