                    </v-card-actions>
                  </v-stepper-content>

                  <v-stepper-content step="6">
                    <div class="text-h5">Presence of Policy Support</div>
                    <!-- ROW 1 -->
                    <v-row>
                      <v-col>
                        <v-radio-group
                          v-model="indicator[5].radio1"
                          :readonly="setReadOnly()"
                          row
                        >
                          <v-radio label="Yes" value="0"></v-radio>
                          <v-radio label="No" value="1"></v-radio>
                        </v-radio-group>
                        <v-textarea
                          auto-grow
                          label="Remarks"
                          :value="indicator[5].remarks1"
                          @change="(v) => (indicator[5].remarks1 = v)"
                          outlined
                          :readonly="setReadOnly()"
                        >
                        </v-textarea>
                      </v-col>
                      <v-divider v-if="value.length != 0" vertical></v-divider>
                      <v-col v-if="value.length != 0">
                        <div class="text-body-2">
                          Source of fund is indicated
                        </div>
                        <v-radio-group
                          v-model="value.indicator_com_6_1"
                          row
                          class="ml-5"
                          :readonly="true"
                        >
                          <v-radio label="Yes" value="0"></v-radio>
                          <v-radio label="No" value="1"></v-radio>
                        </v-radio-group>
                        <p>
                          {{ value.indicator_rem_6_1 }}
                        </p>
                      </v-col>
                    </v-row>
                    <hr />
                    <!-- ROW 1 -->
                    <v-card-actions>
                      <v-btn @click="e1 = 5">Back </v-btn>
                      <v-spacer></v-spacer>
                      <v-btn color="primary" @click="next()">Next</v-btn>
                    </v-card-actions>
                  </v-stepper-content>

                  <v-stepper-content step="7">
                    <div class="text-h5">
                      Presence of institutional/organizational arrangements
                    </div>
                    <!-- ROW 1 -->
                    <v-row>
                      <v-col>
                        <v-radio-group
                          v-model="indicator[6].radio1"
                          :readonly="setReadOnly()"
                          row
                        >
                          <v-radio label="Yes" value="0"></v-radio>
                          <v-radio label="No" value="1"></v-radio>
                        </v-radio-group>
                        <v-textarea
                          auto-grow
                          label="Remarks"
                          :value="indicator[6].remarks1"
                          @change="(v) => (indicator[6].remarks1 = v)"
                          outlined
                          :readonly="setReadOnly()"
                        >
                        </v-textarea>
                      </v-col>
                      <v-divider v-if="value.length != 0" vertical></v-divider>
                      <v-col v-if="value.length != 0">
                        <div class="text-body-2">
                          Source of fund is indicated
                        </div>
                        <v-radio-group
                          v-model="value.indicator_com_7_1"
                          row
                          class="ml-5"
                          :readonly="true"
                        >
                          <v-radio label="Yes" value="0"></v-radio>
                          <v-radio label="No" value="1"></v-radio>
                        </v-radio-group>
                        <p>
                          {{ value.indicator_rem_7_1 }}
                        </p>
                      </v-col>
                    </v-row>
                    <hr />
                    <!-- ROW 1 -->
                    <v-card-actions>
                      <v-btn @click="e1 = 6">Back </v-btn>
                      <v-spacer></v-spacer>
                      <v-btn
                        v-if="formAction == 1"
                        color="primary"
                        @click="save()"
                        >Save</v-btn
                      >
                    </v-card-actions>
                  </v-stepper-content>
                </v-stepper-items>
              </v-card>
            </v-flex>
          </v-layout>
          <!-- END OF STEPPER CONTENT-->
        </v-stepper>
      </v-col>
      <!-- </draggable> -->
    </v-row>

    <!-- View PLAN -->
    <template>
      <v-dialog v-model="dialogAdd" max-width="1300px" :retain-focus="false"
        ><v-card>
          <v-card-title>
            <span class="headline"></span>
          </v-card-title>
          <v-card-text>
            <template v-if="dialogAdd">
              <TypViewModal :header_id="typ_header_id" :typ_year="typ_year" />
            </template>
          </v-card-text>
          <v-card-actions class="justify-end">
            <v-btn text @click="dialogAdd = false">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </template>

    <!-- Snackbar -->
    <template>
      <v-snackbar
        v-model="snackbar"
        top
        right
        :timeout="timeout"
        color="success"
        elevation="24"
      >
        {{ text }}
      </v-snackbar>
    </template>

    <!-- PRINT -->
    <template class="d-none"> </template>

    <!-- View Region evaluation -->
    <template>
      <v-dialog v-model="dialogPrint" max-width="1300px" :retain-focus="false"
        ><v-card>
          <v-card-title>
            <span class="headline"></span>
          </v-card-title>
          <v-card-text>
            <template v-if="dialogPrint">
              <Form2Modal :header_id="typ_header_id" @update="onChildUpdate" />
            </template>
          </v-card-text>
          <v-card-actions class="justify-end">
            <v-btn text @click="dialogPrint = false">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </template>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import TypViewModal from "../Include/typ_view_modal";
import Form2Modal from "../Include/form2_view";
import draggable from "vuedraggable";
//let basePath = "http://localhost/";
export default {
  components: {
    TypViewModal,
    Form2Modal,
    draggable,
  },

  // props: ["chapters"],
  data() {
    return {
      e1: 1,
      name: "FormTwoCom",
      indicator: [
        {
          radio1: null,
          radio2: null,
          radio3: null,
          radio4: null,
          radio5: null,
          radio6: null,
          radio7: null,
          remarks1: null,
          remarks2: null,
          remarks3: null,
          remarks4: null,
          capacity: null,
          location: null,
          fdLocation: null,
          lifeSpan: null,
          fdStatus: null,
        },
        {
          radio1: null,
          remarks1: null,
        },
        {
          radio1: null,
          radio2: null,
          remarks1: null,
        },
        {
          radio1: null,
          remarks1: null,
        },
        {
          radio1: null,
          radio2: null,
          remarks1: null,
          remarks2: null,
        },
        {
          radio1: null,
          remarks1: null,
        },
        {
          radio1: null,
          remarks1: null,
        },
      ],
      dialogAdd: false,
      dialogPrint: false,
      access: 0,
      eval: 1,
      snackbar: false,
      text: "Copied.",
      timeout: 2000,
      formAction: 0,
      typ_header_id: 0,
      typ_year: 0,
      value: [],
    };
  },
  created() {
    this.formAction = localStorage.getItem("formAction");
    this.typ_header_id = localStorage.getItem("typ_header_id");
    // this.formAction = sessionStorage.getItem("formAction");
    // this.typ_header_id = sessionStorage.getItem("typ_header_id");
    this.typ_year = localStorage.getItem("year");
    if (this.getStateSession["access"] == 4) {
      this.eval = 0;
    }
    this.access = this.getStateSession["access"];

    if (localStorage.getItem("form_id") != "null") {
      this.fetchForm2({
        id: localStorage.getItem("form_id"),
      }).then(() => {
        this.setForm2Details();
      });
    }
  },
  methods: {
    ...mapActions(["fetchForm2", "createForm2", "fetchRegForm2"]),
    next() {
      this.e1++;
    },
    save() {
      this.createForm2({
        header_id: parseInt(localStorage.getItem("typ_header_id")),
        indicator_com_1_1: this.indicator[0].radio1,
        indicator_rem_1_1: this.indicator[0].remarks1,
        indicator_com_1_2: this.indicator[0].radio2,
        indicator_rem_1_2: this.indicator[0].remarks2,
        indicator_com_1_3: this.indicator[0].radio3,
        indicator_rem_1_3: this.indicator[0].remarks3,
        indicator_com_1_4: this.indicator[0].radio6,
        indicator_rem_1_4: this.indicator[0].remarks4,
        memo_agreement: this.indicator[0].radio4,
        contract: this.indicator[0].radio5,
        disposal_area: this.indicator[0].radio7,
        capacity: this.indicator[0].capacity,
        location: this.indicator[0].location,
        fdLocation: this.indicator[0].fdLocation,
        lifeSpan: this.indicator[0].lifeSpan,
        fdStatus: this.indicator[0].fdStatus,

        indicator_com_2_1: this.indicator[1].radio1,
        indicator_rem_2_1: this.indicator[1].remarks1,

        indicator_com_3_1: this.indicator[2].radio1,
        indicator_com_3_2: this.indicator[2].radio2,
        indicator_rem_3_1: this.indicator[2].remarks1,

        indicator_com_4_1: this.indicator[3].radio1,
        indicator_rem_4_1: this.indicator[3].remarks1,

        indicator_com_5_1: this.indicator[4].radio1,
        indicator_rem_5_1: this.indicator[4].remarks1,
        indicator_com_5_2: this.indicator[4].radio2,
        indicator_rem_5_2: this.indicator[4].remarks2,

        indicator_com_6_1: this.indicator[5].radio1,
        indicator_rem_6_1: this.indicator[5].remarks1,

        indicator_com_7_1: this.indicator[6].radio1,
        indicator_rem_7_1: this.indicator[6].remarks1,
        date: this.getNow(),
        eval: this.eval,

        user_id: this.getStateSession["id"],
        access: this.getStateSession["access"],
      });
    },

    setForm2Details() {
      let newAllChapter = JSON.stringify(this.allForm2);
      let new_data1 = JSON.parse(newAllChapter);

      if (new_data1 != null) {
        let new_data = new_data1[0];
        this.indicator[0].radio1 = new_data.indicator_com_1_1;
        this.indicator[0].radio2 = new_data.indicator_com_1_2;
        this.indicator[0].radio3 = new_data.indicator_com_1_3;
        this.indicator[0].radio4 = new_data.memo_agreement;
        this.indicator[0].radio5 = new_data.contract;
        this.indicator[0].radio6 = new_data.indicator_com_1_4;
        this.indicator[0].radio7 = new_data.disposal_area;
        this.indicator[0].remarks1 = new_data.indicator_rem_1_1;
        this.indicator[0].remarks2 = new_data.indicator_rem_1_2;
        this.indicator[0].remarks3 = new_data.indicator_rem_1_3;
        this.indicator[0].remarks4 = new_data.indicator_rem_1_4;

        this.indicator[0].capacity = new_data.capacity;
        this.indicator[0].location = new_data.location;
        this.indicator[0].fdLocation = new_data.fdLocation;
        this.indicator[0].lifeSpan = new_data.lifeSpan;
        this.indicator[0].fdStatus = new_data.fdStatus;

        this.indicator[1].radio1 = new_data.indicator_com_2_1;
        this.indicator[1].remarks1 = new_data.indicator_rem_2_1;

        this.indicator[2].radio1 = new_data.indicator_com_3_1;
        this.indicator[2].radio2 = new_data.indicator_com_3_1;
        this.indicator[2].remarks1 = new_data.indicator_rem_3_1;

        this.indicator[3].radio1 = new_data.indicator_com_4_1;
        this.indicator[3].remarks1 = new_data.indicator_rem_4_1;

        this.indicator[4].radio1 = new_data.indicator_com_5_1;
        this.indicator[4].radio2 = new_data.indicator_com_5_2;
        this.indicator[4].remarks1 = new_data.indicator_rem_5_1;
        this.indicator[4].remarks2 = new_data.indicator_rem_5_2;

        this.indicator[5].radio1 = new_data.indicator_com_6_1;
        this.indicator[5].remarks1 = new_data.indicator_rem_6_1;

        this.indicator[6].radio1 = new_data.indicator_com_7_1;
        this.indicator[6].remarks1 = new_data.indicator_rem_7_1;
      }
    },

    getNow() {
      const today = new Date();
      const date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + " " + time;
      return dateTime;
    },
    getRegEvaluation() {
      this.dialogPrint = true;
    },
    print() {
      this.$htmlToPaper("printMe");
    },
    setReadOnly() {
      if (localStorage.getItem("formAction") == "1") {
        return false;
      } else {
        return true;
      }
    },
    onChildUpdate(newValue) {
      if (newValue != null) {
        this.value = newValue[0];
      }
    },
  },
  computed: {
    ...mapGetters(["allForm2", "getStateSession"]),
  },
  watch: {},
};
</script>
