              </tr>
              <!-- 5 -->
              <tr>
                <td>
                  <b>5. Expenditures/Investment</b>
                </td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <!-- 5.1 -->
              <tr>
                <td>
                  <p>
                    Presence of appropriate projected investment costs for<br />
