<script>
import { mapGetters, mapActions } from "vuex";

export default {
  props: ["chapters"],
  data() {
    return {
      e1: 1,
      name: "FormTwoCom",
      indicator: [
        {
          radio1: null,
          radio2: null,
          radio3: null,
          radio4: null,
          radio5: null,
          radio6: null,
          radio7: null,
          remarks1: null,
          remarks2: null,
          remarks3: null,
          remarks4: null,
          capacity: null,
          location: null,
          lifeSpan: null,
          fdLocation: null,
          fdStatus: null,
        },
        {
          radio1: null,
          remarks1: null,
        },
        {
          radio1: null,
          radio2: null,
          remarks1: null,
        },
        {
          radio1: null,
          remarks1: null,
        },
        {
          radio1: null,
          radio2: null,
          remarks1: null,
          remarks2: null,
        },
        {
          radio1: null,
          remarks1: null,
        },
        {
          radio1: null,
          remarks1: null,
        },
      ],
      dialogAdd: false,
      user_type: 0,
      eval: 1,
      snackbar: false,
      text: "Copied.",
      timeout: 2000,
    };
  },
  created() {
    if (sessionStorage.getItem("access") == 4) {
      this.eval = 0;
    }
    this.user_type = sessionStorage.getItem("access");
    // this.pushProjectedYear(sessionStorage.getItem("year"));
    if (sessionStorage.getItem("form_id") != "null") {
      this.fetchForm2({
        id: sessionStorage.getItem("form_id"),
      }).then(() => {
        this.setForm2Details();
      });
    }
    // this.fetchchapters(sessionStorage.getItem("typ_header_id"));
    // this.fetchAttachement(sessionStorage.getItem("typ_header_id"));
  },
  methods: {
    ...mapActions([
      "fetchForm2",
      "createForm2",
      "fetchRegForm2",
      "fetchAllPerChapter",
      "fetchAttachAllPerChapter",
    ]),
    generateReport() {
      this.$refs.html2Pdf.generatePdf();
    },
    next() {
      this.e1++;
    },
    save() {
      this.createForm2({
        header_id: parseInt(sessionStorage.getItem("typ_header_id")),
        indicator_com_1_1: this.indicator[0].radio1,
        indicator_rem_1_1: this.indicator[0].remarks1,
        indicator_com_1_2: this.indicator[0].radio2,
        indicator_rem_1_2: this.indicator[0].remarks2,
        indicator_com_1_3: this.indicator[0].radio3,
        indicator_rem_1_3: this.indicator[0].remarks3,
        indicator_com_1_4: this.indicator[0].radio6,
        indicator_rem_1_4: this.indicator[0].remarks4,
        memo_agreement: this.indicator[0].radio4,
        contract: this.indicator[0].radio5,
        disposal_area: this.indicator[0].radio7,
        capacity: this.indicator[0].capacity,
        location: this.indicator[0].location,
        fdLocation: this.indicator[0].fdLocation,
        lifeSpan: this.indicator[0].lifeSpan,
        fdStatus: this.indicator[0].fdStatus,

        indicator_com_2_1: this.indicator[1].radio1,
        indicator_rem_2_1: this.indicator[1].remarks1,

        indicator_com_3_1: this.indicator[2].radio1,
        indicator_com_3_2: this.indicator[2].radio2,
        indicator_rem_3_1: this.indicator[2].remarks1,

        indicator_com_4_1: this.indicator[3].radio1,
        indicator_rem_4_1: this.indicator[3].remarks1,

        indicator_com_5_1: this.indicator[4].radio1,
        indicator_rem_5_1: this.indicator[4].remarks1,
        indicator_com_5_2: this.indicator[4].radio2,
        indicator_rem_5_2: this.indicator[4].remarks2,

        indicator_com_6_1: this.indicator[5].radio1,
        indicator_rem_6_1: this.indicator[5].remarks1,

        indicator_com_7_1: this.indicator[6].radio1,
        indicator_rem_7_1: this.indicator[6].remarks1,
        date: this.getNow(),
        eval: this.eval,
      });
    },

    setForm2Details() {
      let newAllChapter = JSON.stringify(this.allForm2);
      let new_data = JSON.parse(newAllChapter);
      if (new_data != null) {
        this.indicator[0].radio1 = new_data.indicator_com_1_1;
        this.indicator[0].radio2 = new_data.indicator_com_1_2;
        this.indicator[0].radio3 = new_data.indicator_com_1_3;
        this.indicator[0].radio4 = new_data.memo_agreement;
        this.indicator[0].radio5 = new_data.contract;
        this.indicator[0].radio6 = new_data.indicator_com_1_4;
        this.indicator[0].radio7 = new_data.disposal_area;
        this.indicator[0].remarks1 = new_data.indicator_rem_1_1;
        this.indicator[0].remarks2 = new_data.indicator_rem_1_2;
        this.indicator[0].remarks3 = new_data.indicator_rem_1_3;
        this.indicator[0].remarks4 = new_data.indicator_rem_1_4;

        this.indicator[0].capacity = new_data.capacity;
        this.indicator[0].location = new_data.location;
        this.indicator[0].fdLocation = new_data.fdLocation;
        this.indicator[0].lifeSpan = new_data.lifeSpan;
        this.indicator[0].fdStatus = new_data.fdStatus;

        this.indicator[1].radio1 = new_data.indicator_com_2_1;
        this.indicator[1].remarks1 = new_data.indicator_rem_2_1;

        this.indicator[2].radio1 = new_data.indicator_com_3_1;
        this.indicator[2].radio2 = new_data.indicator_com_3_1;
        this.indicator[2].remarks1 = new_data.indicator_rem_3_1;

        this.indicator[3].radio1 = new_data.indicator_com_4_1;
        this.indicator[3].remarks1 = new_data.indicator_rem_4_1;

        this.indicator[4].radio1 = new_data.indicator_com_5_1;
        this.indicator[4].radio2 = new_data.indicator_com_5_2;
        this.indicator[4].remarks1 = new_data.indicator_rem_5_1;
        this.indicator[4].remarks2 = new_data.indicator_rem_5_2;

        this.indicator[5].radio1 = new_data.indicator_com_6_1;
        this.indicator[5].remarks1 = new_data.indicator_rem_6_1;

        this.indicator[6].radio1 = new_data.indicator_com_7_1;
        this.indicator[6].remarks1 = new_data.indicator_rem_7_1;
      }
    },

    filterHeader(headers) {
      var newArrObj = headers.filter(function (obj) {
        return obj.text !== "Actions";
      });
      return newArrObj;
    },
    nonNullItems(chapterIndex, outlineIndex, tableIndex) {
      let x = this.chapters[chapterIndex].outline[outlineIndex].outlineTable[
        tableIndex
      ].tableHeader
        .filter((e) => {
          return e.total;
        })
        .map((e) => {
          return e.total;
        });
      return x;
    },
    sumField(chapterBase, outline, outlineTable, key) {
      // sum data in give key (property)
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid) {
        let value = this.chapters[chapterBase].outline[outline].outlineTable[
          outlineTable
        ].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        return this.numberWithCommas(value.toFixed(2));
      }
    },

    sumFieldPer(chapterBase, outline, outlineTable, key) {
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid && key != "totalKg") {
        let value = this.chapters[chapterBase].outline[outline].outlineTable[
          outlineTable
        ].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        let total = this.chapters[chapterBase].outline[outline].outlineTable[
          outlineTable
        ].tableData.reduce(
          (a, b) =>
            parseFloat(a) + (parseFloat(b["totalKg"].replace(/,/g, "")) || 0),
          0
        );
        let per = (value / total) * 100;

        return this.numberWithCommas(per.toFixed(2)) + "%";
      }
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return parts.join(".");
    },
    setChapterDetails(i) {
      let newAllChapter = [];
      newAllChapter = JSON.stringify(this.allChapter);
      if (newAllChapter.length >= 3) {
        var chapterBase = i - 1;

        if (i == 1) {
          let new_data = JSON.parse(newAllChapter)[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.introduction;
          this.chapters[chapterBase].outline[1].outlineModel = new_data.purpose;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.approach;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.acknowledgement;
        }
        if (i == 2) {
          let new_data = JSON.parse(newAllChapter)[0];
          //let new_data = new_data1[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.location;
          this.chapters[chapterBase].outline[1].outlineModel = new_data.history;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.population;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.economic_profile_land_use;
          this.chapters[chapterBase].outline[4].outlineModel =
            new_data.physical_characteristic;

          // Checking and populatings For table 231
          if (new_data.current_population_barangay) {
            let curPop = JSON.parse(new_data.current_population_barangay);
            this.chapters[chapterBase].outline[2].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[2].outlineTable[0].tableData =
              curPop;
          }

          // Checking and populating For table 232
          if (new_data.ten_year_projected_p) {
            let tenYear = JSON.parse(new_data.ten_year_projected_p);
            this.chapters[chapterBase].outline[2].outlineTable[1].tableData =
              [];
            this.chapters[chapterBase].outline[2].outlineTable[1].tableData =
              tenYear;
          }

          // Checking and populating  For table 241
          if (new_data.ten_year_projected_p) {
            let listInd = JSON.parse(new_data.list_industries_within_city);
            this.chapters[chapterBase].outline[3].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[0].tableData =
              listInd;
          }

          // Checking and populating  For table 242
          if (new_data.land_used_map_urban) {
            let mapUrban = JSON.parse(new_data.land_used_map_urban);
            this.chapters[chapterBase].outline[3].outlineTable[1].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[1].tableData =
              mapUrban;
          }

          // Checking and populating  For table 243
          if (new_data.land_used_map_urban) {
            let mapGen = JSON.parse(new_data.land_used_map_general);
            this.chapters[chapterBase].outline[3].outlineTable[2].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[2].tableData =
              mapGen;
          }
        }
        if (i == 3) {
          let new_data = JSON.parse(newAllChapter)[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.inistitutional_arrangement;
          this.chapters[chapterBase].outline[1].outlineModel =
            new_data.inventory_equipment_staff;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.source_reduction;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.collection;
          this.chapters[chapterBase].outline[4].outlineModel =
            new_data.transfer;
          this.chapters[chapterBase].outline[5].outlineModel =
            new_data.processing_facilities;
          this.chapters[chapterBase].outline[6].outlineModel =
            new_data.final_disposal;
          this.chapters[chapterBase].outline[7].outlineModel =
            new_data.special_wastes;
          this.chapters[chapterBase].outline[8].outlineModel =
            new_data.health_care_wastes;
          this.chapters[chapterBase].outline[9].outlineModel =
            new_data.market_for_recyclables;
          this.chapters[chapterBase].outline[10].outlineModel = new_data.iec;
