    setAttachment() {
      let newAllAttach = JSON.stringify(this.allAttach);
      let new_data = JSON.parse(newAllAttach);
      //var chapterBase = chapter - 1;
      if (new_data) {
        let count = new_data.length;

        for (let i = 0; i < count; i++) {
          let [b] = [new_data[i]].map((e, i) => ({
            ...e,
            file_path_name: JSON.parse(e.file_path_name).map((e, i) => ({
              ...e,
              new_name: `http://localhost/swms_api/uploads/${e.new_name}`,
              ext: e.new_name.split(".").pop().toLowerCase(),
              //new_name: `http://swm.emb.gov.ph/swms_api/uploads/${e.new_name}`,
            })),
          }));
          this.chapters[new_data[i].chapter - 1].outline[
            new_data[i].outline
          ].uploads[0].curr_data = b.file_path_name;
        }
      }
    },
    pushProjectedYear(event) {
      this.projected_year = [];
      if (this.chapters[1].outline[2].outlineTable[1].tableHeader.length > 3) {
        this.chapters[1].outline[2].outlineTable[1].tableHeader.splice(1, 10);
      }

      let populationFrom232 = "";
      for (let i = 0; i < 10; i++) {
        this.projected_year.push(event);

        //For table and edit form 232
        this.chapters[1].outline[2].outlineTable[1].tableHeader.splice(
          i + 2,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
            divider: true,
            total: "column" + i,
          }
        );

        //For table 432
        this.chapters[3].outline[2].outlineTable[1].tableData.push({
          year: event,
          wasteGeneration: "",
          totalKg: "",
          totalTon: "",
          totalCubic: "",
        });

