        }
      });
    },

    fetchAttachement(header_id, chapter) {
      this.fetchAttachAllPerChapter({
        header_id: header_id,
      }).then(() => {
        this.setAttachment();
      });
    },

    getNow() {
      const today = new Date();
      const date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + " " + time;
      return dateTime;
    },
    getRegEvaluation() {
      this.fetchRegForm2({
        id: sessionStorage.getItem("typ_header_id"),
      }).then(() => {
        this.setForm2Details();
        this.snackbar = true;
      });
    },
    print() {
      // Pass the element id here
      this.$htmlToPaper("printMe");
    },
  },
  computed: {
    ...mapGetters(["allForm2", "allChapter", "allAttach"]),
  },
  watch: {},
};
</script>
