<template>
  <v-container>
    <!-- <v-toolbar color="cyan" dark>
      <v-toolbar-title>Form Two Evaluation</v-toolbar-title>
    </v-toolbar> -->
    <v-card class="mx-auto card-padding">
      <!-- Process modal -->
      <template>
        <v-dialog
          v-model="dialogAdd"
          persistent
          max-width="900px"
          :retain-focus="false"
        >
          <template v-slot:activator="{}">
            <v-row>
              <v-col>
                <v-row>
                  <v-col>
                    <v-text-field
                      dense
                      label="Search Bar"
                      v-model="Search"
                      @keyup="getSearch"
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-combobox
                      dense
                      v-model="limitRow"
                      label="Show"
                      :items="allLimit"
                      @change="limitFunction"
                    >
                    </v-combobox>
                  </v-col>
                </v-row>
                <!-- <v-btn color="primary" dark v-bind="attrs" v-on="on">
                  Add New
                </v-btn> -->
              </v-col>
              <v-col>
                <!-- <v-btn @click="getSearch">Search</v-btn> -->
                <v-btn
                  small
                  class="float-right mini-margin-left"
                  @click="nextFunction"
                  >Next</v-btn
                >
                <v-btn small class="float-right" @click="prevFunction"
                  >Prev</v-btn
                >
                <input v-model="tgetCurrentrow" type="hidden" />
              </v-col>
            </v-row>
          </template>
          <v-card>
            <v-card-title>
              <!-- <span class="headline">{{ formTitle }}</span> -->
            </v-card-title>
            <v-card-text>
              <!-- <small>
                <span class="blue--text"
                  >*Please fill-in the required fields before processing.</span
                ></small
              > -->
              <v-form ref="form" v-model="valid" lazy-validation>
                <v-container>
                  <v-row>
                    <v-col>
                      <div>Approval</div>
                      <v-radio-group
                        v-model="evaluation_radio"
                        row
                        class="ml-5"
                      >
                        <v-radio label="Yes" value="0"></v-radio>
                        <v-radio label="No" value="1"></v-radio>
                      </v-radio-group>
                      <v-textarea
                        auto-grow
                        outlined
                        dense
                        label="Remarks"
                        v-model="remarks"
                        :rules="requiredField"
                        required
                      >
                      </v-textarea>
                      <v-btn
                        class="mt-2 mx-2"
                        small
                        color="primary"
                        @click="dialogViewPlan = true"
                        >10-Year Plan</v-btn
                      >
                      <v-menu offset-y dense>
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn
                            v-bind="attrs"
                            v-on="on"
                            class="mt-2 mx-2"
                            small
                            color="primary"
                          >
                            Central Evaluation
                          </v-btn>
                        </template>
                        <v-list dense class="py-0">
                          <v-list-item
                            dense
                            @click="
                              eval_type = 0;
                              dialogViewForm2 = true;
                            "
                          >
                            Form 2
                          </v-list-item>
                          <v-list-item
                            dense
                            @click="
                              eval_type = 0;
                              dialogViewForm3 = true;
                            "
                          >
                            Form 3</v-list-item
                          >
                        </v-list>
                      </v-menu>
                      <v-menu offset-y dense>
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn
                            v-bind="attrs"
                            v-on="on"
                            class="mt-2 mx-2"
                            small
                            color="primary"
                          >
                            Region Evaluation
                          </v-btn>
                        </template>
                        <v-list dense class="py-0">
                          <v-list-item
                            dense
                            @click="
                              eval_type = 1;
                              dialogViewForm2 = true;
                            "
                          >
                            Form 2
                          </v-list-item>
                          <v-list-item
                            dense
                            @click="
                              eval_type = 1;
                              dialogViewForm3 = true;
                            "
                          >
                            Form 3</v-list-item
                          >
                        </v-list>
                      </v-menu>
                      <!-- <v-row>
                        <v-col md="8">
                          <v-file-input
                            color="blue darken-2"
                            type="file"
                            ref="file"
                            placeholder="Resolution"
                            outlined
                            dense
                            :show-size="1000"
                            small-chips
                          >
                            <template v-slot:selection="{ text }">
                              <v-chip color="blue darken-2" dark label small>
                                {{ text }}
                              </v-chip>
                            </template>
                          </v-file-input>
                        </v-col>
                        <v-col md="4">
                          <v-menu offset-y>
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn
                                color="primary"
                                dark
                                v-bind="attrs"
                                v-on="on"
                                small
                              >
                                Current
                              </v-btn>
                            </template>
                          </v-menu>
                          <v-btn
                            small
                            color="indigo darken-1"
                            class="ml-2 white--text"
                            @click="uploadFilePerOl(chapterIndex, outlineIndex)"
                          >
                            Upload
                          </v-btn>
                        </v-col>
                      </v-row> -->
                    </v-col>
                  </v-row>
                </v-container>
              </v-form>

              <!-- <small>*indicates required field</small> -->
              <div class="text-h5 mb-4">Other Evaluator</div>
              <v-expansion-panels>
                <v-expansion-panel
                  v-for="(data, chapterIndex) in allothersEval"
                  :key="chapterIndex"
                >
                  <v-expansion-panel-header>
                    {{ data.name }}
                    <div class="text-subtitle-2" v-if="data.evaluator_app == 0">
                      <v-icon color="success" class="ml-4"
                        >mdi-checkbox-marked-circle</v-icon
                      >
                      Approved
                    </div>
                    <div class="text-subtitle-2" v-if="data.evaluator_app == 1">
                      <v-icon color="error" class="ml-4"
                        >mdi-close-circle</v-icon
                      >
                      Disapproved
                    </div>
                    <div
                      class="text-subtitle-2"
                      v-if="data.evaluator_app == null"
                    >
                      <v-icon color="blue-grey" class="ml-4"
                        >mdi-close-circle</v-icon
                      >
                      No Evaluation
                    </div>
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <v-divider></v-divider>
                    <div class="text-subtitle-2 mb-4">Comments:</div>
                    <p>
                      {{
                        data.evaluator_remarks == null
                          ? "None"
                          : data.evaluator_remarks
                      }}
                    </p>
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="
                  dialogAdd = false;
                  clearinput();
                "
                >Close</v-btn
              >
              <v-btn color="blue darken-1" text @click="confirmSubmit">{{
                buttonTitle
              }}</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- Submit Second modal -->
      <template>
        <v-dialog v-model="dialogSubmit" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <h2>Are you sure want to submit ?</h2>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogSubmit = false"
                >No</v-btn
              >
              <v-btn color="info darken-1" text @click="submitHeader"
                >Yes</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- View Form 2 Modal -->
      <template>
        <v-dialog
          v-model="dialogViewForm2"
          max-width="1300px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewForm2 == true && eval_type == 0">
                <Form2Modal :header_id="editedIndex" :form_id="form2_id" />
              </template>
              <template v-if="dialogViewForm2 == true && eval_type == 1">
                <Form2Modal :header_id="editedIndex" />
              </template>
            </v-card-text>
          </v-card>
        </v-dialog>
      </template>

      <!-- View Form 3 Modal -->
      <template>
        <v-dialog
          v-model="dialogViewForm3"
          max-width="1300px"
          :retain-focus="false"
          ><v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewForm3 == true && eval_type == 0">
                <Form3Modal :header_id="editedIndex" :form_id="form3_id" />
              </template>
              <template v-if="dialogViewForm3 == true && eval_type == 1">
                <Form3Modal :header_id="editedIndex" />
              </template>
            </v-card-text>
            <v-card-actions class="justify-end"> </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- View Plan Modal -->
      <template>
        <v-dialog
          v-model="dialogViewPlan"
          max-width="1300px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewPlan">
                <TypViewModal :header_id="editedIndex" :typ_year="typ_year" />
              </template>
            </v-card-text>
          </v-card>
        </v-dialog>
      </template>

      <!-- TABLE -->
      <template>
        <v-simple-table
          class="mini-margin-top my_class"
          loading
          loading-text="Loading... Please wait"
        >
          <template v-slot:default>
            <thead>
              <tr>
                <th>Actions</th>
                <th class="display-none">#</th>
                <th>City</th>
                <th>Reference Number</th>
                <th>Remarks</th>
                <th>Evaluated</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="res in allPlanList" :key="res.typ_header_id">
                <td>
                  <!-- <v-btn class="mx-1" color="info" depressed x-small>
                    View
                  </v-btn> -->

                  <v-btn
                    class="mx-1"
                    color="success"
                    depressed
                    x-small
                    @click="getHeaderEdit(res)"
                  >
                    Evaluate
                  </v-btn>
                </td>
                <td class="display-none">{{ res.id }}</td>
                <td>{{ res.city_name }}</td>
                <td>{{ res.reference_number }}</td>
                <td>{{ res.remarks }}</td>
                <td v-if="res.nswmcEval_id == null">
                  <v-icon color="error">mdi-close-circle</v-icon>
                </td>
                <td v-else>
                  <v-icon color="success">mdi-checkbox-marked-circle</v-icon>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        <v-overlay :value="overlay">
          <v-progress-circular indeterminate size="32"></v-progress-circular>
          Loading...
        </v-overlay>
      </template>
    </v-card>
  </v-container>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import TypViewModal from "../Include/typ_view_modal";
import Form2Modal from "../Include/form2_view";
import Form3Modal from "../Include/form3_view";
export default {
  name: "NswmcEvalList",
  components: {
    TypViewModal,
    Form2Modal,
    Form3Modal,
  },
  data: () => ({
    allLimit: [5, 10, 20, 50, 100],
    limitRow: [5],

    dialogAdd: false,
    dialogSubmit: false,
    dialogViewPlan: false,
    dialogViewForm2: false,
    dialogViewForm3: false,
    overlay: true,

    othersEval: false,
    form2_id: 0,
    form3_id: 0,
    eval_type: 0,

    Search: "",
    id: "",
    typ_year: 0,
    evaluation_id: "",
    user_region_id: "",
    // region_num: "",
    // cmbPersonnel: "",
    assigned_group: "",

    evaluation_radio: "",
    remarks: "",

    valid: true,
    editedIndex: -1,
    switch1: "",
    actionChoice: [
      {
        value: 1,
        name: "Submit",
      },
      {
        value: 0,
        name: "Assign",
      },
      {
        value: 2,
        name: "Return",
      },
    ],
    action_taken: "",
    requiredField: [(v) => !!v || "Field is required"],
    getCondition: 2,
  }),
  methods: {
    ...mapActions([
      "fetchPlan",
      "increment",
      "decrement",
      "searchPlanList",
      "SubmitNswmcEval",
      "getOthersEval",
    ]),
    ...mapMutations(["setPlanList"]),
    fetchListComponent() {
      this.fetchPlan({
        txtsearch: this.Search,
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: 4,
        new_app: 1,
        action: this.getCondition,
        user_type: this.getStateSession["user_type"],
        user_id: this.getStateSession["id"],
        regionId: this.getStateSession["region_id"],
        group: this.getStateSession["group"],
      }).then(() => {
        this.overlay = false;
      });
    },
    nextFunction() {
      this.increment({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
      });
      this.fetchListComponent();
    },
    prevFunction() {
      this.decrement({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
      });
      this.fetchListComponent();
    },
    limitFunction() {
      this.fetchListComponent();
    },
    getSearch() {
      this.fetchListComponent();
    },

    getHeaderEdit(res) {
      this.years();
      this.editedIndex = res.id;
      this.typ_year = res.year;
      this.evaluation_id = res.nswmcEval_id;
      this.user_region_id = res.region_id;
      this.txtStartYear = parseInt(res.year);
      this.txtReferenceNumber = res.reference_number;
      // this.region_num = res.rgnnum;

      this.form2_id = res.form_id;
      this.form3_id = res.form_id3;

      this.rowStatus = parseInt(res.status);
      this.route_order = parseInt(res.route_order);
      this.txtPcg = res.pcg;

      this.id = res.id;
      if (res.nswmcEval_id != "null") {
        this.evaluation_radio = res.evaluator_app;
        this.remarks = res.evaluator_remarks;
      }
      this.getOthersEval({
        typ_id: res.id,
        group: parseInt(res.assigned_group),
        condition: "notall",
        user_id: this.getStateSession["id"],
      });
      this.dialogAdd = true;
    },
    confirmSubmit(res) {
      if (this.$refs.form.validate()) {
        this.dialogSubmit = true;
      }
    },
    submitHeader() {
      this.SubmitNswmcEval({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        new_app: 1,
        route_order: this.route_order + 1,
        action_taken: this.switch1.name,
        assigned_group: this.assigned_group.value,
        internal_action: 0,

        search_status: 4,

        user_type: this.getStateSession["user_type"],
        user_id: this.getStateSession["id"],
        regionId: this.getStateSession["region_id"],
        group: this.getStateSession["group"],

        eval_id: this.evaluation_id,
        id: this.editedIndex,
        evaluator_app: this.evaluation_radio,
        remarks: this.remarks,
        date: this.getNow(),

        action: this.getCondition,
        search_status: 4,
        new_app: 1,
        group: this.getStateSession["group"],
        user_id: this.getStateSession["id"],
      });

      this.clearinput();
      this.dialogSubmit = false;
    },
    years() {
      let year = new Date().getFullYear();
      this.select_years = Array.from(
        { length: 10 },
        (value, index) => parseInt(year) + index
      );
      return Array.from(
        { length: 10 },
        (value, index) => parseInt(year) + index
      );
    },
    clearinput() {
      this.remarks = "";
      this.txtWasteGen = "";
      // this.cmbPersonnel = "";
      this.switch1 = "";
      this.dialogAdd = false;
      this.editedIndex = -1;
    },
    getNow() {
      const today = new Date();
      const date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + " " + time;
      return dateTime;
    },
  },
  computed: {
    ...mapGetters([
      "allPlanList",
      "tgetCurrentrow",
      "regionId",
      "allothersEval",
      "getStateSession",
    ]),
    buttonTitle() {
      return this.switch1.value == 0
        ? "Assign"
        : this.switch1.value == 2
        ? "Return"
        : "Submit";
    },
  },
  created() {
    this.setPlanList([]); // mutation from planModule to remove the data from getter allPlanList
    setTimeout(this.fetchListComponent, 1000);
  },
};
</script>