<template>
  <v-container>
    <v-btn small class="mt-2 mx-2" color="primary" @click="generateReport"
      >Print</v-btn
    >
    <section slot="pdf-content" id="printMe">
      <!-- <vue-html2pdf
        :show-layout="false"
        :float-layout="true"
        :enable-download="false"
        :preview-modal="true"
        :paginate-elements-by-height="1400"
        filename="hee hee"
        :pdf-quality="2"
        :manual-pagination="false"
        pdf-format="a4"
        pdf-orientation="portrait"
        pdf-content-width="800px"
        ref="html2Pdf"
      > -->
      <!-- FORM 2 PRINT -->
      <section slot="pdf-content" id="printMe2">
        <div class="text-h6 center" style="text-align: right">
          NSWMC TYP-001 <br />Rev. 1 August 28, 2018
        </div>
        <!-- <div class="text-h6 center">BRIEF INFORMATION</div> -->
        <div id="formTitle" class="text-h6 center">BRIEF INFORMATION</div>
        <div id="formTitle2" class="text-h6 center">
          {{ cityName }}, {{ provName }}
        </div>
      </section>
      <!-- </vue-html2pdf> -->
      <div id="myTable2">
        <table
          class="text-caption"
          dense
          id="myTable"
          style="border-collapse: collapse; border: 1px solid black; width: 98%"
        >
          <thead>
            <tr>
              <th class="text-left">Check Item</th>
              <!-- <th class="text-left">Page</th> -->
              <th class="text-left">Remarks</th>
            </tr>
          </thead>
          <tbody v-for="(infoContent, infoIndex) in info" :key="infoIndex">
            <tr v-for="(item, itemIndex) in infoContent.item" :key="itemIndex">
              <td>
                <b>{{ item.itemHeader }}</b> <br />
                {{ item.itemTitle }}
              </td>
              <td>
                <p>{{ item.itemModel }}</p>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <section slot="pdf-content" id="printMe2">
        <div>
          <div class="text-h6 center">EMB-{{ regName }}</div>
          <div class="text-h6 center">Evaluated on {{ evalDate }}</div>
          <br />
          <table style="collapse; border: 0 solid black; width: 98%">
            <tbody>
              <tr>
                <td
                  style="collapse; border: 0 solid black; width: 50%;font-size: 15px;"
                >
                  Prepared By:
                </td>
                <td
                  style="collapse; border: 0 solid black; width: 50%;font-size: 15px;"
                >
                  Noted By:
                </td>
              </tr>
              <tr
                style="
                  border: 0 solid black;
                  width: 50%;
                  height: 100px;
                  font-size: 15px;
                "
              >
                <td style="border: 0 solid black; vertical-align: middle">
                  <b> {{ regEvaluator }}</b>
                </td>
                <td style="border: 0 solid black; vertical-align: middle">
                  <b> {{ regChief }}</b>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div v-if="this.form_id != undefined">
          <div class="text-h6 center">EMB-CENTRAL OFFICE</div>
          <div class="text-h6 center">
            Evaluated on November 18 and 24, 2021
          </div>
          <br />
          <table style="collapse; border: 0 solid black; width: 98%">
            <tbody>
              <tr>
                <td
                  style="collapse; border: 0 solid black; width: 50%;font-size: 15px;"
                >
                  Prepared By:
                </td>
                <td
                  style="collapse; border: 0 solid black; width: 50%;font-size: 15px;"
                >
                  Noted By:
                </td>
              </tr>
              <tr
                style="
                  border: 0 solid black;
                  width: 50%;
                  height: 100px;
                  font-size: 15px;
                "
              >
                <td style="border: 0 solid black; vertical-align: middle">
                  <!-- <img
                    style="width: 300px"
                    src="http://localhost/swms_api/public/file_375158.jpg"
                  /> -->
                  <br />
                  <b> {{ centralEvaluator }}</b>
                </td>
                <td style="border: 0 solid black; vertical-align: middle">
                  <b> {{ centralChief }}</b>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </section>
    </section>
  </v-container>
</template>
<script>
// import numeral from "numeral/src/numeral";
// import VTextField from "vuetify/lib/components/VTextField";
// import VTextarea from "vuetify/lib/components/VTextarea";
// import VSelect from "vuetify/lib/components/VSelect";
import { mapGetters, mapActions } from "vuex";
let basePath = window.location.origin;
export default {
  components: {},
  props: ["header_id", "form_id"],
  data: () => ({
    name: "Form3Modal",
    info: [
      {
        step: "Step 1",
        item: [
          {
            itemHeader: "1. Name of LGU and Income Class",
            // itemTitle: "Name of LGU and Income Class",
            itemName: "lguName",
            itemModel: "",
          },
          {
            itemHeader: "2. Date Received",
            itemName: "receiveDate",
            itemModel: "",
          },
          {
            itemHeader: "3. LGU Profile",
            itemTitle: "No. of barangays (Urban & Rural)",
            itemName: "noBarangay",
            itemModel: "",
          },
          {
            itemTitle: "Population & 10 year Projection",
            itemName: "popProjection",
            itemModel: "",
          },
          {
            itemTitle: "Area in has",
            itemName: "areaHas",
            itemModel: "",
          },
        ],
      },
      {
        step: "Step 2",
        item: [
          {
            itemHeader: "4. Current SWM Condition",
            itemTitle: "Organization (SWM Office and Board )",
            itemName: "organization",
            itemModel: "",
          },
          {
            itemTitle: "Number of Staff",
            itemName: "numStaff",
            itemModel: "",
          },
          {
            itemTitle: "Collection Equipment",
            itemName: "colEquip",
            itemModel: "",
          },
          {
            itemTitle: "MRF",
            itemName: "mrf",
            itemModel: "",
          },
          {
            itemTitle: "Final disposal site",
            itemName: "finalDisposal",
            itemModel: "",
          },
          {
            itemTitle: "Waste Quantity",
            itemName: "wasteQuan",
            itemModel: "",
          },
          {
            itemTitle: "Waste Composition",
            itemName: "wasteCom",
            itemModel: "",
          },
          {
            itemTitle: "Expenditure on SWM",
            itemName: "expenditure",
            itemModel: "",
          },
          {
            itemTitle: "Annual Budget of the LGU",
            itemName: "annualBud",
            itemModel: "",
          },
        ],
      },
      {
        step: "Step 3",
        item: [
          {
            itemHeader: "5. Future Plan",
            itemTitle: "Source Reduction",
            itemName: "sourceRed",
            itemModel: "",
          },
          {
            itemTitle: "Collection",
            itemName: "collection",
            itemModel: "",
          },
          {
            itemTitle: "Transfer",
            itemName: "transfer",
            itemModel: "",
          },
          {
            itemTitle: "Processing",
            itemName: "processing",
            itemModel: "",
          },
          {
            itemTitle: "Disposal",
            itemName: "disposal",
            itemModel: "",
          },
          {
            itemTitle: "IEC",
            itemName: "iec",
            itemModel: "",
          },
          {
            itemTitle: "Special Waste",
            itemName: "specWaste",
            itemModel: "",
          },
        ],
      },
      {
        step: "Step 4",
        item: [
          {
            itemHeader:
              "6. Waste Diversion Target (Baseline and 10-Year projection)",
            // itemTitle: "Name of LGU and Income Class",
            itemName: "wasteDiv",
            itemModel: "",
          },
          {
            itemHeader: "7. Financing Aspect",
            itemTitle: "Funding Source/Options",
            itemName: "fundingSource",
            itemModel: "",
          },
          {
            itemTitle: "Expenditures/Investment",
            itemName: "expenditure",
            itemModel: "",
          },
          {
            itemTitle: "Repayment Plan/Cost Recovery System",
            itemName: "repaymentPLan",
            itemModel: "",
          },
        ],
      },
    ],
    cityName: "",
    provName: "",
    regName: "",
    evalDate: "",
    revDate: "",
    EvalBy: "",
    EvalNotedBy: "",
    RevBy: "",
    RevNotedBy: "",
    monthNames: [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ],

    regEvaluator: "",
    regChief: "",

    centralEvaluator: "",
    centralChief: "",
  }),
  created() {
    console.log(this.form_id);
    if (this.form_id != undefined) {
      this.fetchForm3({
        id: this.form_id,
      }).then(() => {
        this.setForm3Details();
        this.dialogPrint = true;
        this.getSignatory();
      });
    } else {
      this.fetchRegForm3({
        id: this.header_id,
      }).then(() => {
        this.setForm3Details();
        this.dialogPrint = true;
        this.getSignatory();
        let newAllChapter = JSON.stringify(this.allForm3);
      });
    }
  },
  methods: {
    ...mapActions(["fetchRegForm3", "fetchForm3", "fetchSignatory3"]),
    setForm3Details() {
      let newAllChapter = JSON.stringify(this.allForm3);
      let new_data = JSON.parse(newAllChapter);
      if (new_data != null) {
        console.log(new_data);
        this.info[0].item[0].itemModel = new_data.lguName;
        this.info[0].item[1].itemModel = new_data.receiveDate;
        this.info[0].item[2].itemModel = new_data.noBarangay;
        this.info[0].item[3].itemModel = new_data.popProjection;
        this.info[0].item[4].itemModel = new_data.areaHas;

        this.info[1].item[0].itemModel = new_data.organization;
        this.info[1].item[1].itemModel = new_data.numStaff;
        this.info[1].item[2].itemModel = new_data.colEquip;
        this.info[1].item[3].itemModel = new_data.mrf;
        this.info[1].item[4].itemModel = new_data.finalDisposal;
        this.info[1].item[5].itemModel = new_data.wasteQuan;
        this.info[1].item[6].itemModel = new_data.wasteCom;
        this.info[1].item[7].itemModel = new_data.expenditure;
        this.info[1].item[8].itemModel = new_data.annualBud;

        this.info[2].item[0].itemModel = new_data.sourceRed;
        this.info[2].item[1].itemModel = new_data.collection;
        this.info[2].item[2].itemModel = new_data.transfer;
        this.info[2].item[3].itemModel = new_data.processing;
        this.info[2].item[4].itemModel = new_data.disposal;
        this.info[2].item[5].itemModel = new_data.iec;
        this.info[2].item[6].itemModel = new_data.specWaste;

        this.info[3].item[0].itemModel = new_data.wasteDiv;
        this.info[3].item[1].itemModel = new_data.fundingSource;
        this.info[3].item[2].itemModel = new_data.expenditureInv;
        this.info[3].item[3].itemModel = new_data.repaymentPLan;

        this.cityName = new_data.city_name;
        this.provName = new_data.prov_name;
        this.regName = new_data.reg_name;

        this.evalDate = this.convertDate(new_data.evaluate_date);
      }

      this.$emit("update", this.info);
    },
    generateReport() {
      this.$htmlToPaper("printMe");
    },
    convertDate(date) {
      const date2 = new Date(date);
      //const month = date.toLocaleString("default", { month: "long" });
      //return date.getMonth(long);
      return (
        this.monthNames[date2.getMonth()] +
        " " +
        date2.getDate() +
        ", " +
        date2.getFullYear()
      );
    },

    getSignatory() {
      this.fetchSignatory3({
        id: this.header_id,
      }).then(() => {
        this.setSignatory();
      });
    },

    setSignatory() {
      let signatory = JSON.stringify(this.allSignatory);
      let new_data = JSON.parse(signatory);
      for (let i = 0; i < new_data.length; i++) {
        if (new_data[i]["eval"] == 1) {
          this.regEvaluator = new_data[i]["eval_name"];
          this.regChief = new_data[i]["chief_name"];
        } else {
          this.centralEvaluator = new_data[i]["eval_name"];
          this.centralChief = new_data[i]["chief_name"];
        }
      }
    },
  },
  computed: { ...mapGetters(["allForm3", "allSignatory"]) },
};
</script>