<template>
  <v-container class="pa-1">
    <v-card class="mx-auto pa-4">
      <template>
        <v-dialog
          v-model="dialogAdd"
          persistent
          max-width="700px"
          :retain-focus="false"
        >
          <template v-slot:activator="{}">
            <v-row>
              <v-col>
                <v-row>
                  <v-col>
                    <v-text-field
                      label="Search Bar"
                      v-model="Search"
                      @keyup="getSearch"
                      dense
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-combobox
                      v-model="limitRow"
                      label="Show"
                      :items="allLimit"
                      @change="limitFunction"
                      dense
                    >
                    </v-combobox>
                  </v-col>
                </v-row>
              </v-col>
              <v-col>
                <v-btn
                  class="float-right mini-margin-left"
                  @click="nextFunction"
                  small
                  >Next</v-btn
                >
                <v-btn class="float-right" @click="prevFunction" small
                  >Prev</v-btn
                >
                <input v-model="tgetCurrentrow" type="hidden" />
              </v-col>
            </v-row>
          </template>
        </v-dialog>
      </template>

      <!-- View History Modal -->
      <template>
        <v-dialog
          v-model="dialogViewHistory"
          max-width="1700px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewHistory == true">
                <HistoryViewModal :header_id="editedIndex" />
              </template>
            </v-card-text>
          </v-card>
        </v-dialog>
      </template>

      <!-- TABLE -->
      <template>
        <v-simple-table class="mini-margin-top">
          <template v-slot:default>
            <thead>
              <tr>
                <th>Actions</th>
                <th>City</th>
                <th>Region</th>
                <th>Reference Number</th>
                <th>Status</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="res in allPlanList" :key="res.typ_header_id">
                <td>
                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        class="mx-1"
                        color="success"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="
                          editedIndex = res.id;
                          dialogViewHistory = true;
                        "
                      >
                        <v-icon small>mdi-history</v-icon>
                      </v-btn>
                    </template>
                    <span>History</span>
                  </v-tooltip>
                </td>
                <td class="display-none">{{ res.id }}</td>
                <td>{{ res.city_name }}</td>
                <td>{{ res.rgnnam }}</td>
                <td>{{ res.reference_number }}</td>
                <td>{{ res.status_name }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        <v-overlay :value="overlay">
          <v-progress-circular indeterminate size="32"></v-progress-circular>
          Loading...
        </v-overlay>
      </template>
    </v-card>
  </v-container>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import HistoryViewModal from "../Include/history_view";
export default {
  name: "queryList",
  components: {
    HistoryViewModal,
  },
  data: () => ({
    allLimit: [10, 20, 50, 100],
    limitRow: [10],
    dialogAdd: false,
    Search: "",
    dialogViewHistory: false,
    overlay: true,
    editedIndex: -1,
  }),
  methods: {
    ...mapActions(["fetchPlan", "increment", "decrement"]),
    ...mapMutations(["setPlanList"]),
    fetchListComponent() {
      this.fetchPlan({
        txtsearch: this.Search,
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: 0,
        new_app: 1,
        user_type: this.getStateSession["user_type"],
        user_id: this.getStateSession["id"],
        regionId: this.getStateSession["region_id"],
        group: this.getStateSession["group"],
      }).then(() => {
        this.overlay = false;
      });
    },
    nextFunction() {
      this.increment({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
      });
      this.fetchListComponent();
    },
    prevFunction() {
      this.decrement({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
      });
      this.fetchListComponent();
    },
    limitFunction() {
      this.fetchListComponent();
    },
    getSearch() {
      this.fetchListComponent();
    },
  },
  computed: {
    ...mapGetters(["allPlanList", "tgetCurrentrow", "getStateSession"]),
  },
  created() {
    this.setPlanList([]); // mutation from planModule to remove the data from getter allPlanList
    setTimeout(this.fetchListComponent(), 1000);
  },
};
</script>