<template>
  <v-container>
    <v-row>
      <v-col
        v-for="(editInput, inputIndex) in editFormObject[editFormIndex]
          .editableForm"
        :key="inputIndex"
        cols="12"
        sm="12"
        md="3"
        :lg="setLGSize(editInput.lgSize)"
      >
        <template v-if="editInput.isFile == true">
          <component
            :is="editInput.inputType"
            :label="editInput.label"
            :readonly="isDisable(editInput.editable, inputIndex)"
            :filled="isDisable(editInput.editable, inputIndex)"
            :items="editInput.items"
            :id="`${editInput.model}-${editFormIndex}`"
            :hint="editInput.note"
            persistent-hint
            v-model="editInput.value"
            class=""
            dense
            outlined
            multiple
            small-chips
            color="blue darken-2"
            @keyup="
              isNum(
                editInput.value,
                editFormIndex,
                inputIndex,
                $event,
                editInput.isNumber,
                editInput.model
              )
            "
          >
          </component>
        </template>
        <template v-else-if="editInput.isDate != true">
          <component
            :is="editInput.inputType"
            :label="editInput.label"
            :readonly="isDisable(editInput.editable, inputIndex)"
            :filled="isDisable(editInput.editable, inputIndex)"
            :items="editInput.items"
            :id="`${editInput.model}-${editFormIndex}`"
            :hint="editInput.note"
            persistent-hint
            v-model="editInput.value"
            class=""
            dense
            outlined
            color="blue darken-2"
            @keyup="
              isNum(
                editInput.value,
                editFormIndex,
                inputIndex,
                $event,
                editInput.isNumber,
                editInput.model
              )
            "
          >
          </component>
        </template>
        <template v-else>
          <v-menu
            v-model="menu"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            max-width="290px"
            min-width="auto"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="editInput.value"
                label="Date"
                hint="YYYY/MM/DD"
                persistent-hint
                readonly
                prepend-icon="mdi-calendar"
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker
              v-model="editInput.value"
              no-title
              @input="menu = false"
            ></v-date-picker>
          </v-menu>
        </template>
      </v-col>
    </v-row>
    <!-- v-on:keyup="putComma($event)" -->
  </v-container>
</template>
<script>
import numeral from "numeral/src/numeral";
import VTextField from "vuetify/lib/components/VTextField";
import VTextarea from "vuetify/lib/components/VTextarea";
import VFileInput from "vuetify/lib/components/VFileInput";
import VSelect from "vuetify/lib/components/VSelect";
import VAutocomplete from "vuetify/lib/components/VAutocomplete";
export default {
  components: {
    VTextField,
    VTextarea,
    VSelect,
    numeral,
    VAutocomplete,
    VFileInput,
  },
  props: ["dialogEdit", "editFormObject", "editFormIndex"],
  data: () => ({
    name: "EditModal",
    total17: "",
    total1111: "",
    menu: false,
    date: null,
    activePicker: null,
  }),
  created() {},
  methods: {
    isDisable(option, index) {
      // check option and index
      if (option) {
        return false;
      } else {
        return true;
      }
      // return true - disable, false - active
    },
    isNum(value, index, index2, event, isnum, withCompute) {
      if (isnum) {
        var isValid = /^[0-9,.]*$/.test(value);
        if (isValid) {
          value = value.replace(/,/g, ""); // remove commas from existing input
          this.editFormObject[index].editableForm[index2].value =
            this.numberWithCommas(value); // add commas back in
        } else {
          //event.target.value = event.target.value.replace(/[^0-9,.]/g, "");
          this.editFormObject[index].editableForm[index2].value = "";
        }
        this.withCalcu(value, index, index2, withCompute);
      }
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      9, 150.2;
      return parts.join(".");
    },
    withCalcu(formvalue, editFormIndex, inputIndex, withCompute) {
      formvalue = formvalue.replace(/,/g, ""); // remove commas from existing input
      if (withCompute) {
        if (editFormIndex == 17) {
          //Filter the array and get needed textfield form
          let newArray = this.editFormObject[17].editableForm.filter(
            (e, i) => !(i % 2 !== 0)
          );

          newArray.splice(0, 1); //Remove first object

          //Sum value of array
          let newsum = newArray.reduce(
            (a, b) =>
              parseFloat(a) +
              (parseFloat(b.value.toString().replace(/,/g, "")) || 0),
            0
          );

          //Set to State (TOTAL)
          this.editFormObject[17].editableForm[1].value = this.numberWithCommas(
            newsum.toFixed(2)
          );

          this.total17 = this.numberWithCommas(newsum);
        }
        if (editFormIndex == 20) {
          let wasteGen = parseFloat(
            this.editFormObject[20].editableForm[2].value.replace(/,/g, "")
          );
          if (inputIndex == 4) {
            let wasteDisposedPer = 100 - parseFloat(formvalue);
            let wasteDiverted =
              (parseFloat(formvalue) / 100) *
              parseFloat(
                this.editFormObject[20].editableForm[2].value.replace(/,/g, "")
              );

            let wasteDisposed =
              (wasteDisposedPer / 100) *
              parseFloat(
                this.editFormObject[20].editableForm[2].value.replace(/,/g, "")
              );

            this.editFormObject[20].editableForm[6].value =
              wasteDisposedPer.toFixed(2);

            this.editFormObject[20].editableForm[3].value =
              this.numberWithCommas(wasteDiverted.toFixed(3));

            this.editFormObject[20].editableForm[5].value =
              this.numberWithCommas(wasteDisposed.toFixed(3));
          }

          if (inputIndex == 8) {
            let data = (parseFloat(formvalue) / 100) * wasteGen;
            if (!isNaN(data)) {
              this.editFormObject[20].editableForm[7].value =
                this.numberWithCommas(data.toFixed(3));
            }
          }

          if (inputIndex == 10) {
            let data = (parseFloat(formvalue) / 100) * wasteGen;
            if (!isNaN(data)) {
              this.editFormObject[20].editableForm[9].value =
                this.numberWithCommas(data.toFixed(3));
            }
          }

          if (inputIndex == 12) {
            let data = (parseFloat(formvalue) / 100) * wasteGen;
            if (!isNaN(data)) {
              this.editFormObject[20].editableForm[11].value =
                this.numberWithCommas(data.toFixed(3));
            }
          }

          if (inputIndex == 14) {
            let data = (parseFloat(formvalue) / 100) * wasteGen;
            if (!isNaN(data)) {
              this.editFormObject[20].editableForm[13].value =
                this.numberWithCommas(data.toFixed(3));
            }
          }

          if (inputIndex == 16) {
            let data = (parseFloat(formvalue) / 100) * wasteGen;
            if (!isNaN(data)) {
              this.editFormObject[20].editableForm[15].value =
                this.numberWithCommas(data.toFixed(3));
            }
          }
        }
        if (
          editFormIndex == 36 ||
          editFormIndex == 37 ||
          editFormIndex == 38 ||
          editFormIndex == 39 ||
          editFormIndex == 40 ||
          editFormIndex == 41
        ) {
          let newFormArray = this.editFormObject[
            editFormIndex
          ].editableForm.filter((e, i) => e.isNumber == true); // Filter text field with attribute of isNumber

          newFormArray.splice(0, 1); //Remove first object

          //Sum value of array
          let newsum = newFormArray.reduce(
            (a, b) =>
              parseFloat(a) +
              (parseFloat(b.value.toString().replace(/,/g, "")) || 0),
            0
          );

          //Set to State (TOTAL)
          this.editFormObject[editFormIndex].editableForm[1].value =
            this.numberWithCommas(newsum.toFixed(2));
        }
        if (editFormIndex == 7) {
          let newArray = this.editFormObject[7].editableForm.filter(
            (e, i) => i == 1 || i == 2
          );

          //Sum value of array
          let newsum = newArray.reduce(
            (a, b) =>
              parseFloat(a) +
              (parseFloat(b.value.toString().replace(/,/g, "")) || 0),
            0
          );

          // //Set to State (TOTAL)
          this.editFormObject[7].editableForm[3].value = this.numberWithCommas(
            newsum.toFixed(2)
          );
        }
      }
    },
    setLGSize(size) {
      if (size) {
        return size;
      } else {
        return 6;
      }
    },
    save() {
      this.$refs.menu.save(date);
    },
  },
  computed: {},
  watch: {
    total17(val) {
      let per = 0;
      for (let i = 2; i <= 10; i += 2) {
        if (
          this.editFormObject[17].editableForm[i].value != "" &&
          this.editFormObject[17].editableForm[i].value != 0
        ) {
          per =
            (parseFloat(
              this.editFormObject[17].editableForm[i].value
                .toString()
                .replace(/,/g, "")
            ).toFixed(2) /
              parseFloat(val.replace(/,/g, ""))) *
            100;
        } else {
          per = 0;
        }

        this.editFormObject[17].editableForm[i + 1].value = per.toFixed(2);
      }
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    menu(val) {
      val && setTimeout(() => (this.activePicker = "YEAR"));
    },
  },
};
</script>