<template>
  <v-container>
    <v-stepper alt-labels v-model="e1">
      <!-- START OF STEPPER HEADER-->
      <v-stepper-header>
        <template v-for="(infoContent, infoIndex) in info">
          <v-stepper-step
            :complete="e1 > infoIndex"
            :step="infoIndex + 1"
            :key="infoContent.step"
          >
            <div v-if="e1 - 1 == infoIndex"></div>
          </v-stepper-step>

          <v-divider v-if="infoIndex < info.length - 1" :key="infoIndex" />
        </template>
      </v-stepper-header>
      <!-- END OF STEPPER HEADER-->

      <v-btn class="mt-2 mx-2" color="primary" @click="dialogAdd = true"
        >Show Plan</v-btn
      >
      <v-btn
        v-if="user_type == 4"
        class="mt-2 mx-2"
        color="primary"
        @click="getRegEvaluation"
        >Copy Region's Evaluation</v-btn
      >

      <!-- START OF STEPPER CONTENT-->
      <v-layout row wrap>
        <v-flex>
          <v-card class="overflow-auto mt-7 mx-2">
            <v-stepper-items>
              <v-stepper-content
                v-for="(infoContent, infoIndex) in info"
                :key="infoIndex"
                :step="infoIndex + 1"
              >
                <v-container
                  v-for="(item, itemIndex) in infoContent.item"
                  :key="itemIndex"
                >
                  <div class="text-h5" v-text="item.itemHeader"></div>

                  <br />
                  <div class="text-subtitle-2" v-text="item.itemTitle"></div>
                  <v-textarea
                    :value="item.itemModel"
                    @change="(v) => (item.itemModel = v)"
                    outlined
                  >
                  </v-textarea>
                  <v-divider></v-divider>
                </v-container>
                <v-card-actions>
                  <v-btn @click="previous" v-if="infoIndex > 0">Back </v-btn>
                  <!-- @click="previous" -->
                  <v-spacer></v-spacer>
                  <v-btn
                    color="primary"
                    @click="next()"
                    v-if="infoIndex < info.length - 1"
                    >Next</v-btn
                  >

                  <v-btn
                    @click="submit()"
                    color="success"
                    v-if="infoIndex == info.length - 1"
                    >Save
                  </v-btn>
                </v-card-actions>
              </v-stepper-content>
            </v-stepper-items>
          </v-card>
        </v-flex>
      </v-layout>
      <!-- END OF STEPPER CONTENT-->
    </v-stepper>

    <template>
      <v-dialog v-model="dialogAdd" max-width="1300px" :retain-focus="false"
        ><v-card>
          <v-card-title>
            <span class="headline"></span>
          </v-card-title>
          <v-card-text>
            <v-container
              v-for="(chapterContent, chapterIndex) in chapters"
              :key="chapterIndex"
              :step="chapterIndex + 1"
              class="mt-10"
            >
              <div
                :id="'print-chapter-' + (chapterIndex + 1)"
                class="section-to-print"
              >
                <!-- PRINT LOOP FOR CHAPTER TITLE-->
                <div
                  class="text-h4 text-center"
                  v-text="chapterContent.chapterTitle"
                ></div>
                <!-- PRINT LOOP FOR CHAPTER TITLE-->
                <br />

                <!-- PRINT LOOP CHAPTER SUB TITLE-->
                <div
                  class="text-h5 text-center"
                  v-text="chapterContent.chapterSubTitle"
                ></div>
                <!-- PRINT LOOP CHAPTER SUB TITLE-->
                <br />

                <!--START LOOP OF OUTLINE PER CHAPTER-->
                <v-container
                  v-for="(outline, outlineIndex) in chapterContent.outline"
                  :key="outlineIndex"
                >
                  <p class="text-h6">{{ outline.outlineTitle }}</p>
                  <p>{{ outline.outlineModel }}</p>

                  <!--START ATTACHMENT PER OUTLINE-->
                  <v-container
                    class="not-to-print"
                    v-for="(upload, uploadIndex) in outline.uploads"
                    :key="'up' + uploadIndex"
                  >
                    <v-row id="not-to-print" class="not-to-print">
                      <v-col md="4">
                        <v-menu offset-y>
                          <template v-slot:activator="{ on, attrs }">
                            <v-btn
                              color="primary"
                              dark
                              v-bind="attrs"
                              v-on="on"
                            >
                              Attachments
                            </v-btn>
                          </template>
                          <v-list>
                            <!-- :href="item.new_name" -->
                            <template v-for="(item, index) in upload.curr_data">
                              <v-list-item
                                :key="`fle` + index"
                                v-if="item.ext == `pdf`"
                                :href="item.new_name"
                                target="_blank"
                                :download="item.new_name"
                              >
                                <v-list-item-title>
                                  {{ item.orig_name }}</v-list-item-title
                                >
                              </v-list-item>
                            </template>
                          </v-list>
                        </v-menu>
                      </v-col>
                      <!-- <div class="p" v-text="upload.curr_data"></div> -->
                    </v-row>

                    <v-container>
                      <v-row>
                        <template v-for="(item, index) in upload.curr_data">
                          <v-col
                            :key="`img` + index"
                            v-if="item.ext == `jpg`"
                            class="d-flex child-flex"
                            cols="4"
                          >
                            <v-img
                              :src="item.new_name"
                              max-width="300"
                              class="grey lighten-2"
                            >
                              <template v-slot:placeholder>
                                <v-row
                                  class="fill-height ma-0"
                                  align="center"
                                  justify="center"
                                >
                                  <v-progress-circular
                                    indeterminate
                                    color="grey lighten-5"
                                  ></v-progress-circular>
                                </v-row>
                              </template>
                            </v-img>
                          </v-col>
                        </template>
                      </v-row>
                    </v-container>
                  </v-container>
                  <!--START ATTACHMENT PER OUTLINE-->

                  <!-- PRINT TEXTAREA OF OUTLINE PER CHAPTER-->

                  <!--START LOOP OF TABLE PER OUTLINE-->
                  <v-container
                    v-for="(table, tableIndex) in outline.outlineTable"
                    :key="tableIndex"
                  >
                    <br />
                    <!--START SET UP OF TABLE WITH LOOP FOR HEADERS AND TABLE DATA-->
                    <template v-if="table.tableName == '821'">
                      <v-data-table
                        :headers="table.tableHeader"
                        :items="table.tableData"
                        :hide-default-footer="true"
                        class="elevation-1"
                        style="white-space: pre-line"
                        dense
                      >
                        <template v-slot:top>
                          <v-toolbar flat>
                            <v-toolbar-title class="text-subtitle-2">{{
                              table.tableTitle
                            }}</v-toolbar-title>
                            <v-divider class="mx-4" inset vertical></v-divider>
                            <v-spacer></v-spacer>
                            <template v-if="table.tableAddNew > 0">
                              <v-btn
                                color="primary"
                                dark
                                class="mb-2"
                                @click="
                                  addItem(
                                    chapterIndex,
                                    outlineIndex,
                                    tableIndex,
                                    table.tableName
                                  )
                                "
                              >
                                New Item
                              </v-btn>
                            </template>
                          </v-toolbar>
                        </template>
                        <!-- <template v-slot:header="props">
                  <thead>
                    <tr>
                      <th colspan="2">Category 1</th>
                      <th colspan="3">Category 2</th>
                    </tr>
                  </thead>
                </template> -->
                        <!--START OF CREATING ACTIONS PER ROW-->
                        <template v-slot:[`item.actions`]="{ item }">
                          <v-icon
                            small
                            class="mr-2"
                            @click="
                              editItem(
                                chapterIndex,
                                outlineIndex,
                                tableIndex,
                                table.tableName,
                                item
                              )
                            "
                          >
                            mdi-pencil
                          </v-icon>
                          <v-icon
                            small
                            @click="
                              deleteItem(
                                chapterIndex,
                                outlineIndex,
                                tableIndex,
                                item
                              )
                            "
                            v-if="table.tableAddNew > 0"
                          >
                            mdi-delete
                          </v-icon>
                        </template>
                        <!--END OF CREATING ACTIONS PER ROW-->
                        <!-- <template slot="body.append">
                <tr v-html="footerTotal()" class="pink--text"></tr>
              </template> -->
                      </v-data-table>
                    </template>

                    <template v-else>
                      <v-data-table
                        dense
                        :headers="filterHeader(table.tableHeader)"
                        :items="table.tableData"
                        :items-per-page="-1"
                        :hide-default-footer="true"
                        class="elevation-1"
                        style="white-space: pre-line"
                        loading="false"
                        loading-text="No Data"
                      >
                        <!-- v-if="table.tableName == '231'" -->
                        <template v-slot:top>
                          <v-toolbar flat>
                            <v-toolbar-title class="text-subtitle-2">{{
                              table.tableTitle
                            }}</v-toolbar-title>
                            <v-divider class="mx-4" inset vertical></v-divider>
                            <v-spacer></v-spacer>
                          </v-toolbar>
                        </template>
                        <template
                          slot="body.append"
                          v-if="table.withTotal == 1"
                        >
                          <tr>
                            <td class="text-subtitle-2">Total</td>
                            <td
                              class="text-subtitle-2"
                              v-for="x in nonNullItems(
                                chapterIndex,
                                outlineIndex,
                                tableIndex
                              )"
                              :key="x"
                              :id="`${tableIndex}-${x}`"
                            >
                              {{
                                sumField(
                                  chapterIndex,
                                  outlineIndex,
                                  tableIndex,
                                  x
                                )
                              }}
                            </td>
                          </tr>
                        </template>
                        <template
                          slot="body.append"
                          v-if="table.withTotalPer == 1"
                        >
                          <tr>
                            <td class="text-subtitle-2">Total %</td>
                            <td
                              class="text-subtitle-2"
                              v-for="x in nonNullItems(
                                chapterIndex,
                                outlineIndex,
                                tableIndex
                              )"
                              :key="x"
                              :id="`${tableIndex}-${x}`"
                            >
                              {{
                                sumFieldPer(
                                  chapterIndex,
                                  outlineIndex,
                                  tableIndex,
                                  x
                                )
                              }}
                            </td>
                          </tr>
                        </template>

                        <!-- <template slot="body.append">
                <tr v-html="footerTotal()" class="pink--text"></tr>
              </template> -->
                      </v-data-table>
                    </template>
                    <!--END SET UP OF TABLE WITH LOOP FOR HEADERS AND TABLE DATA-->
                    <br />
                  </v-container>
                  <!--END LOOP OF TABLE PER OUTLINE-->
                </v-container>
                <!--END LOOP OF OUTLINE PER CHAPTER-->
              </div>
              <!--CARD FOOTER-->
              <v-card-actions> </v-card-actions>
              <!--CARD FOOTER-->
            </v-container>
          </v-card-text>
          <v-card-actions class="justify-end">
            <v-btn text @click="dialogAdd = false">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </template>

    <template>
      <v-snackbar
        v-model="snackbar"
        top
        right
        :timeout="timeout"
        color="success"
        elevation="24"
      >
        {{ text }}
      </v-snackbar>
    </template>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  props: ["chapters"],
  data() {
    return {
      e1: 1,
      name: "FormThreeCom",
      info: [
        {
          step: "Step 1",
          item: [
            {
              itemHeader: "1. Name of LGU and Income Class",
              // itemTitle: "Name of LGU and Income Class",
              itemName: "lguName",
              itemModel: "",
            },
            {
              itemHeader: "2. Date Received",
              itemName: "receiveDate",
              itemModel: "",
            },
            {
              itemHeader: "3. LGU Profile",
              itemTitle: "No. of barangays (Urban & Rural)",
              itemName: "noBarangay",
              itemModel: "",
            },
            {
              itemTitle: "Population & 10 year Projection",
              itemName: "popProjection",
              itemModel: "",
            },
            {
              itemTitle: "Area in has",
              itemName: "areaHas",
              itemModel: "",
            },
          ],
        },
        {
          step: "Step 2",
          item: [
            {
              itemHeader: "Current SWM Condition",
              itemTitle: "Organization (SWM Office and Board )",
              itemName: "organization",
              itemModel: "",
            },
            {
              itemTitle: "Number of Staff",
              itemName: "numStaff",
              itemModel: "",
            },
            {
              itemTitle: "Collection Equipment",
              itemName: "colEquip",
              itemModel: "",
            },
            {
              itemTitle: "MRF",
              itemName: "mrf",
              itemModel: "",
            },
            {
              itemTitle: "Final disposal site",
              itemName: "finalDisposal",
              itemModel: "",
            },
            {
              itemTitle: "Waste Quantity",
              itemName: "wasteQuan",
              itemModel: "",
            },
            {
              itemTitle: "Waste Composition",
              itemName: "wasteCom",
              itemModel: "",
            },
            {
              itemTitle: "Expenditure on SWM",
              itemName: "expenditure",
              itemModel: "",
            },
            {
              itemTitle: "Annual Budget of the LGU",
              itemName: "annualBud",
              itemModel: "",
            },
          ],
        },
        {
          step: "Step 3",
          item: [
            {
              itemHeader: "5. Future Plan",
              itemTitle: "Source Reduction",
              itemName: "sourceRed",
              itemModel: "",
            },
            {
              itemTitle: "Collection",
              itemName: "collection",
              itemModel: "",
            },
            {
              itemTitle: "Transfer",
              itemName: "transfer",
              itemModel: "",
            },
            {
              itemTitle: "Processing",
              itemName: "processing",
              itemModel: "",
            },
            {
              itemTitle: "Disposal",
              itemName: "disposal",
              itemModel: "",
            },
            {
              itemTitle: "IEC",
              itemName: "iec",
              itemModel: "",
            },
            {
              itemTitle: "Special Waste",
              itemName: "specWaste",
              itemModel: "",
            },
          ],
        },
        {
          step: "Step 4",
          item: [
            {
              itemHeader:
                "6. Waste Diversion Target (Baseline and 10-Year projection)",
              // itemTitle: "Name of LGU and Income Class",
              itemName: "wasteDiv",
              itemModel: "",
            },
            {
              itemHeader: "7. Financing Aspect",
              itemTitle: "Funding Source/Options",
              itemName: "fundingSource",
              itemModel: "",
            },
            {
              itemTitle: "Expenditures/Investment",
              itemName: "expenditure",
              itemModel: "",
            },
            {
              itemTitle: "Repayment Plan/Cost Recovery System",
              itemName: "repaymentPLan",
              itemModel: "",
            },
          ],
        },
      ],
      dialogAdd: false,
      user_type: 0,
      eval: 1,
      snackbar: false,
      text: "Copied.",
      timeout: 2000,
    };
  },
  created() {
    if (sessionStorage.getItem("access") == 4) {
      this.eval = 0;
    }
    this.user_type = sessionStorage.getItem("access");
    this.pushProjectedYear(sessionStorage.getItem("year"));
    if (sessionStorage.getItem("form_id") != "null") {
      this.fetchForm3({
        id: sessionStorage.getItem("form_id"),
      }).then(() => {
        this.setForm3Details();
      });
    }

    this.fetchchapters(sessionStorage.getItem("typ_header_id"));
    this.fetchAttachement(sessionStorage.getItem("typ_header_id"));
  },
  methods: {
    ...mapActions([
      "fetchForm3",
      "createForm3",
      "fetchRegForm3",
      "fetchAllPerChapter",
      "fetchAttachAllPerChapter",
    ]),
    next() {
      this.e1++;
      this.scrollTop();
    },
    submit() {
      this.createForm3({
        header_id: sessionStorage.getItem("typ_header_id"),
        lguName: this.info[0].item[0].itemModel,
        receiveDate: this.info[0].item[1].itemModel,
        noBarangay: this.info[0].item[2].itemModel,
        popProjection: this.info[0].item[3].itemModel,
        areaHas: this.info[0].item[4].itemModel,

        organization: this.info[1].item[0].itemModel,
        numStaff: this.info[1].item[1].itemModel,
        colEquip: this.info[1].item[2].itemModel,
        mrf: this.info[1].item[3].itemModel,
        finalDisposal: this.info[1].item[4].itemModel,
        wasteQuan: this.info[1].item[5].itemModel,
        wasteCom: this.info[1].item[6].itemModel,
        expenditure: this.info[1].item[7].itemModel,
        annualBud: this.info[1].item[8].itemModel,

        sourceRed: this.info[2].item[0].itemModel,
        collection: this.info[2].item[1].itemModel,
        transfer: this.info[2].item[2].itemModel,
        processing: this.info[2].item[3].itemModel,
        disposal: this.info[2].item[4].itemModel,
        iec: this.info[2].item[5].itemModel,
        specWaste: this.info[2].item[6].itemModel,

        wasteDiv: this.info[3].item[0].itemModel,
        fundingSource: this.info[3].item[1].itemModel,
        expenditureInv: this.info[3].item[2].itemModel,
        repaymentPLan: this.info[3].item[3].itemModel,
        date: this.getNow(),
        eval: 1,
      });
    },
    filterHeader(headers) {
      var newArrObj = headers.filter(function (obj) {
        return obj.text !== "Actions";
      });
      return newArrObj;
    },

    nonNullItems(chapterIndex, outlineIndex, tableIndex) {
      let x = this.chapters[chapterIndex].outline[outlineIndex].outlineTable[
        tableIndex
      ].tableHeader
        .filter((e) => {
          return e.total;
        })
        .map((e) => {
          return e.total;
        });
      return x;
    },
    sumField(chapterBase, outline, outlineTable, key) {
      // sum data in give key (property)
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid) {
        let value = this.chapters[chapterBase].outline[outline].outlineTable[
          outlineTable
        ].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        return this.numberWithCommas(value.toFixed(2));
      }
    },

    sumFieldPer(chapterBase, outline, outlineTable, key) {
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid && key != "totalKg") {
        let value = this.chapters[chapterBase].outline[outline].outlineTable[
          outlineTable
        ].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        let total = this.chapters[chapterBase].outline[outline].outlineTable[
          outlineTable
        ].tableData.reduce(
          (a, b) =>
            parseFloat(a) + (parseFloat(b["totalKg"].replace(/,/g, "")) || 0),
          0
        );
        let per = (value / total) * 100;

        return this.numberWithCommas(per.toFixed(2)) + "%";
      }
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return parts.join(".");
    },
    setChapterDetails(i) {
      let newAllChapter = [];
      newAllChapter = JSON.stringify(this.allChapter);
      if (newAllChapter.length >= 3) {
        var chapterBase = i - 1;

        if (i == 1) {
          let new_data = JSON.parse(newAllChapter)[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.introduction;
          this.chapters[chapterBase].outline[1].outlineModel = new_data.purpose;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.approach;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.acknowledgement;
        }
        if (i == 2) {
          let new_data = JSON.parse(newAllChapter)[0];
          //let new_data = new_data1[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.location;
          this.chapters[chapterBase].outline[1].outlineModel = new_data.history;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.population;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.economic_profile_land_use;
          this.chapters[chapterBase].outline[4].outlineModel =
            new_data.physical_characteristic;

          // Checking and populatings For table 231
          if (new_data.current_population_barangay) {
            let curPop = JSON.parse(new_data.current_population_barangay);
            this.chapters[chapterBase].outline[2].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[2].outlineTable[0].tableData =
              curPop;
          }

          // Checking and populating For table 232
          if (new_data.ten_year_projected_p) {
            let tenYear = JSON.parse(new_data.ten_year_projected_p);
            this.chapters[chapterBase].outline[2].outlineTable[1].tableData =
              [];
            this.chapters[chapterBase].outline[2].outlineTable[1].tableData =
              tenYear;
          }

          // Checking and populating  For table 241
          if (new_data.ten_year_projected_p) {
            let listInd = JSON.parse(new_data.list_industries_within_city);
            this.chapters[chapterBase].outline[3].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[0].tableData =
              listInd;
          }

          // Checking and populating  For table 242
          if (new_data.land_used_map_urban) {
            let mapUrban = JSON.parse(new_data.land_used_map_urban);
            this.chapters[chapterBase].outline[3].outlineTable[1].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[1].tableData =
              mapUrban;
          }

          // Checking and populating  For table 243
          if (new_data.land_used_map_urban) {
            let mapGen = JSON.parse(new_data.land_used_map_general);
            this.chapters[chapterBase].outline[3].outlineTable[2].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[2].tableData =
              mapGen;
          }
        }
        if (i == 3) {
          let new_data = JSON.parse(newAllChapter)[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.inistitutional_arrangement;
          this.chapters[chapterBase].outline[1].outlineModel =
            new_data.inventory_equipment_staff;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.source_reduction;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.collection;
          this.chapters[chapterBase].outline[4].outlineModel =
            new_data.transfer;
          this.chapters[chapterBase].outline[5].outlineModel =
            new_data.processing_facilities;
          this.chapters[chapterBase].outline[6].outlineModel =
            new_data.final_disposal;
          this.chapters[chapterBase].outline[7].outlineModel =
            new_data.special_wastes;
          this.chapters[chapterBase].outline[8].outlineModel =
            new_data.health_care_wastes;
          this.chapters[chapterBase].outline[9].outlineModel =
            new_data.market_for_recyclables;
          this.chapters[chapterBase].outline[10].outlineModel = new_data.iec;
          this.chapters[chapterBase].outline[11].outlineModel =
            new_data.cost_and_revenue;
          this.chapters[chapterBase].outline[12].outlineModel =
            new_data.key_issues;

          // Checking and populatings For table 311
          if (new_data.agencies) {
            this.chapters[chapterBase].outline[0].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[0].outlineTable[0].tableData =
