    setAttachment() {
      let newAllAttach = JSON.stringify(this.allAttach);
      let new_data = JSON.parse(newAllAttach);
      //var chapterBase = chapter - 1;
      if (new_data) {
        let count = new_data.length;

        for (let i = 0; i < count; i++) {
          let [b] = [new_data[i]].map((e, i) => ({
            ...e,
            file_path_name: JSON.parse(e.file_path_name).map((e, i) => ({
              ...e,
              new_name: `http://localhost/swms_api/uploads/${e.new_name}`,
              ext: e.new_name.split(".").pop().toLowerCase(),
              //new_name: `http://swm.emb.gov.ph/swms_api/uploads/${e.new_name}`,
            })),
          }));
          this.chapters[new_data[i].chapter - 1].outline[
            new_data[i].outline
          ].uploads[0].curr_data = b.file_path_name;
        }
      }
    },

    fetchchapters(header_id, chapter) {
      this.fetchAllPerChapter({
        header_id: header_id,
      }).then(() => {
        for (var i = 1; i < 13; i++) {
          this.setChapterDetails(i);
        }
      });
    },

    fetchAttachement(header_id, chapter) {
      this.fetchAttachAllPerChapter({
        header_id: header_id,
      }).then(() => {
        this.setAttachment();
      });
    },

