    pushProjectedYear(event) {
      this.projected_year = [];
      if (this.chapters[1].outline[2].outlineTable[1].tableHeader.length > 3) {
        this.chapters[1].outline[2].outlineTable[1].tableHeader.splice(1, 10);
      }

      let populationFrom232 = "";
      for (let i = 0; i < 10; i++) {
        this.projected_year.push(event);

        //For table and edit form 232
        this.chapters[1].outline[2].outlineTable[1].tableHeader.splice(
          i + 2,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
            divider: true,
            total: "column" + i,
          }
        );

        //For table 432
        this.chapters[3].outline[2].outlineTable[1].tableData.push({
          year: event,
          wasteGeneration: "",
          totalKg: "",
          totalTon: "",
          totalCubic: "",
        });

        //For table 621
        //populationFrom232 = document.getElementById("1-column" + i);
        // var text = populationFrom232.innerText || populationFrom232.textContent;
        // element.innerHTML = text;
        //console.log(populationFrom232);
        this.chapters[5].outline[1].outlineTable[0].tableData.push({
          year: event,
          projPopulation: "",
          wasteGen: "",
          wasteDiv: "",
          wasteDis: "",
        });

        //For table 761
        this.chapters[6].outline[17].outlineTable[0].tableData.push({
          year: event,
          projectedDis: "",
          capacityRes: "",
        });

        //For table 771
        this.chapters[6].outline[21].outlineTable[0].tableData.push({
          year: event,
          estimatedQty: 0,
          collection: "",
          storageTreatment: "",
        });

        //For table 7711
        this.chapters[6].outline[22].outlineTable[0].tableData.push({
          year: event,
          estimatedQty: "",
          collection: "",
          storageTreatment: "",
        });

        //For table and edit form 1111
        this.chapters[10].outline[0].outlineTable[0].tableHeader.splice(
          i + 1,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
          }
        );
        //For table and edit form 1121
        this.chapters[10].outline[1].outlineTable[0].tableHeader.splice(
          i + 1,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
          }
        );

        //For table and edit form 1122
        this.chapters[10].outline[1].outlineTable[1].tableHeader.splice(
          i + 1,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
          }
        );

        //For table and edit form 1131
        this.chapters[10].outline[2].outlineTable[0].tableHeader.splice(
          i + 1,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
          }
        );
        //For table and edit form 1141
        this.chapters[10].outline[3].outlineTable[0].tableHeader.splice(
          i + 1,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
          }
        );

        //For table and edit form 1151
        this.chapters[10].outline[4].outlineTable[0].tableHeader.splice(
          i + 1,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
          }
        );

        //For table and edit form 1231
        this.chapters[11].outline[2].outlineTable[0].tableHeader.splice(
          i + 3,
          0,
          {
            text: event,
            value: "column" + i,
            sortable: false,
          }
        );

        //For table and edit form 821
        this.chapters[7].outline[1].outlineTable[0].tableData.push({
          year: event,
          wasteGen: "",
          projectedPop: "",
          DailyWasteGen: "",
          targetWasteDiv: "",
        });

        event++;
      }
    },
    previous() {
      this.e1--;
      this.scrollTop();
    },
    scrollTop() {
      setTimeout(() => {
        window.scrollTo({
          top: 0,
          behavior: "smooth",
        });
      }, 500);
    },
    getNow() {
      const today = new Date();
      const date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + " " + time;
      return dateTime;
    },
    getRegEvaluation() {
      this.fetchRegForm3({
        id: sessionStorage.getItem("typ_header_id"),
      }).then(() => {
        this.setForm3Details();
        this.snackbar = true;
      });
    },
  },
  computed: {
    ...mapGetters(["allForm3", "allChapter", "allAttach"]),
  },
  watch: {},
};
</script>
