<template>
  <v-container class="pa-1">
    <v-card class="mx-auto pa-4">
      <template>
        <div class="text-h4">SWM System</div>
        <br />
        <v-alert dense outlined type="info">
          Data Tables for<strong> Chapter 7</strong>
        </v-alert>
        <div>
          <v-container v-for="(table, tableIndex) in tables" :key="tableIndex">
            <!--START SET UP OF TABLE WITH LOOP FOR HEADERS AND TABLE DATA-->

            <v-data-table
              v-if="!table.notShow"
              dense
              :headers="table.tableHeader"
              :items="table.tableData"
              :items-per-page="-1"
              :hide-default-footer="true"
              class="elevation-1 my_class2"
              style="white-space: pre-line"
              loading="false"
              loading-text="No Data"
            >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title class="text-subtitle-2">{{
                    table.tableTitle
                  }}</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <v-spacer></v-spacer>
                  <template v-if="table.tableAddNew > 0">
                    <v-btn
                      color="primary"
                      dark
                      class="mb-2"
                      @click="addItem(tableIndex, table.tableName)"
                    >
                      New Item
                    </v-btn>
                  </template>
                </v-toolbar>
              </template>
              <!-- v-if="table.tableName == '231'" -->

              <template slot="body.append" v-if="table.withTotal == 1">
                <tr>
                  <td class="text-subtitle-2">Total</td>
                  <td
                    class="text-subtitle-2"
                    v-for="x in nonNullItems(tableIndex)"
                    :key="x"
                    :id="`${tableIndex}-${x}`"
                  >
                    {{ sumField(x, tableIndex) }}
                  </td>
                </tr>
              </template>
              <template slot="body.append" v-if="table.withTotalPer == 1">
                <tr>
                  <td class="text-subtitle-2">Total %</td>
                  <td
                    class="text-subtitle-2"
                    v-for="x in nonNullItems(tableIndex)"
                    :key="x"
                    :id="`${tableIndex}-${x}`"
                  >
                    {{ sumFieldPer(x, tableIndex) }}
                  </td>
                </tr>
              </template>
              <!--START OF CREATING ACTIONS PER ROW-->
              <template
                v-slot:[`item.actions`]="{ item }"
                v-if="table.nonAction == null"
              >
                <v-icon
                  small
                  class="mr-2"
                  @click="editItem(tableIndex, table.tableName, item)"
                >
                  mdi-pencil
                </v-icon>
                <v-icon
                  small
                  @click="deleteItem(tableIndex, item)"
                  v-if="table.tableAddNew > 0"
                >
                  mdi-delete
                </v-icon>
              </template>
              <!--END OF CREATING ACTIONS PER ROW-->
            </v-data-table>
            <!--END SET UP OF TABLE WITH LOOP FOR HEADERS AND TABLE DATA-->
            <br />
          </v-container>
          <v-dialog v-model="dialogEdit" :retain-focus="false">
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <template v-if="dialogEdit">
                    <EditModal
                      :dialogEdit="dialogEdit"
                      :editFormObject="editFormObject"
                      :editFormIndex="editFormIndex"
                    />
                  </template>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeModal">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="save"> Enter </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </div>
        <br />
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="saveProfile">Save</v-btn>
        </v-card-actions>
      </template>
    </v-card>
  </v-container>
</template>
    

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import HistoryViewModal from "../Include/history_view";
import EditModal from "./Modal/edit_modal";
export default {
  name: "ChapSeven",
  components: {
    HistoryViewModal,
    EditModal,
  },
  props: ["editFormObject", "tab"],
  data: () => ({
    editTable: "",
    dialogEdit: false,
    editFormIndex: -1,
    editedIndex: -1,

    tables: [
      {
        tableTitle: "7.1.1	Source reduction programs to be implemented.",
        tableName: "711",
        tableHeader: [
          {
            text: "Source Reduction program",
            value: "sourceReduction",
            sortable: false,
          },
          {
            text: "Materials to be Addressed",
            value: "matAddress",
            sortable: false,
          },
          {
            text: "Target sectors",
            value: "targetSector",
            sortable: false,
          },
          {
            text: "Schedule of Implementation",
            value: "schedImplment",
            sortable: false,
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 1,
        tableModel: "711",
      },
      {
        tableTitle:
          "7.2.1.1	Type of waste to be collected and schedule of collection per waste source.",
        tableName: "7211",
        tableHeader: [
          {
            text: "Source of Waste",
            value: "sourceWaste",
            sortable: false,
          },
          {
            text: "Types of Waste to be Collected",
            value: "typeWaste",
            sortable: false,
          },
          {
            text: "Collection Schedule",
            value: "collectionSched",
            sortable: false,
          },
          {
            text: "Department Concern",
            value: "departmentConcern",
            sortable: false,
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 1,
        tableModel: "7211",
      },
      {
        tableTitle: "7.2.2.1 Table listing current number of vehicles.",
        tableName: "7221",
        tableHeader: [
          {
            text: "Type of Equipment",
            value: "typeEquip",
            sortable: false,
          },
          {
            text: "Capacity",
            value: "capacity",
            sortable: false,
          },
          {
            text: "Present Condition",
            value: "presentCondition",
            sortable: false,
          },
          {
            text: "Make",
            value: "make",
            sortable: false,
          },
          {
            text: "Model",
            value: "model",
            sortable: false,
          },
          {
            text: " Year Model",
            value: "yearModel",
            sortable: false,
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 1,
        tableModel: "7221",
      },
      {
        tableTitle:
          "7.2.3.1 Listing of service areas, types of waste to be collected, location where waste will be taken Private Hauler.",
        tableName: "7231",
        tableHeader: [
          {
            text: "Collection Area",
            value: "collectionArea",
            sortable: false,
          },
          {
            text: "Type of Waste",
            value: "typeWaste",
            sortable: false,
          },
          {
            text: "Location of Disposal Facility",
            value: "locationDis",
            sortable: false,
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 1,
        tableModel: "7231",
      },
      {
        tableTitle:
          "7.2.4.1 Types of containers to be used for each generator.",
        tableName: "7241",
        tableHeader: [
          {
            text: "Waste Source",
            value: "wasteSource",
            sortable: false,
          },
          {
            text: "Kind/Type of Receptacle",
            value: "typeReceptacle",
            sortable: false,
          },
          {
            text: "Remarks",
            value: "remarks",
            sortable: false,
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 1,
        tableModel: "7241",
      },
      {
        tableTitle:
          "7.6.1 Projection of the amount of disposal capacity needed to accommodate residual wastes.",
        tableName: "761",
        tableHeader: [
          {
            text: "Year",
            value: "year",
            sortable: false,
          },
          {
            text: "Projected disposed wastes (kg/year)",
            value: "projectedDis",
            sortable: false,
          },
          {
            text: "Capacity to accommodate residuals m2(Bulk density is 175kg/m3)",
            value: "capacityRes",
            sortable: false,
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 0,
        tableModel: "761",
      },
      {
        tableTitle:
          "7.7.1 Storage, collection, treatment, disposal and other appropriate technologies for any special wastes .",
        tableName: "771",
        tableHeader: [
          {
            text: "Year",
            value: "year",
            sortable: false,
          },
          {
            text: "Estimated Quantities Generated",
            value: "estimatedQty",
            sortable: false,
          },
          {
            text: "Collection",
            value: "collection",
            sortable: false,
          },
          {
            text: "Storage, Treatment and Disposal",
            value: "storageTreatment",
            sortable: false,
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 0,
        tableModel: "771",
      },
      {
        tableTitle:
          "7.7.1.1 Plan for treatment/disposal and estimated quantities of health care wastes to be generated in the future.",
        tableName: "7711",
        tableHeader: [
          {
            text: "Year",
            value: "year",
            sortable: false,
          },
          {
            text: "Estimated Quantities Generated",
            value: "estimatedQty",
            sortable: false,
          },
          {
            text: "Collection",
            value: "collection",
            sortable: false,
          },
          {
            text: "Storage, Treatment and Disposal",
            value: "storageTreatment",
            sortable: false,
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 0,
        tableModel: "7711",
      },
      {
        tableTitle: "7.8.2.1 Target Audiences and Core Messages.",
        tableName: "7821",
        tableHeader: [
          {
            text: "Sector",
            value: "sector",
            sortable: false,
          },
          {
            text: "Core Messages",
            value: "coreMessages",
            sortable: false,
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 1,
        tableModel: "7711",
      },
      {
        tableTitle: "7.8.3.1 •	Matrix of planned activities.",
        tableName: "7831",
        tableHeader: [
          {
            text: "Activity",
            value: "activity",
            sortable: false,
          },
          {
            text: "Target Audience",
            value: "targetAudience",
            sortable: false,
          },
          {
            text: "Implementation Schedule",
            value: "implementationSched",
            sortable: false,
          },
          {
            text: "Partner Agencies/Responsible",
            value: "partnerAgencies",
            sortable: false,
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 1,
        tableModel: "7831",
      },
    ],
  }),
  methods: {
    ...mapActions([
      "saveProfile7",
      "fetchProfile",
      "fetchPopulation",
      "fetchBarangay",
    ]),
    ...mapMutations([]),

    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      9, 150.2;
      return parts.join(".");
    },
    nonNullItems(index) {
      let x = this.tables[index].tableHeader
        .filter((e) => {
          return e.total;
        })
        .map((e) => {
          return e.total;
        });
      return x;
    },
    sumField(key, index) {
      // sum data in give key (property)
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid) {
        let value = this.tables[index].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        return this.numberWithCommas(value.toFixed(2));
      }
    },
    sumFieldPer(key, index) {
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid && key != "totalKg") {
        let value = this.tables[index].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        let total = this.tables[index].tableData.reduce(
          (a, b) =>
            parseFloat(a) + (parseFloat(b["totalKg"].replace(/,/g, "")) || 0),
          0
        );
        let per = (value / total) * 100;
        if (isNaN(per)) {
          per = 0;
        }
        return this.numberWithCommas(per.toFixed(2)) + "%";
      }
    },

    // TABLE FUNCTION

    addItem(index, tableName) {
      // this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editTable = index;
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );
      this.dialogEdit = true;
    },
    editItem(index, tableName, item) {
      // this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editTable = index;
      //Finding index of relative object from outline table to edit form object
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );
      //Finding index of row in table data
      this.editedIndex = this.tables[index].tableData.indexOf(item);

      //Assign row data to Edit Forms
      let new_item = JSON.parse(JSON.stringify(item));
      this.editFormObject[this.editFormIndex].editableForm =
        this.editFormObject[this.editFormIndex].editableForm.map((e, i) => ({
          ...e,
          value: new_item[e.model],
        }));

      this.dialogEdit = true;
    },
    deleteItem(index, item) {
      // this.editTable = tableName;
      this.tables[index].tableData.splice(
        this.tables[index].tableData.indexOf(item),
        1
      );
      // this.dialogDelete = true;
    },
    setDefaultEdit() {
      let editFormLengthObject =
        this.editFormObject[this.editFormIndex].editableForm.length;
      for (let i = 0; i < editFormLengthObject; i++) {
        this.editFormObject[this.editFormIndex].editableForm[i].value = "";
      }
    },
    closeModal() {
      this.setDefaultEdit();
      this.dialogEdit = false;

      this.$nextTick(() => {
        this.editedIndex = -1;
        this.editFormIndex = -1;
      });
    },
    save() {
      let m = [];
      m = this.editFormObject[this.editFormIndex].editableForm.map((e) => ({
        [e.model]: e.value,
      }));
      let merged = m.reduce((obj, item) => ({ ...obj, ...item }), {});
      if (this.editedIndex > -1) {
        Object.assign(
          this.tables[this.editTable].tableData[this.editedIndex],
          merged
        );
      } else {
        this.tables[this.editTable].tableData.push(merged);
      }

      this.setDefaultEdit();
      this.closeModal();
    },

    // TABLE FUNCTION

    pushProjectedYear() {
      for (let i = 1; i <= 10; i++) {
        //For table 432
        this.tables[5].tableData.push({
          year: "Year " + i,
          projectedDis: "",
          capacityRes: "",
        });

        this.tables[6].tableData.push({
          year: "Year " + i,
          estimatedQty: "",
          collection: "",
          storageTreatment: "",
        });

        this.tables[7].tableData.push({
          year: "Year " + i,
          estimatedQty: "",
          collection: "",
          storageTreatment: "",
        });
      }
    },

    saveProfile() {
      this.saveProfile7({
        user: this.getStateSession["iis_employee_number"],
        lgu: localStorage.getItem("city_id"),
        tbl_711: JSON.stringify(this.tables[0].tableData),
        tbl_7211: JSON.stringify(this.tables[1].tableData),
        tbl_7221: JSON.stringify(this.tables[2].tableData),
        tbl_7231: JSON.stringify(this.tables[3].tableData),
        tbl_7241: JSON.stringify(this.tables[4].tableData),
        tbl_761: JSON.stringify(this.tables[5].tableData),
        tbl_771: JSON.stringify(this.tables[6].tableData),
        tbl_7711: JSON.stringify(this.tables[7].tableData),
        tbl_7821: JSON.stringify(this.tables[8].tableData),
        tbl_7831: JSON.stringify(this.tables[9].tableData),
      });
    },

    getProfile() {
      this.fetchProfile({
        user: this.getStateSession["iis_employee_number"],
        lgu: localStorage.getItem("city_id"),
      }).then(() => {
        let profile = [];
        profile = JSON.stringify(this.allProfile);

        if (profile.length > 3) {
          let new_data = JSON.parse(profile);
          console.log(new_data.tbl_511);
          if (new_data.tbl_711) {
            let table3data = JSON.parse(new_data.tbl_711);
            this.tables[0].tableData = table3data;
          }
          if (new_data.tbl_7211) {
            let table4data = JSON.parse(new_data.tbl_7211);
            this.tables[1].tableData = table4data;
          }
          if (new_data.tbl_7221) {
            let table3data = JSON.parse(new_data.tbl_7221);
            this.tables[2].tableData = table3data;
          }
          if (new_data.tbl_7231) {
            let table4data = JSON.parse(new_data.tbl_7231);
            this.tables[3].tableData = table4data;
          }
          if (new_data.tbl_7241) {
            let table3data = JSON.parse(new_data.tbl_7241);
            this.tables[4].tableData = table3data;
          }
          if (new_data.tbl_761) {
            let table4data = JSON.parse(new_data.tbl_761);
            this.tables[5].tableData = table4data;
          }
          if (new_data.tbl_771) {
            let table3data = JSON.parse(new_data.tbl_771);
            this.tables[6].tableData = table3data;
          }
          if (new_data.tbl_7711) {
            let table4data = JSON.parse(new_data.tbl_7711);
            this.tables[7].tableData = table4data;
          }
          if (new_data.tbl_7821) {
            let table3data = JSON.parse(new_data.tbl_7821);
            this.tables[8].tableData = table3data;
          }
          if (new_data.tbl_7831) {
            let table4data = JSON.parse(new_data.tbl_7831);
            this.tables[9].tableData = table4data;
          }
        }
      });
    },
  },
  computed: {
    ...mapGetters([
      "allProfile",
      "getStateSession",
      "allPopulation",
      "allBarangay",
    ]),
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },
  created() {
    this.getProfile();
    this.pushProjectedYear();
  },
  watch: {
    tab() {
      if (this.tab == 1) {
        this.getProfile();
      }
    },
  },
};
</script>