<template>
  <v-stepper alt-labels v-model="e1">
    <!-- START OF STEPPER CONTENT-->
    <v-stepper-items>
      <v-stepper-content
        v-for="(chapterContent, chapterIndex) in chapters"
        :key="chapterIndex"
        :step="chapterIndex + 1"
      >
        <div
          :id="'print-chapter-' + (chapterIndex + 1)"
          class="section-to-print"
        >
          <!-- PRINT LOOP FOR CHAPTER TITLE-->
          <div
            class="text-h4 text-center"
            v-text="chapterContent.chapterTitle"
          ></div>
          <!-- PRINT LOOP FOR CHAPTER TITLE-->
          <br />

          <!-- PRINT LOOP CHAPTER SUB TITLE-->
          <div
            class="text-h5 text-center"
            v-text="chapterContent.chapterSubTitle"
          ></div>
          <!-- PRINT LOOP CHAPTER SUB TITLE-->
          <br />

          <!--START LOOP OF OUTLINE PER CHAPTER-->
          <v-container
            v-for="(outline, outlineIndex) in chapterContent.outline"
            :key="outlineIndex"
          >
            <p class="text-h6">{{ outline.outlineTitle }}</p>
            <p>{{ outline.outlineModel }}</p>

            <!--START ATTACHMENT PER OUTLINE-->
            <v-container
              class="not-to-print"
              v-for="(upload, uploadIndex) in outline.uploads"
              :key="'up' + uploadIndex"
            >
              <v-row id="not-to-print" class="not-to-print">
                <v-col md="4">
                  <v-menu offset-y>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn color="primary" dark v-bind="attrs" v-on="on">
                        Attachments
                      </v-btn>
                    </template>
                    <v-list>
                      <!-- :href="item.new_name" -->
                      <template v-for="(item, index) in upload.curr_data">
                        <v-list-item
                          :key="`fle` + index"
                          :href="item.new_name"
                          v-if="item.ext == `pdf`"
                          target="_blank"
                          :download="item.new_name"
                        >
                          <v-list-item-title>
                            {{ item.orig_name }}</v-list-item-title
                          >
                          <!-- <v-btn color="primary" fab x-small class="ml-3">
                          <v-icon>mdi-eye</v-icon>
                        </v-btn> -->
                          <!-- <v-btn color="error" fab x-small class="ml-5">
                            <v-icon>mdi-delete-alert-outline</v-icon>
                          </v-btn> -->
                        </v-list-item>
                      </template>
                    </v-list>
                  </v-menu>
                </v-col>
                <!-- <div class="p" v-text="upload.curr_data"></div> -->
              </v-row>

              <v-container>
                <v-row>
                  <template v-for="(item, index) in upload.curr_data">
                    <v-col
                      :key="`img` + index"
                      v-if="item.ext == `jpg`"
                      class="d-flex child-flex"
                      cols="4"
                    >
                      <v-img
                        :src="item.new_name"
                        max-width="287"
                        aspect-ratio="1"
                        class="grey lighten-2"
                      >
                        <template v-slot:placeholder>
                          <v-row
                            class="fill-height ma-0"
                            align="center"
                            justify="center"
                          >
                            <v-progress-circular
                              indeterminate
                              color="grey lighten-5"
                            ></v-progress-circular>
                          </v-row>
                        </template>
                      </v-img>
                    </v-col>
                  </template>
                </v-row>
              </v-container>
            </v-container>
            <!--START ATTACHMENT PER OUTLINE-->

            <!-- PRINT TEXTAREA OF OUTLINE PER CHAPTER-->

            <!--START LOOP OF TABLE PER OUTLINE-->
            <v-container
              v-for="(table, tableIndex) in outline.outlineTable"
              :key="tableIndex"
            >
              <br />
              <!--START SET UP OF TABLE WITH LOOP FOR HEADERS AND TABLE DATA-->
              <template v-if="table.tableName == '821'">
                <v-data-table
                  :headers="table.tableHeader"
                  :items="table.tableData"
                  :hide-default-footer="true"
                  class="elevation-1"
                  style="white-space: pre-line"
                  dense
                >
                  <template v-slot:top>
                    <v-toolbar flat>
                      <v-toolbar-title class="text-subtitle-2">{{
                        table.tableTitle
                      }}</v-toolbar-title>
                      <v-divider class="mx-4" inset vertical></v-divider>
                      <v-spacer></v-spacer>
                      <template v-if="table.tableAddNew > 0">
                        <v-btn
                          color="primary"
                          dark
                          class="mb-2"
                          @click="
                            addItem(
                              chapterIndex,
                              outlineIndex,
                              tableIndex,
                              table.tableName
                            )
                          "
                        >
                          New Item
                        </v-btn>
                      </template>
                    </v-toolbar>
                  </template>
                  <!-- <template v-slot:header="props">
                  <thead>
                    <tr>
                      <th colspan="2">Category 1</th>
                      <th colspan="3">Category 2</th>
                    </tr>
                  </thead>
                </template> -->
                  <!--START OF CREATING ACTIONS PER ROW-->
                  <template v-slot:[`item.actions`]="{ item }">
                    <v-icon
                      small
                      class="mr-2"
                      @click="
                        editItem(
                          chapterIndex,
                          outlineIndex,
                          tableIndex,
                          table.tableName,
                          item
                        )
                      "
                    >
                      mdi-pencil
                    </v-icon>
                    <v-icon
                      small
                      @click="
                        deleteItem(chapterIndex, outlineIndex, tableIndex, item)
                      "
                      v-if="table.tableAddNew > 0"
                    >
                      mdi-delete
                    </v-icon>
                  </template>
                  <!--END OF CREATING ACTIONS PER ROW-->
                  <!-- <template slot="body.append">
                <tr v-html="footerTotal()" class="pink--text"></tr>
              </template> -->
                </v-data-table>
              </template>

              <template v-else>
                <v-data-table
                  dense
                  :headers="filterHeader(table.tableHeader)"
                  :items="table.tableData"
                  :items-per-page="-1"
                  :hide-default-footer="true"
                  class="elevation-1"
                  style="white-space: pre-line"
                  loading="false"
                  loading-text="No Data"
                >
                  <!-- v-if="table.tableName == '231'" -->
                  <template v-slot:top>
                    <v-toolbar flat>
                      <v-toolbar-title class="text-subtitle-2">{{
                        table.tableTitle
                      }}</v-toolbar-title>
                      <v-divider class="mx-4" inset vertical></v-divider>
                      <v-spacer></v-spacer>
                    </v-toolbar>
                  </template>
                  <template slot="body.append" v-if="table.withTotal == 1">
                    <tr>
                      <td class="text-subtitle-2">Total</td>
                      <td
                        class="text-subtitle-2"
                        v-for="x in nonNullItems(
                          chapterIndex,
                          outlineIndex,
                          tableIndex
                        )"
                        :key="x"
                        :id="`${tableIndex}-${x}`"
                      >
                        {{
                          sumField(chapterIndex, outlineIndex, tableIndex, x)
                        }}
                      </td>
                    </tr>
                  </template>
                  <template slot="body.append" v-if="table.withTotalPer == 1">
                    <tr>
                      <td class="text-subtitle-2">Total %</td>
                      <td
                        class="text-subtitle-2"
                        v-for="x in nonNullItems(
                          chapterIndex,
                          outlineIndex,
                          tableIndex
                        )"
                        :key="x"
                        :id="`${tableIndex}-${x}`"
                      >
                        {{
                          sumFieldPer(chapterIndex, outlineIndex, tableIndex, x)
                        }}
                      </td>
                    </tr>
                  </template>

                  <!-- <template slot="body.append">
                <tr v-html="footerTotal()" class="pink--text"></tr>
              </template> -->
                </v-data-table>
              </template>
              <!--END SET UP OF TABLE WITH LOOP FOR HEADERS AND TABLE DATA-->
              <br />
            </v-container>
            <!--END LOOP OF TABLE PER OUTLINE-->
          </v-container>
          <!--END LOOP OF OUTLINE PER CHAPTER-->
        </div>
        <!--CARD FOOTER-->
        <v-card-actions>
          <v-btn
            @click="previous"
            v-if="chapterIndex > 0 && chapterIndex < chapters.length - 1"
            >Back
          </v-btn>
          <v-spacer></v-spacer>
          <!-- v-print="'#print-chapter-' + (chapterIndex + 1)" -->
          <!-- <v-btn color="success" @click="print(chapterIndex + 1)"
            ><v-icon right dark> mdi-printer </v-icon>Print</v-btn
          > -->
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            @click="next"
            v-if="chapterIndex < chapters.length - 2"
            >Next</v-btn
          >
        </v-card-actions>
        <!--CARD FOOTER-->
      </v-stepper-content>
    </v-stepper-items>
    <!-- END OF STEPPER CONTENT-->
  </v-stepper>
</template>

<script>
import Vue from "vue";
import { mapGetters, mapActions } from "vuex";
import VueHtmlToPaper from "vue-html-to-paper";
import Print from "vue-print-nb";
const options = {
  name: "_blank",
  specs: ["fullscreen=yes", "titlebar=yes", "scrollbars=yes"],
  styles: [
    // "https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css",
    "./public/styles/create_plan_style.scss",
    "https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css",
    "https://unpkg.com/kidlat-css/css/kidlat.css",
  ],
  timeout: 1000, // default timeout before the print window appears
  autoClose: true, // if false, the window will not close after printing
  windowTitle: window.document.title, // override the window title
};
Vue.use(VueHtmlToPaper, options);
Vue.use(Print);
export default {
  components: {},
  props: ["chapters", "editFormObject"],
  data() {
    return {
      //e1: 1,
      file: "",
      name: "Planning",
      starting_year: "",
      projected_year: [],
      year: "",

      loader: {},
      loading: false,
    };
  },
  created() {
    console.log("asdasd");
    var year = sessionStorage.getItem("startingYear");
    this.pushProjectedYear(year);
    this.starting_year = year;

    this.fetchPerChapter({
      city: this.$route.query.city,
      header_id: this.$route.query.header_id,
      chapter: this.$route.query.chapter,
    }).then(() => {
      this.setChapterDetails();
    });
    this.fetchAttachPerChapter({
      city: this.$route.query.city,
      header_id: this.$route.query.header_id,
      chapter: this.$route.query.chapter,
    }).then(() => {
      this.setAttachment();
    });
  },
  methods: {
    ...mapActions(["previouss", "fetchPerChapter", "fetchAttachPerChapter"]),
    getNextChap(num) {
      this.fetchPerChapter({
        city: sessionStorage.getItem("city_id"),
        header_id: sessionStorage.getItem("header_id"),
        chapter: num,
      }).then(() => {
        this.setChapterDetails();
      });
    },
    setChapterDetails() {
      let newAllChapter = [];
      newAllChapter = JSON.stringify(this.allChapter);
      if (newAllChapter.length >= 3) {
        var chapterBase = this.e1 - 1;

        if (this.e1 == 1) {
          let new_data = JSON.parse(newAllChapter)[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.introduction;
          this.chapters[chapterBase].outline[1].outlineModel = new_data.purpose;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.approach;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.acknowledgement;
        }
        if (this.e1 == 2) {
          let new_data = JSON.parse(newAllChapter)[0];
          //let new_data = new_data1[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.location;
          this.chapters[chapterBase].outline[1].outlineModel = new_data.history;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.population;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.economic_profile_land_use;
          this.chapters[chapterBase].outline[4].outlineModel =
            new_data.physical_characteristic;

          // Checking and populatings For table 231
          if (new_data.current_population_barangay) {
            let curPop = JSON.parse(new_data.current_population_barangay);
            this.chapters[chapterBase].outline[2].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[2].outlineTable[0].tableData =
              curPop;
          }

          // Checking and populating For table 232
          if (new_data.ten_year_projected_p) {
            let tenYear = JSON.parse(new_data.ten_year_projected_p);
            this.chapters[chapterBase].outline[2].outlineTable[1].tableData =
              [];
            this.chapters[chapterBase].outline[2].outlineTable[1].tableData =
              tenYear;
          }

          // Checking and populating  For table 241
          if (new_data.ten_year_projected_p) {
            let listInd = JSON.parse(new_data.list_industries_within_city);
            this.chapters[chapterBase].outline[3].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[0].tableData =
              listInd;
          }

          // Checking and populating  For table 242
          if (new_data.land_used_map_urban) {
            let mapUrban = JSON.parse(new_data.land_used_map_urban);
            this.chapters[chapterBase].outline[3].outlineTable[1].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[1].tableData =
              mapUrban;
          }

          // Checking and populating  For table 243
          if (new_data.land_used_map_urban) {
            let mapGen = JSON.parse(new_data.land_used_map_general);
            this.chapters[chapterBase].outline[3].outlineTable[2].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[2].tableData =
              mapGen;
          }
        }
        if (this.e1 == 3) {
          let new_data = JSON.parse(newAllChapter)[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.inistitutional_arrangement;
          this.chapters[chapterBase].outline[1].outlineModel =
            new_data.inventory_equipment_staff;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.source_reduction;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.collection;
          this.chapters[chapterBase].outline[4].outlineModel =
            new_data.transfer;
          this.chapters[chapterBase].outline[5].outlineModel =
            new_data.processing_facilities;
          this.chapters[chapterBase].outline[6].outlineModel =
            new_data.final_disposal;
          this.chapters[chapterBase].outline[7].outlineModel =
            new_data.special_wastes;
          this.chapters[chapterBase].outline[8].outlineModel =
            new_data.health_care_wastes;
          this.chapters[chapterBase].outline[9].outlineModel =
            new_data.market_for_recyclables;
          this.chapters[chapterBase].outline[10].outlineModel = new_data.iec;
          this.chapters[chapterBase].outline[11].outlineModel =
            new_data.cost_and_revenue;
          this.chapters[chapterBase].outline[12].outlineModel =
            new_data.key_issues;

          // Checking and populatings For table 311
          if (new_data.agencies) {
            this.chapters[chapterBase].outline[0].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[0].outlineTable[0].tableData =
              JSON.parse(new_data.agencies);
          }

          // Checking and populatings For table 321
          if (new_data.list_existing_equipment) {
            this.chapters[chapterBase].outline[1].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[1].outlineTable[0].tableData =
              JSON.parse(new_data.list_existing_equipment);
          }

          // Checking and populatings For table 322
          if (new_data.number_of_personnel) {
            this.chapters[chapterBase].outline[1].outlineTable[1].tableData =
              [];
            this.chapters[chapterBase].outline[1].outlineTable[1].tableData =
              JSON.parse(new_data.number_of_personnel);
          }

          // Checking and populatings For table 323
          if (new_data.staff_training) {
            this.chapters[chapterBase].outline[1].outlineTable[2].tableData =
              [];
            this.chapters[chapterBase].outline[1].outlineTable[2].tableData =
              JSON.parse(new_data.staff_training);
          }

          // Checking and populatings For table 341
          if (new_data.daily_collection) {
            this.chapters[chapterBase].outline[3].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[0].tableData =
              JSON.parse(new_data.daily_collection);
          }

          // Checking and populatings For table 342
          if (new_data.monthly_collection) {
            this.chapters[chapterBase].outline[3].outlineTable[1].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[1].tableData =
              JSON.parse(new_data.monthly_collection);
          }

          // Checking and populatings For table 371
          if (new_data.final_dis_facilities) {
            this.chapters[chapterBase].outline[6].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[6].outlineTable[0].tableData =
              JSON.parse(new_data.final_dis_facilities);
          }

          // Checking and populatings For table 381
          if (new_data.special_waste_table) {
            this.chapters[chapterBase].outline[7].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[7].outlineTable[0].tableData =
              JSON.parse(new_data.special_waste_table);
          }

          // Checking and populatings For table 3811
          if (new_data.health_care_wastes_table) {
            this.chapters[chapterBase].outline[8].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[8].outlineTable[0].tableData =
              JSON.parse(new_data.health_care_wastes_table);
          }

          // Checking and populatings For table 391
          if (new_data.junkshop_list) {
            this.chapters[chapterBase].outline[9].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[9].outlineTable[0].tableData =
              JSON.parse(new_data.junkshop_list);
          }

          // Checking and populatings For table 3101
          if (new_data.iec_activities) {
            this.chapters[chapterBase].outline[10].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[10].outlineTable[0].tableData =
              JSON.parse(new_data.iec_activities);
          }
        }
        if (this.e1 == 4) {
          let new_data = JSON.parse(newAllChapter)[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.disposed_waste;
          this.chapters[chapterBase].outline[1].outlineModel =
            new_data.diverted_waste;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.generated_waste;

          // Checking and populatings For table 411
          if (new_data.disposed_waste_qty) {
            this.chapters[chapterBase].outline[0].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[0].outlineTable[0].tableData =
              JSON.parse(new_data.disposed_waste_qty);
          }

          // Checking and populatings For table 412
          if (new_data.sector_disposed_waste) {
            this.chapters[chapterBase].outline[0].outlineTable[1].tableData =
              [];
            this.chapters[chapterBase].outline[0].outlineTable[1].tableData =
              JSON.parse(new_data.sector_disposed_waste);
          }

          // Checking and populatings For table 421
          if (new_data.diverted_waste_qty) {
            this.chapters[chapterBase].outline[1].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[1].outlineTable[0].tableData =
              JSON.parse(new_data.diverted_waste_qty);
          }

          // Checking and populatings For table 431
          if (new_data.generated_waste_qty) {
            this.chapters[chapterBase].outline[2].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[2].outlineTable[0].tableData =
              JSON.parse(new_data.generated_waste_qty);
          }

          // Checking and populatings For table 432
          if (new_data.generated_waste_projected) {
            this.chapters[chapterBase].outline[2].outlineTable[1].tableData =
              [];
            this.chapters[chapterBase].outline[2].outlineTable[1].tableData =
              JSON.parse(new_data.generated_waste_projected);
          }
        }
        if (this.e1 == 5) {
          let new_data = JSON.parse(newAllChapter)[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.local_laws_regulations;
          this.chapters[chapterBase].outline[1].outlineModel = new_data.roles;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.city_municipality_swmb;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.barangay_swmc;
          this.chapters[chapterBase].outline[4].outlineModel =
            new_data.stakeholders_particpation;

          //Tables
          // Checking and populatings For table 511
          if (new_data.local_laws_list) {
            this.chapters[chapterBase].outline[0].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[0].outlineTable[0].tableData =
              JSON.parse(new_data.local_laws_list);
          }
          // Checking and populatings For table 531
          if (new_data.eswm_board_list) {
            this.chapters[chapterBase].outline[2].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[2].outlineTable[0].tableData =
              JSON.parse(new_data.eswm_board_list);
          }
          // Checking and populatings For table 541
          if (new_data.monitoring_program_data) {
            this.chapters[chapterBase].outline[3].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[3].outlineTable[0].tableData =
              JSON.parse(new_data.monitoring_program_data);
          }
        }
        if (this.e1 == 6) {
          let new_data = JSON.parse(newAllChapter)[0];
          this.chapters[chapterBase].outline[0].outlineModel = new_data.vision;
          this.chapters[chapterBase].outline[1].outlineModel = new_data.targets;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.strategies;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.strat_collection;
          this.chapters[chapterBase].outline[4].outlineModel =
            new_data.strat_disposal;
          this.chapters[chapterBase].outline[5].outlineModel =
            new_data.strat_other;

          // Checking and populatings For table 621
          if (new_data.target_per_year) {
            this.chapters[chapterBase].outline[1].outlineTable[0].tableData =
              [];
            this.chapters[chapterBase].outline[1].outlineTable[0].tableData =
              JSON.parse(new_data.target_per_year);
          }
        }
        if (this.e1 == 7) {
          let new_data = JSON.parse(newAllChapter)[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.source_reduction;
          this.chapters[chapterBase].outline[1].outlineModel =
            new_data.collection;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.overview;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.equipment_routes;
          this.chapters[chapterBase].outline[4].outlineModel =
            new_data.private_collection_service;
          this.chapters[chapterBase].outline[5].outlineModel =
            new_data.storage_setout;
          this.chapters[chapterBase].outline[6].outlineModel =
            new_data.segrated_recyclables;
          this.chapters[chapterBase].outline[7].outlineModel =
            new_data.segregated_compostables;
          this.chapters[chapterBase].outline[8].outlineModel =
            new_data.mixed_sw_residual;
          this.chapters[chapterBase].outline[9].outlineModel =
            new_data.segregation_recycling_composing;
