              <td>
                <p>Above 50% by year</p>
              </td>
              <td>
                <v-icon v-if="indicator2[2].radio1 == 0">🗸</v-icon>
              </td>
              <td>
                <v-icon v-if="indicator2[2].radio1 == 1">🗸</v-icon>
              </td>
              <td rowspan="2">
                <p>{{ indicator2[2].remarks1 }}</p>
                <table id="innerTable" dense>
                  <thead>
                    <tr>
                      <th>Year</th>
