              </td>
            </tr>
            <!-- 4 -->
            <tr>
              <td>
                <b>4. Segregation at Source and Segregated Collection</b>
              </td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
            <!-- 4.1 -->
            <tr>
              <td>
                <p>100% target for the 10th year planning period</p>
              </td>
              <td>
                <v-icon v-if="indicator2[3].radio1 == 0">🗸</v-icon>
              </td>
              <td>
                <v-icon v-if="indicator2[3].radio1 == 1">🗸</v-icon>
              </td>
              <td>
