      <!-- <vue-html2pdf
        :show-layout="false"
        :float-layout="true"
        :enable-download="false"
        :preview-modal="true"
        :paginate-elements-by-height="1400"
        filename="hee hee"
        :pdf-quality="2"
        :manual-pagination="false"
        pdf-format="a4"
        pdf-orientation="portrait"
        pdf-content-width="800px"
        ref="html2Pdf"
      > -->
      <!-- FORM 2 PRINT -->
      <section slot="pdf-content" id="printMe2">
        <div>
          <div class="text-h6 center">EMB-{{ regName }}</div>
          <div class="text-h6 center">Evaluated on {{ evalDate }}</div>
          <br />
          <table style="collapse; border: 0 solid black; width: 98%">
            <tbody>
              <tr>
                <td
                  style="collapse; border: 0 solid black; width: 50%;font-size: 15px;"
                >
                  Prepared By:
                </td>
                <td
                  style="collapse; border: 0 solid black; width: 50%;font-size: 15px;"
                >
                  Noted By:
                </td>
              </tr>
              <tr
                style="
                  border: 0 solid black;
                  width: 50%;
                  height: 100px;
                  font-size: 15px;
                "
              >
                <td style="border: 0 solid black; vertical-align: middle">
                  <b> {{ regEvaluator }}</b>
                </td>
                <td style="border: 0 solid black; vertical-align: middle">
                  <b> {{ regChief }}</b>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div v-if="this.form_id != undefined">
          <div class="text-h6 center">EMB-CENTRAL OFFICE</div>
          <div class="text-h6 center">
            Evaluated on November 18 and 24, 2021
          </div>
          <br />
          <table style="collapse; border: 0 solid black; width: 98%">
            <tbody>
              <tr>
                <td
                  style="collapse; border: 0 solid black; width: 50%;font-size: 15px;"
                >
                  Prepared By:
                </td>
                <td
                  style="collapse; border: 0 solid black; width: 50%;font-size: 15px;"
                >
                  Noted By:
                </td>
              </tr>
              <tr
                style="
                  border: 0 solid black;
                  width: 50%;
                  height: 100px;
                  font-size: 15px;
                "
              >
                <td style="border: 0 solid black; vertical-align: middle">
                  <!-- <img
                    style="width: 300px"
                    src="http://localhost/swms_api/public/file_375158.jpg"
                  /> -->
                  <br />
                  <b> {{ centralEvaluator }}</b>
                </td>
                <td style="border: 0 solid black; vertical-align: middle">
                  <b> {{ centralChief }}</b>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </section>
      <!-- </vue-html2pdf> -->
    </section>
  </v-container>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
let basePath = window.location.origin;
export default {
  components: {},
  props: ["header_id", "form_id"],
  data: () => ({
    name: "Form2Modal",
    indicator2: [
      {
        radio1: null,
        radio2: null,
        radio3: null,
        radio4: null,
        radio5: null,
        radio6: null,
        radio7: null,
        remarks1: null,
        remarks2: null,
        remarks3: null,
        remarks4: null,
        capacity: null,
        location: null,
        fdLocation: null,
        lifeSpan: null,
        fdStatus: null,
      },
      {
        radio1: null,
        remarks1: null,
      },
      {
        radio1: null,
        radio2: null,
        remarks1: null,
      },
      {
        radio1: null,
        remarks1: null,
      },
      {
        radio1: null,
        radio2: null,
        remarks1: null,
        remarks2: null,
      },
      {
        radio1: null,
        remarks1: null,
      },
      {
        radio1: null,
        remarks1: null,
      },
    ],
    cityName: "",
    provName: "",
    regName: "",
    evalDate: "",
    revDate: "",
    EvalBy: "",
    EvalNotedBy: "",
    RevBy: "",
    RevNotedBy: "",
    monthNames: [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ],
    wasteDiv: "",

    regEvaluator: "",
    regChief: "",

    centralEvaluator: "",
    centralChief: "",
  }),
  created() {
    if (this.form_id != undefined) {
      this.fetchForm2({
        id: this.form_id,
      }).then(() => {
        this.setForm2Details();
        this.dialogPrint = true;
        this.getWasteDiv();
        this.getSignatory();
      });
    } else {
      this.fetchRegForm2({
        id: this.header_id,
      }).then(() => {
        this.setForm2Details();
        this.dialogPrint = true;
        this.getWasteDiv();
        this.getSignatory();
        let regEval = JSON.stringify(this.allForm2);
        this.$emit("update", JSON.parse(regEval));
      });
    }
  },
  methods: {
    ...mapActions([
      "fetchRegForm2",
      "fetchForm2",
      "fetchWasteDiv",
      "fetchSignatory2",
    ]),
    setForm2Details() {
      let regEval = JSON.stringify(this.allForm2);
      let new_data1 = JSON.parse(regEval);
      let new_data = new_data1[0];
      if (new_data != null) {
        this.indicator2[0].radio1 = new_data.indicator_com_1_1;
        this.indicator2[0].radio2 = new_data.indicator_com_1_2;
        this.indicator2[0].radio3 = new_data.indicator_com_1_3;
        this.indicator2[0].radio4 = new_data.memo_agreement;
        this.indicator2[0].radio5 = new_data.contract;
        this.indicator2[0].radio6 = new_data.indicator_com_1_4;
        this.indicator2[0].radio7 = new_data.disposal_area;
        this.indicator2[0].remarks1 = new_data.indicator_rem_1_1;
        this.indicator2[0].remarks2 = new_data.indicator_rem_1_2;
        this.indicator2[0].remarks3 = new_data.indicator_rem_1_3;
        this.indicator2[0].remarks4 = new_data.indicator_rem_1_4;

        this.indicator2[0].capacity = new_data.capacity;
        this.indicator2[0].location = new_data.location;
        this.indicator2[0].fdLocation = new_data.fdLocation;
        this.indicator2[0].lifeSpan = new_data.lifeSpan;
        this.indicator2[0].fdStatus = new_data.fdStatus;

        this.indicator2[1].radio1 = new_data.indicator_com_2_1;
        this.indicator2[1].remarks1 = new_data.indicator_rem_2_1;

        this.indicator2[2].radio1 = new_data.indicator_com_3_1;
        this.indicator2[2].radio2 = new_data.indicator_com_3_1;
        this.indicator2[2].remarks1 = new_data.indicator_rem_3_1;

        this.indicator2[3].radio1 = new_data.indicator_com_4_1;
        this.indicator2[3].remarks1 = new_data.indicator_rem_4_1;

        this.indicator2[4].radio1 = new_data.indicator_com_5_1;
        this.indicator2[4].radio2 = new_data.indicator_com_5_2;
        this.indicator2[4].remarks1 = new_data.indicator_rem_5_1;
        this.indicator2[4].remarks2 = new_data.indicator_rem_5_2;

        this.indicator2[5].radio1 = new_data.indicator_com_6_1;
        this.indicator2[5].remarks1 = new_data.indicator_rem_6_1;

        this.indicator2[6].radio1 = new_data.indicator_com_7_1;
        this.indicator2[6].remarks1 = new_data.indicator_rem_7_1;

        this.cityName = new_data.city_name;
        this.provName = new_data.prov_name;
        this.regName = new_data.reg_name;

        this.evalDate = this.convertDate(new_data.evaluate_date);
      }
    },
    generateReport() {
      this.$htmlToPaper("printMe");
    },
    convertDate(date) {
      const date2 = new Date(date);
      //const month = date.toLocaleString("default", { month: "long" });
      //return date.getMonth(long);
      return (
        this.monthNames[date2.getMonth()] +
        " " +
        date2.getDate() +
        ", " +
        date2.getFullYear()
      );
    },
    getWasteDiv() {
      this.fetchWasteDiv(this.header_id).then(() => {
        let x = JSON.parse(this.allwasteDiv);
        if (x.length != 0) {
          let y = JSON.parse(x[0]["target_per_year"]);
          let m = y.map((e, i) => ({
            year: e.year,
            value: e.wasteDiv,
            valuePer: e.wasteDivPer,
          }));
          this.wasteDiv = m;
        }
      });
    },

    getSignatory() {
      this.fetchSignatory2({
        id: this.header_id,
      }).then(() => {
        this.setSignatory();
      });
    },

    setSignatory() {
      let signatory = JSON.stringify(this.allSignatory);
      let new_data = JSON.parse(signatory);
      for (let i = 0; i < new_data.length; i++) {
        if (new_data[i]["eval"] == 1) {
          this.regEvaluator = new_data[i]["eval_name"];
          this.regChief = new_data[i]["chief_name"];
        } else {
          this.centralEvaluator = new_data[i]["eval_name"];
          this.centralChief = new_data[i]["chief_name"];
        }
      }
    },
  },
  computed: { ...mapGetters(["allForm2", "allwasteDiv", "allSignatory"]) },
};
</script>