<template>
  <v-container class="pa-1">
    <v-toolbar dense color="cyan" dark>
      <v-toolbar-title>USER MANAGEMENT</v-toolbar-title>
    </v-toolbar>
    <v-card dense class="mx-auto card-padding pa-4">
      <template>
        <v-dialog v-model="dialogAdd" persistent max-width="600px">
          <template v-slot:activator="{ on, attrs }">
            <v-row>
              <v-col>
                <v-row>
                  <v-col>
                    <v-text-field
                      dense
                      label="Search Bar"
                      v-model="userSearch"
                      @keyup="getSearch"
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-combobox
                      dense
                      v-model="limitRow"
                      label="Show"
                      :items="allLimit"
                      @change="limitFunction"
                    >
                    </v-combobox>
                  </v-col>
                </v-row>
                <v-btn small color="primary" dark v-bind="attrs" v-on="on">
                  Add New
                </v-btn>
              </v-col>
              <v-col>
                <!-- <v-btn @click="getSearch">Search</v-btn> -->
                <v-btn
                  small
                  class="float-right mini-margin-left"
                  @click="nextFunction"
                  >Next</v-btn
                >
                <v-btn small class="float-right" @click="prevFunction"
                  >Prev</v-btn
                >
                <input v-model="getCurrenrow" type="hidden" />
              </v-col>
            </v-row>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">Add New User</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="6">
                    <v-text-field
                      label="Search by First or Last Name"
                      v-model="iisemployee"
                      required
                    >
                    </v-text-field>
                  </v-col>
                  <v-col cols="1">
                    <v-btn
                      @click="getEmployee"
                      icon
                      style="margin: 20px 0px 0px 0px !important"
                    >
                      <v-icon>mdi-magnify</v-icon>
                    </v-btn>
                  </v-col>
                  <v-col cols="5">
                    <v-switch
                      flat
                      @change="switch1 = 1 - switch1"
                      :label="`${switch1 == 1 ? 'CRS' : 'IIS'}`"
                    ></v-switch>
                  </v-col>
                  <v-col cols="12">
                    <v-simple-table class="mini-margin-top">
                      <template v-slot:default>
                        <thead>
                          <tr>
                            <th>Selection</th>
                            <th>Employee Number</th>
                            <th>Name</th>
                            <th>Region</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="iis in allIisEmployee" :key="iis.id">
                            <td>
                              <v-btn
                                color="info"
                                depressed
                                x-small
                                @click="selectIISEmp(iis)"
                                fab
                              >
                                <v-icon>mdi-selection-multiple</v-icon>
                              </v-btn>
                            </td>
                            <td>{{ iis.id }}</td>
                            <td>{{ iis.name }}</td>
                            <td>{{ iis.region }}</td>
                          </tr>
                        </tbody>
                      </template>
                    </v-simple-table>
                  </v-col>
                  <v-col cols="6">
                    <v-text-field
                      label="Employee Number *"
                      v-model="iis_employee_number"
                      required
                      readonly
                    >
                    </v-text-field>
                  </v-col>
                  <v-col cols="6">
                    <v-text-field
                      label="Full Name *"
                      v-model="name"
                      required
                      readonly
                    >
                    </v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-combobox
                      :items="allAccess"
                      v-model="user_access_level"
                      label="User Type"
                      :disabled="isDisable"
                      outlined
                      dense
                    >
                    </v-combobox>
                    <v-select
                      v-if="switch1 == 0"
                      :items="adminStatus"
                      v-model="switch2"
                      label="Administrator"
                      outlined
                      dense
                    >
                      <option
                        v-for="option in adminStatus"
                        v-bind:key="option.value"
                      >
                        {{ option.text }}
                      </option>
                    </v-select>
                  </v-col>
                </v-row>
              </v-container>
              <small>*indicates required field</small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogAdd = false"
                >Close</v-btn
              >
              <v-btn color="blue darken-1" text @click="insertUser">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- Modal Update  -->
      <template>
        <v-dialog v-model="dialogEdit" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline">Update User Details</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      label="Employee Number *"
                      v-model="iis_employee_number_edit"
                      required
                      readonly
                    >
                    </v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      label="Full Name *"
                      v-model="name_edit"
                      required
                      readonly
                    >
                    </v-text-field>
                  </v-col>
                  <v-col cols="6">
                    <v-select
                      :readonly="ifLGU(this.user_access_level_edit)"
                      :items="allAccess"
                      v-model="user_access_level_edit"
                      label="Responsibility"
                      outlined
                      dense
                    >
                      <option
                        v-for="option in allAccess"
                        v-bind:key="option.value"
                      >
                        {{ option.text }}
                      </option>
                    </v-select>
                  </v-col>
                  <v-col cols="6">
                    <v-select
                      :items="itemStatus"
                      v-model="active"
                      label="Status"
                      outlined
                      dense
                    >
                      <option
                        v-for="option in itemStatus"
                        v-bind:key="option.value"
                      >
                        {{ option.text }}
                      </option>
                    </v-select>
                  </v-col>
                  <v-col cols="6">
                    <v-select
                      v-if="switch1 == 0"
                      :items="adminStatus"
                      v-model="switch2"
                      label="Administrator"
                      outlined
                      dense
                    >
                      <option
                        v-for="option in adminStatus"
                        v-bind:key="option.value"
                      >
                        {{ option.text }}
                      </option>
                    </v-select>
                  </v-col>
                  <v-col cols="6">
                    <v-select
                      v-if="this.user_access_level_edit == 6"
                      :items="allGroup"
                      v-model="group"
                      label="Group"
                      outlined
                      dense
                    >
                      <!-- <option
                        v-for="group in allGroup"
                        v-bind:key="group.value"
                      >
                        {{ group.text }}
                      </option> -->
                    </v-select>
                  </v-col>
                </v-row>
              </v-container>
              <!-- <small>*indicates required field</small> -->
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogEdit = false"
                >Close</v-btn
              >
              <v-btn color="blue darken-1" text @click="updateUser"
                >Update</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- Modal Delete  -->
      <template>
        <v-dialog v-model="dialogDelete" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <h2>Are sure want to delete ?</h2>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogDelete = false"
                >No</v-btn
              >
              <v-btn color="info darken-1" text @click="deleteUser">Yes</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <template>
        <v-simple-table class="mini-margin-top">
          <template v-slot:default>
            <thead>
              <tr>
                <th>Actions</th>
                <!-- <th>#</th> -->
                <th>Name</th>
                <!-- <th>Origin Employee Number</th> -->
                <th>Region</th>
                <th>Access Level</th>
                <th>Administrator</th>
                <th>Origin</th>
                <th>Status</th>
                <th>Default Receiver</th>
                <th>Group</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="user in allUsers" :key="user.id">
                <td>
                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        color="info"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="getEdit(user)"
                      >
                        <v-icon small>mdi-pencil</v-icon>
                      </v-btn>
                    </template>
                    <span>Edit</span>
                  </v-tooltip>

                  <!-- <v-btn
                    color="error"
                    depressed
                    fab
                    x-small
                    @click="getDelete(user)"
                  >
                    <v-icon>mdi-delete-alert-outline</v-icon>
                  </v-btn> -->
                </td>

                <td>{{ user.name }}</td>
                <td>{{ user.rgnnam2 }}</td>
                <td>{{ user.access_level }}</td>
                <td>
                  <v-icon
                    :class="`ma-2 ml-0 font-weight-light pr-4 ${setColor(
                      user.admin
                    )}--text`"
                    >{{
                      user.admin == 0
                        ? "mdi-close-circle"
                        : "mdi-checkbox-marked-circle"
                    }}</v-icon
                  >
                </td>
                <td>{{ user.origin == 1 ? "CRS" : "IIS" }}</td>
                <td>
                  <v-icon
                    :class="`ma-2 ml-0 font-weight-light pr-4 ${setColor(
                      user.active
                    )}--text`"
                    >{{
                      user.active == 0
                        ? "mdi-close-circle"
                        : "mdi-checkbox-marked-circle"
                    }}</v-icon
                  >
                </td>
                <td>
                  <v-icon
                    :class="`ma-2 ml-0 font-weight-light pr-4 ${setColor(
                      user.default_receiver
                    )}--text`"
                    >{{
                      user.default_receiver == 0
                        ? "mdi-close-circle"
                        : "mdi-checkbox-marked-circle"
                    }}</v-icon
                  >
                </td>
                <td>{{ user.group_name }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </template>
    </v-card>
  </v-container>
</template>
<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "UserManagement",
  data() {
    return {
      dialogAdd: false,
      dialogEdit: false,
      dialogDelete: false,
      user_access_level: "",
      iis_employee_number: "",
      region_id: "",
      name: "",
      switch1: 0,
      switch2: 0,
      active: 0,
      group: 0,
      itemStatus: [
        {
          value: 0,
          text: "Inactive",
        },
        {
          value: 1,
          text: "Active",
        },
      ],
      adminStatus: [
        {
          value: 1,
          text: "Yes",
        },
        {
          value: 0,
          text: "No",
        },
      ],
      userSearch: "",
      userID: "",
      user_access_level_edit: "",
      iis_employee_number_edit: "",
      name_edit: "",
      allLimit: [5, 10, 20, 50, 100],
      limitRow: 10,
      iisemployee: "",
      isDisable: false,
    };
  },
  methods: {
    ...mapActions([
      "fetchUsers",
      "createUser",
      "increment",
      "decrement",
      "searchUsers",
      "updateUsers",
      "deleteUsers",
      "fetchAccess",
      "getIssEmployee",
      "fetchGroup",
    ]),
    selectIISEmp(iis) {
      this.iis_employee_number = iis.id;
      this.name = iis.name;
      this.region_id = iis.regid;
    },
    insertUser() {
      this.createUser({
        iis_employee_number: this.iis_employee_number,
        name: this.name,
        region: this.region_id,
        admin: this.switch2,
        user_access_level: this.user_access_level,
        offset: this.getCurrenrow,
        limit: this.limitRow,
        origin: this.switch1,
        id: this.getStateSession["id"],
      });

      this.iis_employee_number = "";
      this.name = "";
      // this.user_access_level = "";
    },
    nextFunction() {
      this.increment({
        offset: this.getCurrenrow,
        limit: this.limitRow,
      });
      this.fetchUsers({
        offset: this.getCurrenrow,
        limit: this.limitRow,
        iis_employee_number: this.getStateSession["id"],
      });
    },
    prevFunction() {
      this.decrement({
        offset: this.getCurrenrow,
        limit: this.limitRow,
      });
      this.fetchUsers({
        offset: this.getCurrenrow,
        limit: this.limitRow,
        iis_employee_number: this.getStateSession["id"],
      });
    },
    getSearch() {
      this.searchUsers({
        txtsearch: this.userSearch,
        limit: this.limitRow,
        iis_employee_number: this.getStateSession["id"],
      });
    },
    getEdit(user) {
      this.iis_employee_number_edit = user.iis_employee_number;
      this.userID = user.id;
      this.active = parseInt(user.active);
      this.name_edit = user.name;
      this.switch1 = user.origin;
      this.switch2 = parseInt(user.admin);
      this.user_access_level_edit = user.user_access_level;
      this.dialogEdit = true;
      this.group = user.group;
      if (this.user_access_level_edit == 6) {
        this.fetchGroup({
          reg_id: 0,
        });
      }
    },
    updateUser() {
      this.updateUsers({
        id: this.userID,
        editorId: this.getStateSession["id"],
        // name: this.name_edit,
        admin: this.switch2,
        active: this.active,
        user_access_level: this.user_access_level_edit,
        offset: this.getCurrenrow,
        limit: this.limitRow,
        group: this.group,
      });
    },
    getDelete(user) {
      this.userID = user.id;
      this.dialogDelete = true;
    },
    deleteUser() {
      this.deleteUsers({
        id: this.userID,
        offset: this.getCurrenrow,
        limit: this.limitRow,
        iis_employee_number: this.getStateSession["id"],
      });
      this.dialogDelete = false;
    },
    limitFunction() {
      this.fetchUsers({
        offset: this.getCurrenrow,
        limit: this.limitRow,
        iis_employee_number: this.getStateSession["id"],
      });
    },
    getEmployee() {
      this.getIssEmployee({
        name: this.iisemployee,
        origin: this.switch1,
        region: this.getStateSession["region_id"],
        super_admin: this.getStateSession["super_admin"],
      });
    },
    setColor(res) {
      if (res == 0) {
        return "error";
      } else if (res == 1) {
        return "success";
      }
    },

    ifLGU(data) {
      if (data == 2) {
        return true;
      } else {
        return false;
      }
    },
  },
  computed: mapGetters([
    "allUsers",
    "allGroup",
    "getCurrenrow",
    "allAccess",
    "allIisEmployee",
    "getStateSession",
  ]),
  created() {
    this.fetchUsers({
      offset: this.getCurrenrow,
      limit: this.limitRow,
      iis_employee_number: this.getStateSession["id"],
    });
    this.fetchAccess();
  },
  watch: {
    switch1(val) {
      if (val == 1) {
        this.isDisable = true;
        this.user_access_level = {
          text: "LGU Account",
          value: "2",
        };
      } else {
        this.isDisable = false;
        this.user_access_level = "";
      }
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
};
</script>