<template>
  <v-container>
    <template>
      <v-simple-table class="my_class">
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-left">Date Process</th>
              <th class="text-left">From</th>
              <th class="text-left">Action Taken</th>
              <th class="text-left">To</th>
              <th class="text-left">Remarks</th>
              <!-- <th class="text-left">Status</th> -->
              <th class="text-left">Attachments</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, i) in newHistoryData" :key="i">
              <td>{{ convertDate(item.createdDate) }}</td>

              <td>
                {{ item.sender_name }} - <b>{{ setRole(item.sender_role) }}</b>
              </td>

              <td
                :class="` font-weight-light pr-4 ${setColor2(
                  item.action_taken
                )}--text`"
              >
                <b
                  >{{ item.action_taken }}
                  {{ item.action_taken != null ? "to" : "Approved" }}</b
                >
              </td>

              <td>
                {{
                  item.action_taken == "Assign"
                    ? item.group_name
                    : item.receiver_name
                }}
                -
                <b>{{
                  item.action_taken != null ? setRole(item.receiver_role) : ""
                }}</b>
              </td>

              <td>
                <p>{{ item.remarks }}</p>
              </td>

              <td>
                <v-menu
                  offset-y
                  dense
                  max-height="200"
                  v-if="item.files.length > 0"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      class="mx-1"
                      color="info"
                      v-bind="attrs"
                      v-on="on"
                      depressed
                      x-small
                    >
                      View
                      <v-icon small>mdi-menu-down-outline</v-icon>
                    </v-btn>
                  </template>
                  <v-list dense class="py-0">
                    <v-list-item
                      dense
                      v-for="(itemfiles, ifiles) in item.files"
                      :key="ifiles"
                      :href="itemfiles.file_path_name[0]['new_name']"
                      target="_blank"
                      :download="itemfiles.file_path_name[0]['new_name']"
                    >
                      <!-- :download="itemfiles.file_path_name[0]['new_name']" -->
                      {{ itemfiles.file_path_name[0]["orig_name"] }}
                    </v-list-item>
                  </v-list>
                </v-menu>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </template></v-container
  >
</template>
  </v-container>
</template>
<script>
import { mapGetters, mapActions } from "vuex";

export default {
  components: {},
  props: ["header_id"],
  data: () => ({
    name: "HistoryViewModal",

    items: [
      {
        color: "red",
        icon: "mdi-star",
        year: "1960",
      },
      {
        color: "purple",
        icon: "mdi-book-variant",
        year: "1970",
      },
      {
        color: "green",
        icon: "mdi-airballoon",
        year: "1980",
      },
      {
        color: "indigo",
        icon: "mdi-buffer",
        year: "1990",
      },
    ],
    monthNames: [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ],
    newHistoryData: "",
  }),
  created() {
    this.fetchHistory({
      header_id: this.header_id,
    }).then(() => {
      this.fetchAllTypAttach({
        header_id: this.header_id,
      }).then(() => {
        this.newHistoryData = JSON.parse(JSON.stringify(this.allHistory));
        // this.newHistoryData = this.allHistory;
        this.setAttachment();
      });
    });
  },
  methods: {
    ...mapActions(["fetchHistory", "fetchAllTypAttach"]),
    setColor2(res) {
      switch (res) {
        case "Submit":
          return "green";
          break;
        case "Forward":
          return "blue";
          break;
        case "Return":
          return "red";
          break;
        case "Assign":
          return "deep-orange";
          break;
        default:
          return "green";
      }
    },
    setColor(res) {
      switch (res) {
        case "1":
          return "green";
          break;
        case "2":
          return "blue";
          break;
        case "3":
          return "teal";
          break;
        case "4":
          return "brown";
          break;
        case "5":
          return "brown darken-4";
          break;
        default:
          return "light-green";
      }
    },
    setRole(res) {
      switch (res) {
        case "2":
          return "LGU";
          break;
        case "3":
          return "Regional Office";
          break;
        case "4":
          return "Central Office";
          break;
        case "5":
          return "NSWMC";
          break;
        default:
          return "NSWMC Evaluator";
      }
    },
    convertDate(date) {
      const date2 = new Date(date);
      //const month = date.toLocaleString("default", { month: "long" });
      //return date.getMonth(long);
      return (
        this.monthNames[date2.getMonth()] +
        " " +
        date2.getDate() +
        ", " +
        date2.getFullYear()
      );
    },
    getNow() {
      const today = new Date();
      const date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + " " + time;
      return dateTime;
    },
    setAttachment() {
      let newAllAttach = JSON.parse(JSON.stringify(this.allTypAttach));
      let count = this.newHistoryData.length;

      // for (let i = 0; i < count; i++) {
      this.newHistoryData = this.newHistoryData.map((e, i) => ({
        ...e,
        files: newAllAttach
          .filter((e2) => parseInt(e.route_order) === parseInt(e2.route) + 1)
          .map((e, i) => ({
            ...e,
            file_path_name: JSON.parse(e.file_path_name).map((e3, i) => ({
              ...e3,
              // new_name: `${this.globalBasepath}/swms_api/public/typ_file/${e.reference_number}/others/${e3.new_name}`,
              new_name: this.globalFileChangeNameOthers(
                this.globalBasepath,
                e.reference_number,
                e3.new_name
              ),
            })),
          })),
      }));
      // }
    },
  },
  computed: { ...mapGetters(["allHistory", "allTypAttach", "globalBasepath"]) },
};
</script>