<template>
  <v-container class="pa-1">
    <v-card class="mx-auto pa-4">
      <template>
        <div class="text-h4">WACS Characteristics</div>
        <br />
        <v-alert dense outlined type="info">
          Data Tables for<strong> Chapter 11</strong>
        </v-alert>

        <v-container v-for="(table, tableIndex) in tables" :key="tableIndex">
          <!--START SET UP OF TABLE WITH LOOP FOR HEADERS AND TABLE DATA-->

          <v-data-table
            v-if="!table.notShow"
            dense
            :headers="table.tableHeader"
            :items="table.tableData"
            :items-per-page="-1"
            :hide-default-footer="true"
            class="elevation-1 my_class2"
            style="white-space: pre-line"
            loading="false"
            loading-text="No Data"
          >
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title class="text-subtitle-2">{{
                  table.tableTitle
                }}</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <template v-if="table.tableAddNew > 0">
                  <v-btn
                    color="primary"
                    dark
                    class="mb-2"
                    @click="addItem(tableIndex, table.tableName)"
                  >
                    New Item
                  </v-btn>
                </template>
              </v-toolbar>
            </template>
            <!-- v-if="table.tableName == '231'" -->

            <template slot="body.append" v-if="table.withTotal == 1">
              <tr>
                <td class="text-subtitle-2">Total</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(tableIndex)"
                  :key="x"
                  :id="`${tableIndex}-${x}`"
                >
                  {{ sumField(x, tableIndex) }}
                </td>
              </tr>
            </template>
            <template slot="body.append" v-if="table.withTotalPer == 1">
              <tr>
                <td class="text-subtitle-2">Total %</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(tableIndex)"
                  :key="x"
                  :id="`${tableIndex}-${x}`"
                >
                  {{ sumFieldPer(tableIndex, x) }}
                </td>
              </tr>
            </template>
            <!--START OF CREATING ACTIONS PER ROW-->
            <template
              v-slot:[`item.actions`]="{ item }"
              v-if="table.nonAction == null"
            >
              <v-icon
                small
                class="mr-2"
                @click="editItem(tableIndex, table.tableName, item)"
              >
                mdi-pencil
              </v-icon>
              <v-icon
                small
                @click="deleteItem(tableIndex, item)"
                v-if="table.tableAddNew > 0"
              >
                mdi-delete
              </v-icon>
            </template>
            <!--END OF CREATING ACTIONS PER ROW-->
          </v-data-table>
          <!--END SET UP OF TABLE WITH LOOP FOR HEADERS AND TABLE DATA-->
          <br />
        </v-container>

        <v-dialog v-model="dialogEdit" :retain-focus="false">
          <v-card>
            <v-card-title>
              <span class="text-h5">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <template v-if="dialogEdit">
                  <EditModal
                    :dialogEdit="dialogEdit"
                    :editFormObject="editFormObject"
                    :editFormIndex="editFormIndex"
                  />
                </template>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeModal">
                Cancel
              </v-btn>
              <v-btn color="blue darken-1" text @click="save"> Enter </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <br />
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="saveProfile">Save</v-btn>
        </v-card-actions>
      </template>
    </v-card>
  </v-container>
</template>
    

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import HistoryViewModal from "../Include/history_view";
import EditModal from "./Modal/edit_modal";
export default {
  name: "costEstimate",
  components: {
    HistoryViewModal,
    EditModal,
  },
  props: ["editFormObject", "tab"],
  data: () => ({
    editTable: "",
    dialogEdit: false,
    editFormIndex: -1,
    editedIndex: -1,

    tables: [
      {
        tableTitle: "11.1.1 Investment cost.",
        tableName: "1111",
        tableHeader: [
          {
            text: "Investment",
            value: "investment",
            sortable: false,
            divider: true,
            total: null,
          },
          {
            text: "Total",
            value: "total",
            sortable: false,
            divider: true,
            total: "total",
          },
          {
            text: "Fund Source",
            value: "fundSource",
            sortable: false,
            divider: true,
            total: 2,
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 1,
        tableModel: "1111",
        withTotal: 1,
      },
      {
        tableTitle: "11.2.1 Annual Cost (Personnel Services).",
        tableName: "1121",
        tableHeader: [
          {
            text: "Items",
            value: "items",
            sortable: false,
            divider: true,
            total: null,
          },
          {
            text: "Total",
            value: "total",
            sortable: false,
            divider: true,
            total: "total",
          },
          {
            text: "Fund Source",
            value: "fundSource",
            sortable: false,
            divider: true,
            total: 2,
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 1,
        tableModel: "1121",
        withTotal: 1,
      },
      {
        tableTitle: "11.2.2 Annual Cost (Maintenance and Operating Services).",
        tableName: "1122",
        tableHeader: [
          {
            text: "Items",
            value: "items",
            sortable: false,
            divider: true,
            total: null,
          },
          {
            text: "Total",
            value: "total",
            sortable: false,
            divider: true,
            total: "total",
          },
          {
            text: "Fund Source",
            value: "fundSource",
            sortable: false,
            divider: true,
            total: 2,
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 1,
        tableModel: "1122",
        withTotal: 1,
      },
      {
        tableTitle: "11.3.1 Funding Options.",
        tableName: "1131",
        tableHeader: [
          {
            text: "Particular",
            value: "particular",
            sortable: false,
            divider: true,
            total: null,
          },
          {
            text: "Total",
            value: "total",
            sortable: false,
            divider: true,
            total: "total",
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 1,
        tableModel: "1131",
        withTotal: 1,
      },
      {
        tableTitle: "11.4.1 Cost Evaluation and Comparison.",
        tableName: "1141",
        tableHeader: [
          {
            text: "Particular",
            value: "particular",
            sortable: false,
            divider: true,
            total: null,
          },
          {
            text: "Total",
            value: "total",
            sortable: false,
            divider: true,
            total: "total",
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 1,
        tableModel: "1141",
        withTotal: 1,
      },
      {
        tableTitle: "11.5.1 Summary.",
        tableName: "1151",
        tableHeader: [
          {
            text: "Particular",
            value: "particular",
            sortable: false,
            divider: true,
            total: null,
          },
          {
            text: "Total",
            value: "total",
            sortable: false,
            divider: true,
            total: "total",
          },
          // { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 0,
        tableModel: "1151",
        withTotal: 1,
      },
    ],
  }),
  methods: {
    ...mapActions(["saveProfile11", "fetchProfile", "fetchPopulation"]),
    ...mapMutations([]),

    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      9, 150.2;
      return parts.join(".");
    },
    nonNullItems(index) {
      let x = this.tables[index].tableHeader
        .filter((e) => {
          return e.total;
        })
        .map((e) => {
          return e.total;
        });
      return x;
    },
    sumField(key, index) {
      // sum data in give key (property)
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid) {
        let value = this.tables[index].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        return this.numberWithCommas(value.toFixed(2));
      }
    },
    sumFieldPer(key, index) {
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid && key != "totalKg") {
        let value = this.tables[index].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        let total = this.tables[index].tableData.reduce(
          (a, b) =>
            parseFloat(a) + (parseFloat(b["totalKg"].replace(/,/g, "")) || 0),
          0
        );
        let per = (value / total) * 100;
        if (isNaN(per)) {
          per = 0;
        }
        return this.numberWithCommas(per.toFixed(2)) + "%";
      }
    },

    //START of TABLE FUNCTION
    addItem(index, tableName) {
      // this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editTable = index;
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );
      this.dialogEdit = true;
    },
    editItem(index, tableName, item) {
      // this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editTable = index;
      //Finding index of relative object from outline table to edit form object
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );
      //Finding index of row in table data
      this.editedIndex = this.tables[index].tableData.indexOf(item);

      //Assign row data to Edit Forms
      let new_item = JSON.parse(JSON.stringify(item));
      this.editFormObject[this.editFormIndex].editableForm =
        this.editFormObject[this.editFormIndex].editableForm.map((e, i) => ({
          ...e,
          value: new_item[e.model],
        }));

      this.dialogEdit = true;
    },
    deleteItem(index, item) {
      // this.editTable = tableName;
      this.tables[index].tableData.splice(
        this.tables[index].tableData.indexOf(item),
        1
      );
      // this.dialogDelete = true;
    },
    setDefaultEdit() {
      let editFormLengthObject =
        this.editFormObject[this.editFormIndex].editableForm.length;
      for (let i = 0; i < editFormLengthObject; i++) {
        this.editFormObject[this.editFormIndex].editableForm[i].value = "";
      }
    },
    closeModal() {
      this.setDefaultEdit();
      this.dialogEdit = false;

      this.$nextTick(() => {
        this.editedIndex = -1;
        this.editFormIndex = -1;
      });
    },
    save() {
      let m = [];
      m = this.editFormObject[this.editFormIndex].editableForm.map((e) => ({
        [e.model]: e.value,
      }));
      let merged = m.reduce((obj, item) => ({ ...obj, ...item }), {});
      if (this.editedIndex > -1) {
        Object.assign(
          this.tables[this.editTable].tableData[this.editedIndex],
          merged
        );
      } else {
        this.tables[this.editTable].tableData.push(merged);
      }

      this.setDefaultEdit();
      this.closeModal();
    },

    // END of TABLE FUNCTION

    pushProjectedYear() {
      for (let i = 0; i < 10; i++) {
        //For table and edit form 1111
        let x = i + 1;
        this.tables[0].tableHeader.splice(i + 1, 0, {
          text: "Year " + x,
          value: "column" + i,
          total: "column" + i,
          sortable: false,
          divider: true,
        });
        this.editFormObject[36].editableForm.splice(i + 3, 0, {
          label: "Year " + x.toString(),
          value: "",
          inputType: "v-text-field",
          editable: true,
          isNumber: true,
          model: "column" + i,
          divider: true,
        });

        //For table and edit form 1121
        this.tables[1].tableHeader.splice(i + 1, 0, {
          text: "Year " + x,
          value: "column" + i,
          sortable: false,
          divider: true,
          total: "column" + i,
        });
        this.editFormObject[37].editableForm.splice(i + 3, 0, {
          label: "Year " + x.toString(),
          value: "",
          inputType: "v-text-field",
          editable: true,
          isNumber: true,
          model: "column" + i,
        });

        //For table and edit form 1122
        this.tables[2].tableHeader.splice(i + 1, 0, {
          text: "Year " + x,
          value: "column" + i,
          sortable: false,
          divider: true,
          total: "column" + i,
        });
        this.editFormObject[38].editableForm.splice(i + 3, 0, {
          label: "Year " + x.toString(),
          value: "",
          inputType: "v-text-field",
          editable: true,
          isNumber: true,
          model: "column" + i,
        });

        //For table and edit form 1131
        this.tables[3].tableHeader.splice(i + 1, 0, {
          text: "Year " + x,
          value: "column" + i,
          sortable: false,
          divider: true,
          total: "column" + i,
        });
        this.editFormObject[39].editableForm.splice(i + 2, 0, {
          label: "Year " + x.toString(),
          value: "",
          inputType: "v-text-field",
          editable: true,
          isNumber: true,
          model: "column" + i,
        });

        //For table and edit form 1141
        this.tables[4].tableHeader.splice(i + 1, 0, {
          text: "Year " + x,
          value: "column" + i,
          sortable: false,
          divider: true,
          total: "column" + i,
        });
        this.editFormObject[40].editableForm.splice(i + 2, 0, {
          label: "Year " + x.toString(),
          value: "",
          inputType: "v-text-field",
          editable: true,
          isNumber: true,
          model: "column" + i,
        });

        //For table and edit form 1151
        this.tables[5].tableHeader.splice(i + 1, 0, {
          text: "Year " + x,
          value: "column" + i,
          sortable: false,
          divider: true,
          total: "column" + i,
        });
        this.editFormObject[41].editableForm.splice(i + 2, 0, {
          label: "Year " + x.toString(),
          value: "",
          inputType: "v-text-field",
          editable: true,
          isNumber: true,
          model: "column" + i,
        });
      }
    },

    saveProfile() {
      this.saveProfile11({
        user: this.getStateSession["iis_employee_number"],
        lgu: localStorage.getItem("city_id"),
        tbl_1111: JSON.stringify(this.tables[0].tableData),
        tbl_1121: JSON.stringify(this.tables[1].tableData),
        tbl_1122: JSON.stringify(this.tables[2].tableData),
        tbl_1131: JSON.stringify(this.tables[3].tableData),
        tbl_1141: JSON.stringify(this.tables[4].tableData),
        tbl_1151: JSON.stringify(this.tables[5].tableData),
      });
    },

    getProfile() {
      this.fetchProfile({
        user: this.getStateSession["iis_employee_number"],
        lgu: localStorage.getItem("city_id"),
      }).then(() => {
        let profile = [];
        profile = JSON.stringify(this.allProfile);

        if (profile.length > 3) {
          let new_data = JSON.parse(profile);

          if (new_data.tbl_1111) {
            let table1data = JSON.parse(new_data.tbl_1111);
            this.tables[0].tableData = table1data;
          }

          if (new_data.tbl_1121) {
            let table2data = JSON.parse(new_data.tbl_1121);
            this.tables[1].tableData = table2data;
          }

          if (new_data.tbl_1122) {
            let table3data = JSON.parse(new_data.tbl_1122);
            this.tables[2].tableData = table3data;
          }
          if (new_data.tbl_1131) {
            let table4data = JSON.parse(new_data.tbl_1131);
            this.tables[3].tableData = table4data;
          }
          if (new_data.tbl_1141) {
            let table5data = JSON.parse(new_data.tbl_1141);
            this.tables[4].tableData = table5data;
          }
          // if (new_data.tbl_1151) {
          //   let table6data = JSON.parse(new_data.tbl_1151);
          //   this.table6.tableData = table6data;
          // }
        }
      });
    },

    pushSummaryTable() {
      const new_array = [];
      var invCost = this.getInvCostTotal();
      var annCost = this.getAnnCostTotal();
      if (Object.keys(invCost).length != 0) {
        new_array.push(invCost);
      }
      if (Object.keys(annCost).length != 0) {
        new_array.push(annCost);
      }

      this.tables[5].tableData = new_array;
    },

    getInvCostTotal() {
      let new_data = this.tables[0].tableData;

      let props = [
        "column0",
        "column1",
        "column2",
        "column3",
        "column4",
        "column5",
        "column6",
        "column7",
        "column8",
        "column9",
        "total",
      ];

      new_data = new_data.map((e, i) => ({
        ...e,
        column0: e.column0 ? e.column0 : "0",
        column1: e.column1 ? e.column1 : "0",
        column2: e.column2 ? e.column2 : "0",
        column3: e.column3 ? e.column3 : "0",
        column4: e.column4 ? e.column4 : "0",
        column5: e.column5 ? e.column5 : "0",
        column6: e.column6 ? e.column6 : "0",
        column7: e.column7 ? e.column7 : "0",
        column8: e.column8 ? e.column8 : "0",
        column9: e.column9 ? e.column9 : "0",
        total: e.total ? e.total : "0",
      }));

      const result = new_data.reduce(
        (previousValue, currentValue) =>
          Object.fromEntries(
            props.map((column) => [
              column,
              (parseFloat(previousValue[column]) || 0) +
                parseFloat(currentValue[column].replace(/,/g, "")),
            ])
          ),
        {}
      );

      let newRes = Object.assign(
        {},
        ...Object.keys(result).map((k) => ({
          ["particular"]: "Total Investment Cost",
          [k]: this.numberWithCommas(result[k].toFixed(2)),
        }))
      );
      return newRes;
    },
    getAnnCostTotal() {
      let personnel = this.tables[1].tableData;
      let Maintenance = this.tables[2].tableData;
      let props = [
        "column0",
        "column1",
        "column2",
        "column3",
        "column4",
        "column5",
        "column6",
        "column7",
        "column8",
        "column9",
        "total",
      ];
      var arr3 = [...personnel, ...Maintenance];

      arr3 = arr3.map((e, i) => ({
        ...e,
        column0: e.column0 ? e.column0 : "0",
        column1: e.column1 ? e.column1 : "0",
        column2: e.column2 ? e.column2 : "0",
        column3: e.column3 ? e.column3 : "0",
        column4: e.column4 ? e.column4 : "0",
        column5: e.column5 ? e.column5 : "0",
        column6: e.column6 ? e.column6 : "0",
        column7: e.column7 ? e.column7 : "0",
        column8: e.column8 ? e.column8 : "0",
        column9: e.column9 ? e.column9 : "0",
        total: e.total ? e.total : "0",
      }));

      const result = arr3.reduce(
        (previousValue, currentValue) =>
          Object.fromEntries(
            props.map((column) => [
              column,
              (parseFloat(previousValue[column]) || 0) +
                parseFloat(currentValue[column].replace(/,/g, "")),
            ])
          ),
        {}
      );

      let newRes = Object.assign(
        {},
        ...Object.keys(result).map((k) => ({
          ["particular"]: "Total Annual Cost",
          [k]: this.numberWithCommas(result[k].toFixed(2)),
        }))
      );

      return newRes;
    },
  },
  computed: {
    ...mapGetters(["allProfile", "getStateSession", "allPopulation"]),
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
    watchTable1: function () {
      return this.tables[0].tableData;
    },
    watchTable2: function () {
      return this.tables[1].tableData;
    },
    watchTable3: function () {
      return this.tables[2].tableData;
    },
  },
  created() {
    this.getProfile();
    this.pushProjectedYear();
  },
  watch: {
    watchTable1: {
      handler(newValue, oldValue) {
        this.pushSummaryTable();
      },
      deep: true,
    },
    watchTable2: {
      handler(newValue, oldValue) {
        this.pushSummaryTable();
      },
      deep: true,
    },
    watchTable3: {
      handler(newValue, oldValue) {
        this.pushSummaryTable();
      },
      deep: true,
    },
    tab() {
      if (this.tab == 3) {
        this.getProfile();
      }
    },
    // "tables[0].tableData": {
    //   handler(newValue, oldValue) {
    //     this.pushSummaryTable();
    //   },
    //   deep: true,
    // },
    // "tables[1].tableData": {
    //   handler(newValue, oldValue) {
    //     this.pushSummaryTable();
    //   },
    //   deep: true,
    // },
    // "tables[2].tableData": {
    //   handler(newValue, oldValue) {
    //     this.pushSummaryTable();
    //   },
    //   deep: true,
    // },
  },
};
</script>