<template>
  <v-container class="pa-1">
    <v-card class="mx-auto pa-4">
      <template>
        <div class="text-h4">WACS Characteristics</div>
        <br />
        <v-alert dense outlined type="info">
          Data Tables for<strong> Chapter 4</strong>
        </v-alert>

        <div>
          <apexchart
            width="100%"
            height="300px"
            type="donut"
            :options="chartOptions2"
            :series="chartOptions2['data']"
          ></apexchart>
          <v-container v-for="(table, tableIndex) in tables" :key="tableIndex">
            <!--START SET UP OF TABLE WITH LOOP FOR HEADERS AND TABLE DATA-->

            <v-data-table
              v-if="!table.notShow"
              dense
              :headers="table.tableHeader"
              :items="table.tableData"
              :items-per-page="-1"
              :hide-default-footer="true"
              class="elevation-1 my_class2"
              style="white-space: pre-line"
              loading="false"
              loading-text="No Data"
            >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title class="text-subtitle-2">{{
                    table.tableTitle
                  }}</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <v-spacer></v-spacer>
                  <template v-if="table.tableAddNew > 0">
                    <v-btn
                      color="primary"
                      dark
                      class="mb-2"
                      @click="addItem(tableIndex, table.tableName)"
                    >
                      New Item
                    </v-btn>
                  </template>
                </v-toolbar>
              </template>
              <!-- v-if="table.tableName == '231'" -->

              <template slot="body.append" v-if="table.withTotal == 1">
                <tr>
                  <td class="text-subtitle-2">Total</td>
                  <td
                    class="text-subtitle-2"
                    v-for="x in nonNullItems(tableIndex)"
                    :key="x"
                    :id="`${tableIndex}-${x}`"
                  >
                    {{ sumField(x, tableIndex) }}
                  </td>
                </tr>
              </template>
              <template slot="body.append" v-if="table.withTotalPer == 1">
                <tr>
                  <td class="text-subtitle-2">Total %</td>
                  <td
                    class="text-subtitle-2"
                    v-for="x in nonNullItems(tableIndex)"
                    :key="x"
                    :id="`${tableIndex}-${x}`"
                  >
                    {{ sumFieldPer(x, tableIndex) }}
                  </td>
                </tr>
              </template>
              <!--START OF CREATING ACTIONS PER ROW-->
              <template
                v-slot:[`item.actions`]="{ item }"
                v-if="table.nonAction == null"
              >
                <v-icon
                  small
                  class="mr-2"
                  @click="editItem(tableIndex, table.tableName, item)"
                >
                  mdi-pencil
                </v-icon>
                <v-icon
                  small
                  @click="deleteItem(tableIndex, item)"
                  v-if="table.tableAddNew > 0"
                >
                  mdi-delete
                </v-icon>
              </template>
              <!--END OF CREATING ACTIONS PER ROW-->
            </v-data-table>
            <!--END SET UP OF TABLE WITH LOOP FOR HEADERS AND TABLE DATA-->
            <br />
          </v-container>
          <v-dialog v-model="dialogEdit" :retain-focus="false">
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <template v-if="dialogEdit">
                    <EditModal
                      :dialogEdit="dialogEdit"
                      :editFormObject="editFormObject"
                      :editFormIndex="editFormIndex"
                    />
                  </template>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeModal">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="save"> Enter </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </div>
        <br />
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="saveProfile">Save</v-btn>
        </v-card-actions>
      </template>
    </v-card>
  </v-container>
</template>
    

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import HistoryViewModal from "../Include/history_view";
import EditModal from "./Modal/edit_modal";
// import { VueApexCharts as apexchart } from "vue-apexcharts";
export default {
  name: "wacsProfile",
  components: {
    HistoryViewModal,
    EditModal,
  },
  props: ["editFormObject", "tab"],
  data: () => ({
    editTable: "",
    dialogEdit: false,
    editFormIndex: -1,
    editedIndex: -1,
    chartOptions2: {
      title: {
        text: "Chart based on Table 4.1.2",
        align: "center",
        margin: 10,
        offsetX: 0,
        offsetY: 0,
        floating: false,
        style: {
          fontSize: "14px",
          fontWeight: "bold",
          fontFamily: "Helvetica",
          color: "#263238",
        },
      },
      labels: ["Biodegradable", "Recyclable", "RWP", "For Disposal", "Special"],
      data: [],
      legend: {
        position: "bottom",
      },
    },
    tables: [
      {
        tableTitle:
          "4.1.2 Summary tables and figures showing the quantity and composition of disposed waste by sector.",
        tableName: "412",
        tableHeader: [
          {
            text: "Category / Sector",
            value: "catSector",
            sortable: false,
            total: null,
            divider: true,
          },
          {
            text: "Total (Kg/day)",
            value: "totalKg",
            sortable: false,
            total: "totalKg",
            divider: true,
          },
          {
            text: "Biodegradable Kg/day",
            value: "bioKg",
            sortable: false,
            total: "bioKg",
            divider: true,
          },
          {
            text: "%",
            value: "bioKgPer",
            sortable: false,
            total: "1",
            divider: true,
          },
          {
            text: "Recyclable Kg/day",
            value: "recKg",
            sortable: false,
            total: "recKg",
            divider: true,
          },
          {
            text: "%",
            value: "recKgPer",
            sortable: false,
            total: "2",
            divider: true,
          },
          {
            text: "RWP For Diversion Kg/day",
            value: "rwpKg",
            sortable: false,
            total: "rwpKg",
            divider: true,
          },
          {
            text: "%",
            value: "rwpKgPer",
            sortable: false,
            total: "3",
            divider: true,
          },
          {
            text: "For Disposal Kg/day",
            value: "forDisKg",
            sortable: false,
            total: "forDisKg",
            divider: true,
          },
          {
            text: "%",
            value: "forDisKgPer",
            sortable: false,
            total: "4",
            divider: true,
          },
          {
            text: "Special Kg/day",
            value: "specKg",
            sortable: false,
            total: "specKg",
            divider: true,
          },
          {
            text: "%",
            value: "specKgPer",
            sortable: false,
            total: "5",
            divider: true,
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 1,
        withTotal: 1,
        withTotalPer: 1,
        tableModel: "412",
      },
      {
        tableTitle:
          "4.2.1 Quantity of waste currently recycled and composted based on existing information.",
        tableName: "421",
        tableHeader: [
          {
            text: "Category / Sector",
            value: "catSector",
            sortable: false,
            total: null,
          },
          {
            text: "Biodegradable Kg/day",
            value: "bioKg",
            sortable: false,
            total: "bioKg",
          },
          // {
          //   text: "%",
          //   value: "bioKgPer",
          //   sortable: false,
          //   total: "1",
          // },
          {
            text: "Recyclable Kg/day",
            value: "recKg",
            sortable: false,
            total: "recKg",
          },
          // {
          //   text: "%",
          //   value: "recKgPer",
          //   sortable: false,
          //   total: "2",
          // },
          {
            text: "RWPD Kg/day",
            value: "rwpKg",
            sortable: false,
            total: "rwpKg",
          },
          // {
          //   text: "%",
          //   value: "rwpKgPer",
          //   sortable: false,
          //   total: "3",
          // },

          // { text: "Action", value: "Action", sortable: false },
        ],
        tableData: [],
        tableAddNew: 0,
        tableModel: "421",
        withTotal: 1,
        withTotalPer: 1,
        nonAction: "1",
      },
      {
        tableTitle: "4.3.1 Quantity of waste generated (disposed + diverted).",
        tableName: "431",
        tableHeader: [
          {
            text: "Classification of Waste",
            value: "classWaste",
            sortable: false,
            total: null,
          },
          {
            text: "Generated (kg/ day)",
            value: "generated",
            sortable: false,
            total: "generated",
          },
          {
            text: "Percentage (%)",
            value: "percentage",
            sortable: false,
            total: "percentage",
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [
          {
            classWaste: "Diverted (Biodegradables + Recyclables)",
            generated: "",
            percentage: "",
          },
          {
            classWaste: "Disposed (Residuals + Special Waste)",
            generated: "",
            percentage: "",
          },
        ],
        tableAddNew: 0,
        withTotal: 1,
        tableModel: "431",
      },
      {
        tableTitle:
          "4.3.2 Projection of quantity of waste generated based on population projections.",
        tableName: "432",
        tableHeader: [
          {
            text: "Year",
            value: "year",
            sortable: false,
            total: null,
            divider: true,
          },
          {
            text: "Population",
            // value: "popul",
            value: "projPopulation",
            sortable: false,
            total: 123,
            divider: true,
          },

          {
            text: "Total Waste Generation(kg)",
            value: "wasteGen",
            sortable: false,
            total: "wasteGen",
            // total: "totalKg",
            divider: true,
          },
          {
            text: "Waste to be diverted",
            value: "wasteDiv",
            sortable: false,
            divider: true,
          },
          {
            text: "(%)",
            value: "wasteDivPer",
            sortable: false,
            divider: true,
          },
          {
            text: "Target waste to be disposed",
            value: "wasteDis",
            sortable: false,
            divider: true,
          },
          {
            text: "(%)",
            value: "wasteDisPer",
            sortable: false,
            divider: true,
          },
          {
            text: "Biodegradables",
            value: "wacsbio",
            sortable: false,
            divider: true,
          },
          {
            text: "(%)",
            value: "wacsbioPer",
            sortable: false,
            divider: true,
          },
          {
            text: "Recyclables",
            value: "wacsrec",
            sortable: false,
            divider: true,
          },
          {
            text: "(%)",
            value: "wacsrecPer",
            sortable: false,
            divider: true,
          },
          {
            text: "RWPD",
            value: "wacsrwpd",
            sortable: false,
            divider: true,
          },
          {
            text: "(%)",
            value: "wacsrwpdPer",
            sortable: false,
            divider: true,
          },
          {
            text: "For Disposal",
            value: "wacsdis",
            sortable: false,
            divider: true,
          },
          {
            text: "(%)",
            value: "wacsdisPer",
            sortable: false,
            divider: true,
          },
          {
            text: "Special",
            value: "wacsspec",
            sortable: false,
            divider: true,
          },
          {
            text: "(%)",
            value: "wacsspecPer",
            sortable: false,
            divider: true,
          },
          { text: "Actions", value: "actions", sortable: false },
        ],
        tableData: [],
        tableAddNew: 0,
        tableModel: "432",
      },
    ],
  }),
  methods: {
    ...mapActions(["saveProfile4", "fetchProfile", "fetchPopulation"]),
    ...mapMutations([]),

    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      9, 150.2;
      return parts.join(".");
    },
    nonNullItems(index) {
      let x = this.tables[index].tableHeader
        .filter((e) => {
          return e.total;
        })
        .map((e) => {
          return e.total;
        });
      return x;
    },
    sumField(key, index) {
      // sum data in give key (property)
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid) {
        let value = this.tables[index].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        return this.numberWithCommas(value.toFixed(2));
      }
    },
    sumFieldPer(key, index) {
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid && key != "totalKg") {
        let value = this.tables[index].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        let total = this.tables[index].tableData.reduce(
          (a, b) =>
            parseFloat(a) + (parseFloat(b["totalKg"].replace(/,/g, "")) || 0),
          0
        );
        let per = (value / total) * 100;
        if (isNaN(per)) {
          per = 0;
        }
        return this.numberWithCommas(per.toFixed(2)) + "%";
      }
    },

    // TABLE FUNCTION

    addItem(index, tableName) {
      // this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editTable = index;
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );
      this.dialogEdit = true;
    },
    editItem(index, tableName, item) {
      // this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editTable = index;
      //Finding index of relative object from outline table to edit form object
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );
      //Finding index of row in table data
      this.editedIndex = this.tables[index].tableData.indexOf(item);

      //Assign row data to Edit Forms
      let new_item = JSON.parse(JSON.stringify(item));
      this.editFormObject[this.editFormIndex].editableForm =
        this.editFormObject[this.editFormIndex].editableForm.map((e, i) => ({
          ...e,
          value: new_item[e.model],
        }));

      this.dialogEdit = true;
    },
    deleteItem(index, item) {
      // this.editTable = tableName;
      this.tables[index].tableData.splice(
        this.tables[index].tableData.indexOf(item),
        1
      );
      // this.dialogDelete = true;
    },
    setDefaultEdit() {
      let editFormLengthObject =
        this.editFormObject[this.editFormIndex].editableForm.length;
      for (let i = 0; i < editFormLengthObject; i++) {
        this.editFormObject[this.editFormIndex].editableForm[i].value = "";
      }
    },
    closeModal() {
      this.setDefaultEdit();
      this.dialogEdit = false;

      this.$nextTick(() => {
        this.editedIndex = -1;
        this.editFormIndex = -1;
      });
    },
    save() {
      let m = [];
      m = this.editFormObject[this.editFormIndex].editableForm.map((e) => ({
        [e.model]: e.value,
      }));
      let merged = m.reduce((obj, item) => ({ ...obj, ...item }), {});
      if (this.editedIndex > -1) {
        Object.assign(
          this.tables[this.editTable].tableData[this.editedIndex],
          merged
        );
      } else {
        this.tables[this.editTable].tableData.push(merged);
      }

      //=============FOR COPY OF TABLE 412 TO TABLE 421 ====================//

      if (this.editTable == 0) {
        if (this.editedIndex > -1) {
          Object.assign(this.tables[1].tableData[this.editedIndex], merged);
        } else {
          this.tables[1].tableData.push(merged);
        }
      }

      //=============FOR COPY OF TABLE 412 TO TABLE 421 ====================//

      this.setDefaultEdit();
      this.closeModal();
    },

    // TABLE FUNCTION

    pushProjectedYear() {
      for (let i = 1; i <= 10; i++) {
        //For table 432
        this.tables[3].tableData.push({
          year: "Year " + i,
          projPopulation: "",
          wasteGen: "",
          wacsbio: "",
          wacsbioPer: "",
          wacsdis: "",
          wacsdisPer: "",
          wacsrec: "",
          wacsrecPer: "",
          wacsrwpd: "",
          wacsrwpdPer: "",
          wacsspec: "",
          wacsspecPer: "",
          wasteDis: "",
          wasteDisPer: "",
          wasteDiv: "",
          wasteDivPer: "",
        });
      }
    },

    saveProfile() {
      this.saveProfile4({
        user: this.getStateSession["iis_employee_number"],
        lgu: localStorage.getItem("city_id"),
        wacs: JSON.stringify(this.tables[0].tableData),
        tbl_431: JSON.stringify(this.tables[2].tableData),
        tbl_432: JSON.stringify(this.tables[3].tableData),
      });
    },

    getProfile() {
      this.fetchProfile({
        user: this.getStateSession["iis_employee_number"],
        lgu: localStorage.getItem("city_id"),
      }).then(() => {
        let profile = [];
        profile = JSON.stringify(this.allProfile);

        if (profile.length > 3) {
          let new_data = JSON.parse(profile);

          if (new_data.wacs) {
            let table1data = JSON.parse(new_data.wacs);
            this.tables[0].tableData = table1data;

            let table2data = JSON.parse(new_data.wacs);
            this.tables[1].tableData = table2data;
          }

          if (new_data.tbl_431) {
            let table3data = JSON.parse(new_data.tbl_431);
            this.tables[2].tableData = table3data;
          }
          if (new_data.tbl_432) {
            let table4data = JSON.parse(new_data.tbl_432);
            this.tables[3].tableData = table4data;
          }

          // this.getPopulation();
          this.pushPopulation(new_data.proj_bar_popul);
          this.pushChartData(new_data.wacs);
        }
      });
    },

    pushPopulation(data) {
      let new_data = JSON.parse(data);

      if (new_data != undefined) {
        let props = [
          "column0",
          "column1",
          "column2",
          "column3",
          "column4",
          "column5",
          "column6",
          "column7",
          "column8",
          "column9",
        ];
        new_data = new_data.map((e, i) => ({
          ...e,
          column0: e.column0 ? e.column0 : "0",
          column1: e.column1 ? e.column1 : "0",
          column2: e.column2 ? e.column2 : "0",
          column3: e.column3 ? e.column3 : "0",
          column4: e.column4 ? e.column4 : "0",
          column5: e.column5 ? e.column5 : "0",
          column6: e.column6 ? e.column6 : "0",
          column7: e.column7 ? e.column7 : "0",
          column8: e.column8 ? e.column8 : "0",
          column9: e.column9 ? e.column9 : "0",
        }));
        const result = new_data.reduce(
          (previousValue, currentValue) =>
            Object.fromEntries(
              props.map((column) => [
                column,
                (parseFloat(previousValue[column]) || 0) +
                  parseFloat(currentValue[column].replace(/,/g, "")),
              ])
            ),
          {}
        );
        // const new_array = [];
        // new_array.push(result);

        this.tables[3].tableData = this.tables[3].tableData.map((e, i) => ({
          ...e,
          projPopulation: this.numberWithCommas(result["column" + i]),
        }));
      }
    },
    pushChartData(data) {
      let new_data = JSON.parse(data);

      if (new_data != undefined) {
        let props = ["bioKg", "recKg", "rwpKg", "forDisKg", "specKg"];
        new_data = new_data.map((e, i) => ({
          ...e,
          bioKg: e.bioKg ? e.bioKg : "0",
          recKg: e.recKg ? e.recKg : "0",
          rwpKg: e.rwpKg ? e.rwpKg : "0",
          forDisKg: e.forDisKg ? e.forDisKg : "0",
          specKg: e.specKg ? e.specKg : "0",
        }));
        const result = new_data.reduce(
          (previousValue, currentValue) =>
            Object.fromEntries(
              props.map((column) => [
                column,
                (parseFloat(previousValue[column]) || 0) +
                  parseFloat(currentValue[column].replace(/,/g, "")),
              ])
            ),
          {}
        );
        // const new_array = [];
        // new_array.push(result);
        let values = [];
        for (let key in result) {
          values.push(result[key]);
        }
        this.chartOptions2.data = values;

        // this.tables[3].tableData = this.tables[3].tableData.map((e, i) => ({
        //   ...e,
        //   projPopulation: this.numberWithCommas(result["column" + i]),
        // }));
      }
    },
  },
  computed: {
    ...mapGetters(["allProfile", "getStateSession", "allPopulation"]),
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
    watchTable1: function () {
      return this.tables[0].tableData;
      // return this.tables.map((table) => table.tableData);
    },
  },
  created() {
    this.getProfile();
    this.pushProjectedYear();
  },
  watch: {
    tab() {
      if (this.tab == 2) {
        this.getProfile();
      }
    },
    watchTable1: {
      handler(newValue, oldValue) {
        this.pushChartData(JSON.stringify(newValue));
      },
      deep: true,
    },
  },
};
</script>