<template>
  <v-container class="pa-1">
    <!-- <v-toolbar color="cyan" dark>
      <v-toolbar-title>10 YEAR PLAN HEADER</v-toolbar-title>
    </v-toolbar> -->
    <v-card class="mx-auto pa-4">
      <!-- Add NEW MODAL -->
      <template>
        <v-dialog v-model="dialogAdd" persistent width="unset">
          <template v-slot:activator="{ on, attrs }">
            <v-row>
              <v-col>
                <v-row>
                  <v-col md="6">
                    <v-text-field
                      dense
                      label="Search Bar"
                      v-model="Search"
                      @keyup="getSearch"
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-combobox
                      dense
                      v-model="limitRow"
                      label="Show"
                      :items="allLimit"
                      @change="limitFunction"
                    >
                    </v-combobox>
                  </v-col>
                  <v-col>
                    <v-select
                      dense
                      v-model="cstatus"
                      label="Status"
                      :items="status"
                      @change="statusFunction"
                    >
                    </v-select>
                  </v-col>
                </v-row>
                <v-btn
                  small
                  v-if="checkStatus"
                  color="primary"
                  dark
                  v-bind="attrs"
                  v-on="on"
                  @click="addNew()"
                >
                  Add New Plan
                </v-btn>
              </v-col>
              <v-col>
                <!-- <v-btn @click="getSearch">Search</v-btn> -->
                <v-btn
                  small
                  class="float-right mini-margin-left"
                  @click="nextFunction"
                  >Next</v-btn
                >
                <v-btn small class="float-right" @click="prevFunction"
                  >Prev</v-btn
                >
                <input v-model="tgetCurrentrow" type="hidden" />
              </v-col>
            </v-row>
          </template>
          <v-card>
            <v-card-title> </v-card-title>
            <v-card-text>
              <v-form ref="formAdd" v-model="valid" lazy-validation>
                <v-container>
                  <v-row>
                    <v-col>
                      <small>
                        <span class="black--text"
                          >10-Year Plan Basic Information:
                        </span></small
                      >

                      <v-text-field
                        label="Reference Number"
                        v-model="txtReferenceNumber"
                        dense
                        outlined
                        readonly
                        filled
                      ></v-text-field>
                      <v-row>
                        <v-col cols="6">
                          <v-select
                            label="Year"
                            v-model="txtStartYear"
                            :readonly="editedIndex >= 0"
                            dense
                            outlined
                            placeholder="YYYY"
                            :items="select_years"
                            :rules="requiredField"
                            required
                          ></v-select>
                        </v-col>
                        <v-col cols="6">
                          <v-text-field
                            label="PCG"
                            v-model="txtPcg"
                            dense
                            outlined
                            :readonly="editedIndex >= 0"
                            @keyup="isNum(txtPcg, `txtPcg`, 1)"
                            :rules="requiredField"
                            required
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-col>
                  </v-row>
                </v-container>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="
                  dialogAdd = false;
                  clearinput();
                "
                >Close</v-btn
              >
              <v-btn color="blue darken-1" text @click="insertHeader"
                >Continue</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- Modal Delete Res -->
      <template>
        <v-dialog v-model="dialogDelete" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <h2>Are sure want to delete ?</h2>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogDelete = false"
                >No</v-btn
              >
              <v-btn color="info darken-1" text @click="deletePlan">Yes</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- Submit Second modal -->
      <template>
        <v-dialog v-model="dialogSubmit" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <h2>Are you sure want to submit ?</h2>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogSubmit = false"
                >No</v-btn
              >
              <v-btn color="info darken-1" text @click="submitHeader"
                >Yes</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- View Plan Modal -->
      <template>
        <v-dialog
          v-model="dialogViewPlan"
          max-width="1500px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewPlan">
                <TypViewModal :header_id="editedIndex" :typ_year="typ_year" />
              </template>
            </v-card-text>
          </v-card>
        </v-dialog>
      </template>

      <!-- Process Plan Modal -->
      <!-- :retain-focus="false" -->
      <template>
        <v-dialog v-model="dialogProcess" max-width="1000px" persistent>
          <template v-if="dialogProcess">
            <ProcessModal
              :childData="childData"
              @submit-data="handleSubmitData"
              @close-modal="handleCancel"
            />
          </template>
        </v-dialog>
      </template>

      <!-- TABLE-->
      <template>
        <v-simple-table class="mini-margin-top my_class">
          <template v-slot:default>
            <thead>
              <tr>
                <th>Actions</th>
                <th>Reference Number</th>
                <!-- <th>Region</th> -->
                <th>City</th>
                <!-- <th v-if="checkStatus">Current Chapter</th> -->
                <th v-if="checkStatus">Action Taken</th>
                <th v-if="cstatus == 0 ? true : false">Status</th>
                <th v-if="checkStatus">Remarks</th>
                <!-- <th>Year</th> -->
              </tr>
            </thead>
            <tbody>
              <tr v-for="res in allPlanList" :key="res.id">
                <td>
                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        class="mx-1"
                        color="info"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="
                          editedIndex = res.id;
                          typ_year = res.year;
                          dialogViewPlan = true;
                        "
                      >
                        <v-icon small>mdi-archive-eye-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>View</span>
                  </v-tooltip>
                  <v-menu offset-y dense v-if="checkStatus">
                    <template v-slot:activator="{ on: menu, attrs }">
                      <v-tooltip top>
                        <template v-slot:activator="{ on: tooltip }">
                          <v-btn
                            class="mx-1"
                            color="info"
                            v-bind="attrs"
                            v-on="{ ...tooltip, ...menu }"
                            depressed
                            x-small
                            @click="getEdit(res)"
                          >
                            <v-icon small>mdi-pencil-outline</v-icon>
                          </v-btn>
                        </template>
                        <span>Edit</span>
                      </v-tooltip>
                    </template>
                    <!-- <v-list dense class="py-0">
                      <v-list-item dense @click="getHeaderEdit(res, '2')">
                        Update Header
                      </v-list-item>
                      <v-list-item dense @click="getEdit(res)">
                        Update Content</v-list-item
                      >
                    </v-list> -->
                  </v-menu>

                  <v-tooltip top v-if="checkStatus">
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        class="mx-1"
                        color="success"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="getHeaderEdit(res, '3')"
                      >
                        <v-icon small>mdi-account-arrow-right</v-icon>
                      </v-btn>
                    </template>
                    <span>Process</span>
                  </v-tooltip>

                  <!-- <v-btn
                    class="mx-1"
                    v-if="checkStatus"
                    color="error"
                    depressed
                    fab
                    x-small
                    @click="getDelete(res)"
                  >
                    <v-icon>mdi-delete-alert-outline</v-icon>
                  </v-btn> -->
                </td>
                <td>{{ res.reference_number }}</td>
                <td>{{ res.city_name }}</td>
                <!-- <td v-if="checkStatus">{{ res.chapter }}</td> -->
                <td v-if="res.action_taken === '0' && checkStatus">
                  <v-chip small color="info" class="ma-2 ml-0"> None </v-chip>
                </td>
                <td v-else-if="res.action_taken === 'Return' && checkStatus">
                  <v-chip small color="error" class="ma-2 ml-0">
                    {{ res.action_taken }}
                  </v-chip>
                </td>
                <td v-if="cstatus == 0 ? true : false">
                  <v-chip
                    :color="setColor(res.status)"
                    class="ma-2 ml-0 white--text"
                    small
                  >
                    {{ res.status_name }}
                  </v-chip>
                </td>
                <td v-if="checkStatus">
                  <p>{{ res.remarks }}</p>
                </td>
                <!-- <td>{{ res.year }}</td> -->
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </template>
    </v-card>
  </v-container>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import TypViewModal from "../Include/typ_view_modal";
import ProcessModal from "../Include/process_modal";
export default {
  name: "Eplan",
  components: {
    TypViewModal,
    ProcessModal,
  },
  data: () => ({
    //table header states
    allLimit: [5, 10, 20, 50, 100],
    limitRow: [10],
    cstatus: "1",
    status: [
      { text: "All", value: "0" },
      { text: "Draft", value: "1" },
    ],
    Search: "",
    select_years: [],

    //-core states
    id: "",
    typ_year: "",

    txtReferenceNumber: "",
    txtStartYear: "",
    txtPcg: "",

    cmbPersonnel: "",
    remarks: "",
    // rowStatus: 0,
    route_order: "",

    //-modals state
    editedIndex: -1,
    dialogDelete: false,
    dialogSubmit: false,
    dialogViewPlan: false,
    dialogProcess: false,
    dialogAdd: false,

    //-validation states
    valid: true,
    requiredField: [(v) => !!v || "Field is required"],

    submitStat: "",
    childData: {},
  }),
  methods: {
    ...mapActions([
      "fetchPlan",
      "CreateHeaderPlan",
      "SubmitHeaderPlan",

      "increment",
      "decrement",
      "deletePlantList",
    ]),
    // ...mapMutations(["mutateHeader", "mutateCity", "chapter"]),
    //Table Header Functions
    fetchListComponent() {
      console.log(this.Search);
      this.fetchPlan({
        txtsearch: this.Search,
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: this.cstatus,
        new_app: 1,
        user_type: this.getStateSession["user_type"],
        user_id: this.getStateSession["id"],
        regionId: this.getStateSession["region_id"],
        group: this.getStateSession["group"],
      });
    },
    nextFunction() {
      this.increment({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
      });
      this.fetchListComponent();
    },
    prevFunction() {
      this.decrement({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
      });
      this.fetchListComponent();
    },
    limitFunction() {
      this.fetchListComponent();
    },
    statusFunction() {
      this.fetchListComponent();
    },
    getSearch() {
      this.fetchListComponent();
    },

    //Table Body Functions
    //--modals
    getDelete(res) {
      this.dialogDelete = true;
      this.editedIndex = res.id;
    },
    getHeaderEdit(res, action) {
      this.years();
      this.editedIndex = res.id;

      if (res.central_to_lgu == 1) {
        this.submitStat = "3";
      } else {
        this.submitStat = "2";
      }

      this.childData = {
        user_role: this.getStateSession["access"],
        user_id: this.getStateSession["id"],

        headerId: res.id,
        regionId: res.region_id,
        route_order: res.route_order,

        senderId: res.sender,
        central_to_lgu: res.central_to_lgu,
      };

      // this.rowStatus = parseInt(res.status);
      this.route_order = parseInt(res.route_order);
      localStorage.setItem("reference_number", res.reference_number);
      this.dialogProcess = true;
    },
    handleSubmitData(newValue) {
      // console.log(newValue["personnel"]);
      this.cmbPersonnel = newValue["personnel"];
      this.remarks = newValue["remarks"];
      this.dialogSubmit = true;
      // if (newValue != null) {
      //   this.value = newValue[0];
      // }
    },
    handleCancel() {
      this.editedIndex = -1;
      this.dialogProcess = false;
    },
    addNew() {
      this.years();
      this.generateRefNo();
    },

    //--Core Functions
    deletePlan() {
      this.deletePlantList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        id: this.editedIndex,
        new_app: 1,
        user_type: this.getStateSession["user_type"],
        user_id: this.getStateSession["id"],
        regionId: this.getStateSession["region_id"],
        group: this.getStateSession["group"],
      });
      this.dialogDelete = false;
    },
    insertHeader() {
      if (this.$refs.formAdd.validate()) {
        this.CreateHeaderPlan({
          referenceNumber: this.txtReferenceNumber,
          year: this.txtStartYear,
          pcg: this.txtPcg,
          created_date: this.getNow(),
          search_status: 1,

          city_id: localStorage.getItem("city_id"),
          province_id: localStorage.getItem("province_id"),

          offset: this.tgetCurrentrow,
          limit: this.limitRow,
          new_app: 1,
          user_type: this.getStateSession["user_type"],
          user_id: this.getStateSession["id"],
          region_id: this.getStateSession["region_id"],
          name: this.getStateSession["name"],
        });
        this.clearinput();
      }
    },
    submitHeader() {
      console.log(this.cmbPersonnel);
      this.SubmitHeaderPlan({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,

        user_type: this.getStateSession["user_type"],

        id: this.editedIndex,
        user_id: this.getStateSession["id"],
        user_name: this.getStateSession["name"],
        user_role: this.getStateSession["access"],
        regionId: this.getStateSession["region_id"],

        receiver_id: this.cmbPersonnel["value"],
        receiver_name: this.cmbPersonnel["text"],
        receiver_region: this.cmbPersonnel["region"],
        receiver_role: this.cmbPersonnel["role"],

        remarks: this.remarks,
        status: this.submitStat,
        route_order: this.route_order + 1,
        action_taken: "Submit",
        internal_action: 0,
        assigned_group: "",
        date: this.getNow(),

        search_status: 1,
        new_app: 1,
        action: "",
        group: this.getStateSession["group"],
        city: "",
      });
      this.dialogSubmit = false;
      this.dialogProcess = false;
    },
    getEdit(res) {
      localStorage.setItem("pcg", res.pcg);
      localStorage.setItem("startingYear", res.year);
      localStorage.setItem("reference_number", res.reference_number);
      localStorage.setItem("header_id", res.id);

      // sessionStorage.setItem("pcg", res.pcg);
      // sessionStorage.setItem("startingYear", res.year);
      // sessionStorage.setItem("reference_number", res.reference_number);
      // sessionStorage.setItem("header_id", res.id);

      // this.chapter(res.chapter);
      this.$router.push({
        path: "plan",
        query: {
          chapter: res.chapter,
        },
      });
    },

    //Additional Method

    isNum(value, thisModel, withComa) {
      var isValid = /^[0-9,.]*$/.test(value);
      if (isValid) {
        value = value.replace(/,/g, ""); // remove commas from existing input
        if (withComa) {
          this[thisModel] = this.numberWithCommas(value); // add commas back in
        }
      } else {
        this[thisModel] = "";
      }
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return parts.join(".");
    },
    getNow() {
      const today = new Date();
      const date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + " " + time;
      return dateTime;
    },
    years() {
      let year = new Date().getFullYear();
      this.select_years = Array.from(
        { length: 10 },
        (value, index) => parseInt(year) - index
      );
      return Array.from(
        { length: 10 },
        (value, index) => parseInt(year) - index
      );
    },
    generateRefNo() {
      let rand = Math.floor(100000 + Math.random() * 900000);
      let reg = localStorage.getItem("region_name");
      let year = new Date().getFullYear();
      this.txtReferenceNumber = reg + "-" + year + "-" + rand;
    },
    clearinput() {
      this.txtReferenceNumber = "";
      this.txtStartYear = "";
      this.txtPcg = "";
      this.dialogAdd = false;
      this.$refs.formAdd.resetValidation();
    },
    setColor(res) {
      if (res == "1") {
        return "green";
      } else if (res == "2") {
        return "blue";
      } else if (res == "3") {
        return "teal  ";
      } else if (res == "4") {
        return "brown";
      } else if (res == "5") {
        return "brown";
      }
    },
  },
  computed: {
    ...mapGetters(["allPlanList", "tgetCurrentrow", "getStateSession"]),
    formTitle() {
      return this.editedIndex === -1 ? "Add 10 Year Plan" : "Edit 10 Year Plan";
    },
    checkStatus() {
      return this.cstatus === "1" ? true : false;
    },
  },
  created() {},
  watch: {},
};
</script>