<template>
  <v-container class="pa-1">
    <v-card dense class="mx-auto card-padding pa-4">
      <template>
        <v-dialog
          v-model="dialogProcess"
          persistent
          width="1000px"
          :retain-focus="false"
        >
          <template v-slot:activator="{}">
            <v-row>
              <v-col>
                <v-row>
                  <v-col>
                    <v-text-field
                      dense
                      label="Search Bar"
                      v-model="Search"
                      @keyup="getSearch"
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-combobox
                      dense
                      v-model="limitRow"
                      label="Show"
                      :items="allLimit"
                      @change="limitFunction"
                    >
                    </v-combobox>
                  </v-col>
                </v-row>
                <!-- <v-btn color="primary" dark v-bind="attrs" v-on="on">
                  Add New
                </v-btn> -->
              </v-col>
              <v-col>
                <!-- <v-btn @click="getSearch">Search</v-btn> -->
                <v-btn
                  small
                  class="float-right mini-margin-left"
                  @click="nextFunction"
                  >Next</v-btn
                >
                <v-btn small class="float-right" @click="prevFunction"
                  >Prev</v-btn
                >
                <input v-model="tgetCurrentrow" type="hidden" />
              </v-col>
            </v-row>
          </template>
          <template v-if="dialogProcess">
            <ProcessModal
              :childData="childData"
              @submit-data="handleSubmitData"
              @close-modal="handleCancel"
            />
          </template>
        </v-dialog>
      </template>

      <!-- Submit Second modal -->
      <template>
        <v-dialog v-model="dialogSubmit" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <h2>Are you sure want to submit ?</h2>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogSubmit = false"
                >No</v-btn
              >
              <v-btn color="info darken-1" text @click="submitHeader"
                >Yes</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- View History Modal -->
      <template>
        <v-dialog
          v-model="dialogViewHistory"
          max-width="1700px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewHistory == true">
                <HistoryViewModal :header_id="editedIndex" />
              </template>
            </v-card-text>
          </v-card>
        </v-dialog>
      </template>

      <!-- View Form 2 Modal -->
      <template>
        <v-dialog
          v-model="dialogViewForm2"
          max-width="1300px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewForm2 == true">
                <Form2Modal :header_id="editedIndex" />
              </template>
            </v-card-text>
          </v-card>
        </v-dialog>
      </template>

      <!-- View Form 3 Modal -->
      <template>
        <v-dialog
          v-model="dialogViewForm3"
          max-width="1300px"
          :retain-focus="false"
          ><v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewForm3 == true">
                <Form3Modal :header_id="editedIndex" />
              </template>
            </v-card-text>
            <v-card-actions class="justify-end"> </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- TABLE -->
      <template>
        <v-simple-table class="mini-margin-top my_class">
          <template v-slot:default>
            <thead>
              <tr>
                <th>Actions</th>
                <th class="display-none">#</th>
                <th>City</th>
                <th>Reference Number</th>
                <!-- <th>Created Date</th> -->
                <!-- <th>Created By</th> -->
                <th>Sender</th>
                <th>Action Needed</th>
                <th>Remarks</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="res in allPlanList" :key="res.typ_header_id">
                <td>
                  <v-menu offset-y dense>
                    <template v-slot:activator="{ on: menu, attrs }">
                      <v-tooltip top>
                        <template v-slot:activator="{ on: tooltip }">
                          <v-btn
                            v-bind="attrs"
                            v-on="{ ...tooltip, ...menu }"
                            class="mx-1"
                            color="info"
                            depressed
                            x-small
                          >
                            <v-icon small>{{
                              res.internal_action == "0"
                                ? "mdi-archive-check-outline"
                                : "mdi-archive-eye-outline"
                            }}</v-icon>
                          </v-btn>
                        </template>
                        <span>{{
                          res.internal_action == "0" ? "Evaluate" : "View"
                        }}</span>
                      </v-tooltip>
                    </template>
                    <v-list dense class="py-0">
                      <v-list-item dense @click="getEditForm2(res)">
                        Form 2
                      </v-list-item>
                      <v-list-item dense @click="getEditForm3(res)">
                        Form 3</v-list-item
                      >
                    </v-list>
                  </v-menu>

                  <v-menu offset-y dense>
                    <template v-slot:activator="{ on: menu, attrs }">
                      <v-tooltip top>
                        <template v-slot:activator="{ on: tooltip }">
                          <v-btn
                            v-bind="attrs"
                            v-on="{ ...tooltip, ...menu }"
                            class="mx-1"
                            color="info"
                            depressed
                            x-small
                          >
                            <v-icon small>mdi-printer-outline</v-icon>
                          </v-btn>
                        </template>
                        <span>View</span>
                      </v-tooltip>
                    </template>
                    <v-list dense class="py-0">
                      <v-list-item dense @click="showPrintForm2(res)">
                        Form 2
                      </v-list-item>
                      <v-list-item dense @click="showPrintForm3(res)">
                        Form 3</v-list-item
                      >
                    </v-list>
                  </v-menu>

                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        class="mx-1"
                        color="success"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="getHeaderEdit(res)"
                      >
                        <v-icon small>mdi-account-arrow-right</v-icon>
                      </v-btn>
                    </template>
                    <span>Process</span>
                  </v-tooltip>

                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        class="mx-1"
                        color="success"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="
                          editedIndex = res.id;
                          dialogViewHistory = true;
                        "
                      >
                        <v-icon small>mdi-history</v-icon>
                      </v-btn>
                    </template>
                    <span>History</span>
                  </v-tooltip>
                </td>
                <td class="display-none">{{ res.id }}</td>
                <td width="150px">{{ res.city_name }}</td>
                <td width="150px">{{ res.reference_number }}</td>
                <!-- <td>{{ res.createdDate }}</td> -->
                <!-- <td>{{ res.name }}</td> -->
                <td width="210px">{{ res.sender_name }}</td>
                <td>
                  <v-chip
                    :color="checkInternalAction(res.internal_action).color1"
                    class="ma-2 ml-0"
                  >
                    {{ checkInternalAction(res.internal_action).desc1 }}
                  </v-chip>
                  <!-- <td v-if="res.internal_action === '0'">
                  <v-chip link color="success" class="ma-2 ml-0">
                    For Evaluation
                  </v-chip>
                </td>
                <td v-else-if="res.internal_action === 1">
                  <v-chip link color="primary" class="ma-2 ml-0">
                    For Review
                  </v-chip>
                </td>
                <td v-else-if="res.internal_action === 2">
                  <v-chip link color="error" class="ma-2 ml-0">
                    For Comment
                  </v-chip>
                </td> -->
                </td>

                <td>{{ res.remarks }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        <v-overlay :value="overlay">
          <v-progress-circular indeterminate size="32"></v-progress-circular>
          Loading...
        </v-overlay>
      </template>
    </v-card>
  </v-container>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import HistoryViewModal from "../Include/history_view";
import Form2Modal from "../Include/form2_view";
import Form3Modal from "../Include/form3_view";
import ProcessModal from "../Include/process_modal";
export default {
  name: "EvalList",
  components: {
    HistoryViewModal,
    Form2Modal,
    Form3Modal,
    ProcessModal,
  },
  data: () => ({
    allLimit: [5, 10, 20, 50, 100],
    limitRow: [5],
    dialogProcess: false,
    dialogSubmit: false,
    dialogViewHistory: false,
    dialogViewForm2: false,
    dialogViewForm3: false,
    overlay: true,
    Search: "",
    id: "",

    editedIndex: -1,
    actionTaken: "",
    internalActionTaken: "",
    cmbPersonnel: "",
    remarks: "",

    route_order: "",

    //new
    childData: {},
  }),
  methods: {
    ...mapActions([
      "increment",
      "decrement",
      "searchPlanList",
      "SubmitHeaderPlan",
      // "fetchForm2",
      // "fetchForm3",
      //new
      "fetchPlan",
    ]),
    ...mapMutations(["setPlanList"]),
    fetchListComponent() {
      this.fetchPlan({
        txtsearch: this.Search,
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: 2,
        new_app: 1,
        user_type: this.getStateSession["user_type"],
        user_id: this.getStateSession["id"],
        regionId: this.getStateSession["region_id"],
        group: this.getStateSession["group"],
      }).then(() => {
        this.overlay = false;
      });
    },
    nextFunction() {
      this.increment({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
      });
      this.fetchListComponent();
    },
    prevFunction() {
      this.decrement({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
      });
      this.fetchListComponent();
    },
    limitFunction() {
      this.fetchListComponent();
    },
    getSearch() {
      this.fetchListComponent();
    },

    getDelete(res) {
      this.dialogDelete = true;
      this.id = res.id;
    },
    getEditForm2(res) {
      localStorage.setItem("typ_header_id", res.id);
      localStorage.setItem("form_id", res.form_id);
      localStorage.setItem("year", res.year);
      localStorage.setItem("reference_number", res.reference_number);

      // sessionStorage.setItem("typ_header_id", res.id);
      // sessionStorage.setItem("form_id", res.form_id);
      // sessionStorage.setItem("year", res.year);
      // sessionStorage.setItem("reference_number", res.reference_number);
      if (res.internal_action == 0) {
        localStorage.setItem("formAction", 1);
        // sessionStorage.setItem("formAction", 1);
      } else {
        localStorage.setItem("formAction", 1);
        // sessionStorage.setItem("formAction", 2);
      }
      this.$router.push({
        path: "formtwo",
      });
    },
    getEditForm3(res) {
      localStorage.setItem("typ_header_id", res.id);
      localStorage.setItem("form_id", res.form_id);
      localStorage.setItem("year", res.year);
      localStorage.setItem("reference_number", res.reference_number);
      // sessionStorage.setItem("typ_header_id", res.id);
      // sessionStorage.setItem("form_id", res.form_id3);
      // sessionStorage.setItem("year", res.year);
      // sessionStorage.setItem("reference_number", res.reference_number);
      this.$router.push({
        path: "formthree",
      });
    },
    showPrintForm2(res) {
      if (res.form_id != null) {
        this.editedIndex = res.id;
        this.dialogViewForm2 = true;
      } else {
        swal({
          title: "No Evaluation",
          icon: "warning",
        });
      }
    },
    showPrintForm3(res) {
      if (res.form_id3 != null) {
        this.editedIndex = res.id;
        this.dialogViewForm3 = true;
      } else {
        swal({
          title: "No Evaluation",
          icon: "warning",
        });
      }
    },

    getHeaderEdit(res) {
      this.editedIndex = res.id;

      this.rowStatus = parseInt(res.status);
      this.dialogProcess = true;
      this.id = res.id;

      this.route_order = parseInt(res.route_order);
      localStorage.setItem("reference_number", res.reference_number);

      this.childData = {
        user_role: this.getStateSession["access"],
        user_id: this.getStateSession["id"],

        headerId: res.id,
        regionId: res.region_id,
        route_order: res.route_order,

        creatorId: res.created_by,
        senderId: res.sender,
      };
    },
    handleSubmitData(newValue) {
      this.cmbPersonnel = newValue["personnel"];
      this.remarks = newValue["remarks"];
      this.actionTaken = newValue["action1"];
      this.internalActionTaken = newValue["action2"];
      this.dialogSubmit = true;
    },
    handleCancel() {
      this.editedIndex = -1;
      this.dialogProcess = false;
    },

    submitHeader() {
      let stat = 0;
      let internalAct = 0;
      if (this.actionTaken.name == "Forward") {
        stat = this.rowStatus;
        internalAct = this.internalActionTaken.value;
      } else if (this.actionTaken.name == "Submit") {
        stat = this.rowStatus + 1;
      } else {
        stat = this.rowStatus - 1;
      }
      this.SubmitHeaderPlan({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,

        user_type: this.getStateSession["user_type"],

        id: this.editedIndex,
        user_id: this.getStateSession["id"],
        user_name: this.getStateSession["name"],
        user_role: this.getStateSession["access"],
        regionId: this.getStateSession["region_id"],

        receiver_id: this.cmbPersonnel["value"],
        receiver_name: this.cmbPersonnel["text"],
        receiver_region: this.cmbPersonnel["region"],
        receiver_role: this.cmbPersonnel["role"],

        remarks: this.remarks,
        status: stat,
        route_order: this.route_order + 1,

        action_taken: this.actionTaken.name,
        internal_action: internalAct,
        assigned_group: "",
        date: this.getNow(),

        search_status: 2,
        new_app: 1,
        action: "",
        group: this.getStateSession["group"],
        city: "",
      });

      this.dialogSubmit = false;
      this.dialogProcess = false;
    },

    checkInternalAction(action) {
      let desc = "";
      let color = "";
      if (parseInt(action) == 0) {
        desc = "For Evaluation";
        color = "success";
      } else if (parseInt(action) == 1) {
        desc = "For Review";
        color = "success";
      } else if (parseInt(action) == 2) {
        desc = "For Comment";
        color = "success";
      }

      return { desc1: desc, color1: color };
    },
    getNow() {
      const today = new Date();
      const date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + " " + time;
      return dateTime;
    },
    printForm2() {
      this.$htmlToPaper("printMe");
    },
    printForm3() {
      this.$htmlToPaper("printMeForm3");
    },
    //File Upload
  },
  computed: {
    ...mapGetters(["allPlanList", "tgetCurrentrow", "getStateSession"]),
    buttonTitle() {
      return this.actionTaken.value == 0
        ? "Forward"
        : this.actionTaken.value == 2
        ? "Return"
        : "Submit";
    },
  },
  created() {
    this.setPlanList([]); // mutation from planModule to remove the data from getter allPlanList
    setTimeout(this.fetchListComponent, 1000);
  },
};
</script>