<template>
  <v-container class="pa-1">
    <v-tabs dense v-model="tab" align-with-title class="">
      <v-tabs-slider color="yellow"></v-tabs-slider>

      <v-tab @click="changeTab(1)"> New Application </v-tab>
      <v-tab @click="changeTab(0)"> Approved Application </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item>
        <EMBPlanList />
      </v-tab-item>
      <v-tab-item>
        <PlanlistApproved />
      </v-tab-item>
    </v-tabs-items>
  </v-container>
</template>

<script>
import EMBPlanList from "../components/Eplan/EmbPlanList";
import PlanlistApproved from "../components/Eplan/PlanlistApproved";
import "../sass/create_plan_style.scss";
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Eplan",
  data() {
    return { tab: null, limitRow: [10] };
  },
  computed: {
    ...mapGetters(["tgetCurrentrow", "getStateSession"]),
  },
  created() {
    // this.fetchPlanList({
    //   offset: this.tgetCurrentrow,
    //   limit: this.limitRow,
    //   status: 1,
    //   new_app: 1,
    //   user_type: this.getStateSession["user_type"],
    //   user_id: this.getStateSession["id"],
    //   regionId: this.getStateSession["region_id"],
    //   group: this.getStateSession["group"],
    // });
    setTimeout(this.getplanlist, 1000);
  },
  methods: {
    ...mapActions(["fetchPlan"]),
    changeTab(index) {
      this.fetchPlan({
        txtsearch: "",
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: 1,
        new_app: index,
        user_type: this.getStateSession["user_type"],
        user_id: this.getStateSession["id"],
        regionId: this.getStateSession["region_id"],
        group: this.getStateSession["group"],
      });
    },
    getplanlist() {
      this.fetchPlan({
        txtsearch: "",
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: 1,
        new_app: 1,
        user_type: this.getStateSession["user_type"],
        user_id: this.getStateSession["id"],
        regionId: this.getStateSession["region_id"],
        group: this.getStateSession["group"],
      });
    },
  },
  components: {
    EMBPlanList,
    PlanlistApproved,
  },
};
</script>