<template>
  <v-container class="ma-0" style="max-width: 100%" max-width="100%">
    <v-row>
      <!-- <draggable class="d-flex" style="width: 100%"> -->
      <v-col cols="12" md="6" lg="6" class="ma-0">
        <v-card class="overflow-auto" height="90vh">
          <template>
            <TypViewModal :header_id="typ_header_id" :typ_year="typ_year" />
          </template>
        </v-card>
      </v-col>
      <v-col cols="12" md="6" lg="6" class="ma-0">
        <v-stepper alt-labels v-model="e1" class="overflow-auto" height="90vh">
          <!-- START OF STEPPER HEADER-->
          <v-stepper-header>
            <template v-for="(infoContent, infoIndex) in info">
              <v-stepper-step
                :complete="e1 > infoIndex"
                :step="infoIndex + 1"
                :key="infoContent.step"
              >
                <div v-if="e1 - 1 == infoIndex"></div>
              </v-stepper-step>

              <v-divider v-if="infoIndex < info.length - 1" :key="infoIndex" />
            </template>
          </v-stepper-header>
          <!-- END OF STEPPER HEADER-->

          <!-- <v-btn
            class="mt-2 mx-2"
            small
            color="primary"
            @click="dialogAdd = true"
            >10-Year Plan</v-btn
          > -->
          <v-btn
            v-if="access == 4"
            small
            class="mt-2 mx-2"
            color="primary"
            @click="getRegEvaluation"
            >Region's Evaluation</v-btn
          >

          <v-btn
            v-if="value.length > 0"
            small
            class="mt-2 mx-2"
            color="primary"
            @click="value = []"
            >Hide Region Evaluation</v-btn
          >

          <!-- START OF STEPPER CONTENT-->
          <v-layout row wrap style="max-width: 100%" class="ma-0">
            <v-flex>
              <v-card class="overflow-auto mt-1 mx-0">
                <v-stepper-items>
                  <v-stepper-content
                    v-for="(infoContent, infoIndex) in info"
                    :key="infoIndex"
                    :step="infoIndex + 1"
                  >
                    <v-container
                      v-for="(item, itemIndex) in infoContent.item"
                      :key="itemIndex"
                    >
                      <div class="text-h5" v-text="item.itemHeader"></div>

                      <br />
                      <div
                        class="text-subtitle-2"
                        v-text="item.itemTitle"
                      ></div>
                      <v-textarea
                        auto-grow
                        :value="item.itemModel"
                        @change="(v) => (item.itemModel = v)"
                        outlined
                      >
                      </v-textarea>
                      <p v-if="value.length > 0"><b>Region's Evaluation</b></p>
                      <p v-if="value.length > 0">
                        <b
                          >{{
                            value[infoIndex].item[itemIndex].itemModel != ""
                              ? value[infoIndex].item[itemIndex].itemModel
                              : "N/A"
                          }}
                        </b>
                      </p>
                      <v-divider></v-divider>
                    </v-container>
                    <v-card-actions>
                      <v-btn @click="previous" v-if="infoIndex > 0"
                        >Back
                      </v-btn>
                      <!-- @click="previous" -->
                      <v-spacer></v-spacer>
                      <v-btn
                        color="primary"
                        @click="next()"
                        v-if="infoIndex < info.length - 1"
                        >Next</v-btn
                      >

                      <v-btn
                        @click="submit()"
                        color="success"
                        v-if="infoIndex == info.length - 1"
                        >Save
                      </v-btn>
                    </v-card-actions>
                  </v-stepper-content>
                </v-stepper-items>
              </v-card>
            </v-flex>
          </v-layout>
          <!-- END OF STEPPER CONTENT-->
        </v-stepper>
      </v-col>
      <!-- </draggable> -->
    </v-row>

    <!-- VIEW 10 YEAR PLAN -->
    <template>
      <v-dialog v-model="dialogAdd" max-width="1300px" :retain-focus="false"
        ><v-card>
          <v-card-title>
            <span class="headline"></span>
          </v-card-title>
          <v-card-text>
            <template v-if="dialogAdd">
              <TypViewModal :header_id="typ_header_id" :typ_year="typ_year" />
            </template>
          </v-card-text>
          <v-card-actions class="justify-end"> </v-card-actions>
        </v-card>
      </v-dialog>
    </template>

    <!-- View Region evaluation -->
    <template>
      <v-dialog v-model="dialogView" max-width="1300px" :retain-focus="false"
        ><v-card>
          <v-card-title>
            <span class="headline"></span>
          </v-card-title>
          <v-card-text>
            <template v-if="dialogView">
              <Form3Modal :header_id="typ_header_id" @update="onChildUpdate" />
            </template>
          </v-card-text>
          <v-card-actions class="justify-end"> </v-card-actions>
        </v-card>
      </v-dialog>
    </template>

    <!-- SNACKBAR -->
    <template>
      <v-snackbar
        v-model="snackbar"
        top
        right
        :timeout="timeout"
        color="success"
        elevation="24"
      >
        {{ text }}
      </v-snackbar>
    </template>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import TypViewModal from "../Include/typ_view_modal";
import Form3Modal from "../Include/form3_view";
import draggable from "vuedraggable";
let basePath = window.location.origin;
export default {
  components: {
    TypViewModal,
    Form3Modal,
    draggable,
  },
  data() {
    return {
      e1: 1,
      name: "FormThreeCom",
      info: [
        {
          step: "Step 1",
          item: [
            {
              itemHeader: "1. Name of LGU and Income Class",
              // itemTitle: "Name of LGU and Income Class",
              itemName: "lguName",
              itemModel: "",
            },
            {
              itemHeader: "2. Date Received",
              itemName: "receiveDate",
              itemModel: "",
            },
            {
              itemHeader: "3. LGU Profile",
              itemTitle: "No. of barangays (Urban & Rural)",
              itemName: "noBarangay",
              itemModel: "",
            },
            {
              itemTitle: "Population & 10 year Projection",
              itemName: "popProjection",
              itemModel: "",
            },
            {
              itemTitle: "Area in has",
              itemName: "areaHas",
              itemModel: "",
            },
          ],
        },
        {
          step: "Step 2",
          item: [
            {
              itemHeader: "Current SWM Condition",
              itemTitle: "Organization (SWM Office and Board )",
              itemName: "organization",
              itemModel: "",
            },
            {
              itemTitle: "Number of Staff",
              itemName: "numStaff",
              itemModel: "",
            },
            {
              itemTitle: "Collection Equipment",
              itemName: "colEquip",
              itemModel: "",
            },
            {
              itemTitle: "MRF",
              itemName: "mrf",
              itemModel: "",
            },
            {
              itemTitle: "Final disposal site",
              itemName: "finalDisposal",
              itemModel: "",
            },
            {
              itemTitle: "Waste Quantity",
              itemName: "wasteQuan",
              itemModel: "",
            },
            {
              itemTitle: "Waste Composition",
              itemName: "wasteCom",
              itemModel: "",
            },
            {
              itemTitle: "Expenditure on SWM",
              itemName: "expenditure",
              itemModel: "",
            },
            {
              itemTitle: "Annual Budget of the LGU",
              itemName: "annualBud",
              itemModel: "",
            },
          ],
        },
        {
          step: "Step 3",
          item: [
            {
              itemHeader: "5. Future Plan",
              itemTitle: "Source Reduction",
              itemName: "sourceRed",
              itemModel: "",
            },
            {
              itemTitle: "Collection",
              itemName: "collection",
              itemModel: "",
            },
            {
              itemTitle: "Transfer",
              itemName: "transfer",
              itemModel: "",
            },
            {
              itemTitle: "Processing",
              itemName: "processing",
              itemModel: "",
            },
            {
              itemTitle: "Disposal",
              itemName: "disposal",
              itemModel: "",
            },
            {
              itemTitle: "IEC",
              itemName: "iec",
              itemModel: "",
            },
            {
              itemTitle: "Special Waste",
              itemName: "specWaste",
              itemModel: "",
            },
          ],
        },
        {
          step: "Step 4",
          item: [
            {
              itemHeader:
                "6. Waste Diversion Target (Baseline and 10-Year projection)",
              // itemTitle: "Name of LGU and Income Class",
              itemName: "wasteDiv",
              itemModel: "",
            },
            {
              itemHeader: "7. Financing Aspect",
              itemTitle: "Funding Source/Options",
              itemName: "fundingSource",
              itemModel: "",
            },
            {
              itemTitle: "Expenditures/Investment",
              itemName: "expenditure",
              itemModel: "",
            },
            {
              itemTitle: "Repayment Plan/Cost Recovery System",
              itemName: "repaymentPLan",
              itemModel: "",
            },
          ],
        },
      ],
      dialogAdd: false,
      dialogView: false,
      access: 0,
      eval: 1,
      snackbar: false,
      text: "Copied.",
      timeout: 2000,
      typ_header_id: 0,
      typ_year: 0,
      value: [],
    };
  },
  created() {
    this.typ_header_id = localStorage.getItem("typ_header_id");
    this.typ_year = localStorage.getItem("year");
    // this.typ_header_id = sessionStorage.getItem("typ_header_id");
    // this.typ_year = sessionStorage.getItem("year");
    if (this.getStateSession["access"] == 4) {
      this.eval = 0;
    }
    this.access = this.getStateSession["access"];
    if (localStorage.getItem("form_id") != "null") {
      this.fetchForm3({
        id: localStorage.getItem("form_id"),
      }).then(() => {
        this.setForm3Details();
      });
    }
  },
  methods: {
    ...mapActions(["fetchForm3", "createForm3", "fetchRegForm3"]),
    next() {
      this.e1++;
      this.scrollTop();
    },
    submit() {
      this.createForm3({
        header_id: localStorage.getItem("typ_header_id"),
        lguName: this.info[0].item[0].itemModel,
        receiveDate: this.info[0].item[1].itemModel,
        noBarangay: this.info[0].item[2].itemModel,
        popProjection: this.info[0].item[3].itemModel,
        areaHas: this.info[0].item[4].itemModel,

        organization: this.info[1].item[0].itemModel,
        numStaff: this.info[1].item[1].itemModel,
        colEquip: this.info[1].item[2].itemModel,
        mrf: this.info[1].item[3].itemModel,
        finalDisposal: this.info[1].item[4].itemModel,
        wasteQuan: this.info[1].item[5].itemModel,
        wasteCom: this.info[1].item[6].itemModel,
        expenditure: this.info[1].item[7].itemModel,
        annualBud: this.info[1].item[8].itemModel,

        sourceRed: this.info[2].item[0].itemModel,
        collection: this.info[2].item[1].itemModel,
        transfer: this.info[2].item[2].itemModel,
        processing: this.info[2].item[3].itemModel,
        disposal: this.info[2].item[4].itemModel,
        iec: this.info[2].item[5].itemModel,
        specWaste: this.info[2].item[6].itemModel,

        wasteDiv: this.info[3].item[0].itemModel,
        fundingSource: this.info[3].item[1].itemModel,
        expenditureInv: this.info[3].item[2].itemModel,
        repaymentPLan: this.info[3].item[3].itemModel,
        date: this.getNow(),
        eval: this.eval,

        user_id: this.getStateSession["id"],
        access: this.getStateSession["access"],
      });
    },

    setForm3Details() {
      let newAllChapter = JSON.stringify(this.allForm3);
      let new_data = JSON.parse(newAllChapter);

      this.info[0].item[0].itemModel = new_data.lguName;
      this.info[0].item[1].itemModel = new_data.receiveDate;
      this.info[0].item[2].itemModel = new_data.noBarangay;
      this.info[0].item[3].itemModel = new_data.popProjection;
      this.info[0].item[4].itemModel = new_data.areaHas;

      this.info[1].item[0].itemModel = new_data.organization;
      this.info[1].item[1].itemModel = new_data.numStaff;
      this.info[1].item[2].itemModel = new_data.colEquip;
      this.info[1].item[3].itemModel = new_data.mrf;
      this.info[1].item[4].itemModel = new_data.finalDisposal;
      this.info[1].item[5].itemModel = new_data.wasteQuan;
      this.info[1].item[6].itemModel = new_data.wasteCom;
      this.info[1].item[7].itemModel = new_data.expenditure;
      this.info[1].item[8].itemModel = new_data.annualBud;

      this.info[2].item[0].itemModel = new_data.sourceRed;
      this.info[2].item[1].itemModel = new_data.collection;
      this.info[2].item[2].itemModel = new_data.transfer;
      this.info[2].item[3].itemModel = new_data.processing;
      this.info[2].item[4].itemModel = new_data.disposal;
      this.info[2].item[5].itemModel = new_data.iec;
      this.info[2].item[6].itemModel = new_data.specWaste;

      this.info[3].item[0].itemModel = new_data.wasteDiv;
      this.info[3].item[1].itemModel = new_data.fundingSource;
      this.info[3].item[2].itemModel = new_data.expenditureInv;
      this.info[3].item[3].itemModel = new_data.repaymentPLan;
    },

    previous() {
      this.e1--;
      this.scrollTop();
    },
    scrollTop() {
      setTimeout(() => {
        window.scrollTo({
          top: 0,
          behavior: "smooth",
        });
      }, 500);
    },
    getNow() {
      const today = new Date();
      const date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + " " + time;
      return dateTime;
    },
    getRegEvaluation() {
      this.dialogView = true;
    },

    onChildUpdate(newValue) {
      if (newValue != null || newValue != undefined) {
        this.value = newValue;
      }
    },
  },
  computed: {
    ...mapGetters(["allForm3", "getStateSession"]),
  },
  watch: {},
};
</script>
